.submenu {
    position: relative;
    width: 100%;

    &:before {
        content: '';
        clip-path: polygon(0 0, 0 92%, 30% 100%, 100% 96%, 100% 0);
        width: 100%;
        height: 100%;
        background-color: $color-gray-bg;
        position: absolute;
        bottom: 0;
        left: 0;

        @include bp(sm-min) {
            clip-path: polygon(0 0, 0 85%, 30% 100%, 65% 92%, 100% 92%, 100% 0);
        }

        @include bp(md-min) {
            clip-path: polygon(0 0, 0 82%, 20% 100%, 40% 90%, 75% 88%, 100% 100%, 100% 0);
        }
    }
}

.submenu__container {
    height: 161px;
    max-width: 768px;
    padding: 0 16px;
    width: 100%;
    margin: 0 auto;

    @include bp(sm-min) {
        height: 170px;
        padding: 0 24px;
    }

    @include bp(md-min) {
        max-width: 1440px;
        height: 134px;
        padding: 0 96px;
    }
}

.submenu__elements {
    z-index: 10;
    position: relative;

    &:after {
        width: 50px;
        height: 100%;
        right: -16px;
        bottom: 0;
        top: 0;
        position: absolute;
        display: block;
        content: '';
        background: linear-gradient(to left, rgba($color-gray-bg, 1) 0%, rgba($color-gray-bg, 0) 100%);
        pointer-events: none;

        @include bp(sm-min) {
            display: none;
        }
    }

    @include bp(sm-min) {
        margin-bottom: 20px;
    }

    @include bp(md-min) {
        display: flex;
        align-items: baseline;
        margin-bottom: 0;
    }
}

.submenu__title {
    font-size: $font-size-mmidlarge;
    text-transform: uppercase;
    font-weight: $font-weight-bold;
    color: $color-dark;
    margin-bottom: 16px;
    padding-top: 49px;

    @include bp(sm-min) {
        font-size: $font-size-xlarge;
        margin-bottom: 16px;
        padding-top: 36px;
    }

    @include bp(md-min) {
        margin-right: 36px;
        margin-bottom: 0;
        padding-top: 44px;
    }
}

.submenu__items {
    font-size: $font-size-tiny;
    color: $color-dark;
    text-transform: uppercase;
    font-weight: $font-weight-medium;
    letter-spacing: 1px;
    display: flex;
    margin-left: -16px;
    margin-right: -16px;
    padding-left: 16px;
    overflow-x: auto;

    @include bp(sm-min) {
        padding-left: 0;
        margin-left: 0;
        margin-right: 0;
    }

    &--active {
        color: $color-red-brand;
    }
}

.submenu__item {
    padding-right: 26px;
    color: $color-dark;
    white-space: nowrap;

    &--active {
        color: $color-red-brand;
    }
}

.submenu__item-link {
    color: inherit;
}

.submenu__select {
    font-weight: inherit;
    font-size: inherit;
    padding: 0;
}
