// Colours
$pikaday-text-color: #333333 !default;
$pikaday-title-color: #333333 !default;
$pikaday-title-bg: #ffffff !default;
$pikaday-picker-bg: #ffffff !default;
$pikaday-picker-border: 1px solid #f2f2f2;
$pikaday-picker-shadow: 6px 4px 10px 0 rgba($color-black, .25) !default;
$pikaday-picker-arrow-shadow: 0 0 7px 0 rgba($color-black, .15) !default;
$pikaday-th-color: #9aa2b0 !default;
$pikaday-day-color: #666666 !default;
$pikaday-day-bg: #f5f5f5 !default;
$pikaday-day-hover-color: #ffffff !default;
$pikaday-day-hover-bg: #ff8000 !default;
$pikaday-day-today-color: $color-red-brand !default;
$pikaday-day-selected-color: #ffffff !default;
$pikaday-day-selected-bg: #33aaff !default;
$pikaday-day-selected-shadow: #178fe5 !default;
$pikaday-day-disabled-color: #999999 !default;
$pikaday-week-color: #999999 !default;

/* Input styles */

.form-datepicker .form-textfield__icon {
    display: inline-block;
}

/* Datepicker styles */

.pika-single {
    z-index: 10001;
    display: inline-block;
    position: relative;
    // margin-top: 14px;
    color: $pikaday-text-color;
    border: $pikaday-picker-border;
    background: $pikaday-picker-bg;
    opacity: 1;
    visibility: visible;
    transition: 300ms ease;
    transform-property: opacity, visibility;
    transform: translateZ(11px);

    &.is-hidden {
        display: none;
        opacity: 0;
        visibility: hidden;
        transform: translateY(10px);
    }

    &.is-bound {
        position: absolute;
        box-shadow: $pikaday-picker-shadow;
    }

    // clear child float (pika-lendar), using the famous micro clearfix hack
    // http://nicolasgallagher.com/micro-clearfix-hack/

    *zoom: 1;

    &:before,
    &:after {
        content: ' ';
        display: table;
    }

    &:after {
        clear: both;
    }
}

.pika-lendar {
    position: relative;
    float: left;
    width: 330px;
    margin: 28px 5px 14px;
}

.pika-title {
    position: relative;
    text-align: center;

    select {
        cursor: pointer;
        position: absolute;
        z-index: 9998;
        margin: 0;
        left: 0;
        top: 5px;
        filter: alpha(opacity=0);
        opacity: 0;
    }
}

.pika-label {
    display: inline-block;
    position: relative;
    z-index: 9999;
    overflow: hidden;
    margin: 0;
    padding: 0 3px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    color: $pikaday-title-color;
    background-color: $pikaday-title-bg;
}

.pika-prev,
.pika-next {
    display: block;
    cursor: pointer;
    position: relative;
    outline: none;
    border: 0;
    padding: 0;
    width: 20px;
    height: 20px;
    text-indent: 20px; // hide text using text-indent trick, using width value (it's enough)
    white-space: nowrap;
    overflow: hidden;
    background-color: transparent;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 75% 75%;
    opacity: .5;

    &:hover {
        opacity: 1;
    }

    &.is-disabled {
        cursor: default;
        opacity: .2;
    }
}

.pika-prev,
.is-rtl .pika-next {
    float: left;
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAeCAYAAAAsEj5rAAAAUklEQVR42u3VMQoAIBADQf8Pgj+OD9hG2CtONJB2ymQkKe0HbwAP0xucDiQWARITIDEBEnMgMQ8S8+AqBIl6kKgHiXqQqAeJepBo/z38J/U0uAHlaBkBl9I4GwAAAABJRU5ErkJggg==');
    *left: 0;
}

.pika-next,
.is-rtl .pika-prev {
    float: right;
    background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAeCAYAAAAsEj5rAAAAU0lEQVR42u3VOwoAMAgE0dwfAnNjU26bYkBCFGwfiL9VVWoO+BJ4Gf3gtsEKKoFBNTCoCAYVwaAiGNQGMUHMkjGbgjk2mIONuXo0nC8XnCf1JXgArVIZAQh5TKYAAAAASUVORK5CYII=');
    *right: 0;
}

.pika-select {
    display: inline-block;
}

.pika-table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    border: 0;

    th,
    td {
        width: 14.285714285714286%;
        padding: 0;
    }

    th {
        padding-bottom: 10px;
        color: $pikaday-th-color;
        font-size: 12px;
        line-height: 25px;
        font-weight: 700;
        text-align: center;
        text-transform: uppercase;
    }

    abbr {
        border: none;
        text-decoration: none;
    }
}

.pika-button {
    cursor: pointer;
    display: block;
    position: relative;
    box-sizing: border-box;
    outline: none;
    border: 0;
    margin: 0;
    width: 100%;
    padding: 13px 0;
    background: transparent;
    color: $pikaday-day-color;
    font-size: 12px;
    line-height: 12px;
    text-align: center;
    z-index: 1;

    .is-today & {
        color: $pikaday-day-today-color;
        font-weight: 700;
    }

    .is-selected & {
        color: $pikaday-day-selected-color;
        font-weight: 700;
        border-radius: 3px;

        &:after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: -1;
            width: 44px;
            height: 44px;
            margin: -22px 0 0 -22px;
            border-radius: 50%;
            background: $color-red-brand;
        }
    }

    .is-disabled &,
    .is-outside-current-month & {
        pointer-events: none;
        cursor: default;
        color: $pikaday-day-disabled-color;
        opacity: .3;
    }
}

.pika-week {
    font-size: 11px;
    color: $pikaday-week-color;
}

.is-inrange .pika-button {
    background: #d5e9f7;
}

.is-startrange .pika-button {
    color: #ffffff;
    background: #6cb31d;
    box-shadow: none;
    border-radius: 3px;
}

.is-endrange .pika-button {
    color: #ffffff;
    background: #33aaff;
    box-shadow: none;
    border-radius: 3px;
}
