.checkout__column {
    @include bp(xs-max) {
        display: block !important;
    }
}

.checkout__notes {
    margin-top: 36px;
}

.checkout__notes-added {
    display: none;
}

.checkout__notes-text {
    font-size: $font-size-tiny;
    color: $color-dark;
    opacity: .5;
}

.pickup_location {
    select {
        background: url('../img/chevron.svg') no-repeat right 50%;
        background-size: 12px;
        padding-right: 20px;
        border: none;
        border-bottom: 2px solid $color-brown-light;
        border-radius: 0;
        appearance: none;
        font-size: 12px;
    }

    td {
        font-size: 12px;
        padding-left: 40px;
    }

    & > th {
        display: none;
    }
}

.checkout ul.woocommerce-error li {
    display: block;
    margin: 10px 0;
    color: $color-red;
}

.checkout .woocommerce-message {
    display: none;
}

.woocommerce-order-pay .blockOverlay {
    opacity: 1 !important;
}
