.terms__title {
    font-size: $font-size-xlarge;
    font-weight: $font-weight-medium;
    margin-bottom: 36px;
    margin-top: 48px;

    @include bp(sm-min) {
        font-size: $font-size-xxlarge;
        margin-top: 56px;
    }
}

.terms__container {
    max-width: 586px !important;

    @include bp(md-min) {
        max-width: 780px !important;
    }
}

.terms__paragraph {
    p {
        margin-bottom: 30px;
        line-height: 26px;

        &:last-child {
            margin-bottom: 156px;

            @include bp(max-min) {
                margin-bottom: 166px;
            }
        }
    }
}
