.editor {
    img {
        max-width: 100%;
        height: auto;
    }

    /* WordPress Core */

    .alignnone {
        margin: 5px 20px 20px 0;
    }

    .aligncenter,
    div.aligncenter {
        display: block;
        margin: 2em auto;

        @include bp(sm-min) {
            margin: 2em -96px 2em -96px;
        }
    }

    .alignright {
        margin: 2em 0;

        @include bp(sm-min) {
            margin: 2em -96px 2em 20px;
            float: right;
        }
    }

    .alignleft {
        margin: 2em 0;

        @include bp(sm-min) {
            margin: 2em 20px 2em -96px;
            float: left;
        }
    }

    .wp-caption {
        width: auto !important;
        padding: 5px 3px 10px;
        text-align: center;
    }

    .wp-caption.alignnone {
        margin: 2em 0;
    }

    .wp-caption.alignleft {
        margin: 2em 0;

        @include bp(sm-min) {
            margin: 2em 20px 2em -96px;
            float: left;
        }
    }

    .wp-caption.alignright {
        margin: 2em 0;

        @include bp(sm-min) {
            margin: 2em -96px 2em 20px;
            float: right;
        }
    }

    .wp-caption img {
        border: 0 none;
        height: auto;
        margin: 0;
        max-width: 100%;
        padding: 0;
        width: auto;
    }

    .wp-caption .wp-caption-text {
        font-size: 12px;
        line-height: 17px;
        margin: 0;
        padding: 0 4px 5px;
        text-align: right;
    }

    .video {
        width: auto;

        @include bp(sm-min) {
            margin-left: -96px;
            margin-right: -96px;
        }
    }

    .video__btn {
        overflow: hidden;
    }
}
