.notification {
    background-color: rgba($color-black, .4);
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    padding: 8px 12px 8px 16px;
    width: 100%;
    top: 0;
    right: -100%;
    transition: right .6s;
    transition-timing-function: ease-out;

    &.isOpen {
        right: 0;
    }

    @include bp(xs-min) {
        width: auto;
    }
}

.notification__content {
    color: $color-white;
    padding-right: 12px;
}

.notification__close {
    color: $color-red-brand;
    line-height: 1;
    padding: 4px;
    cursor: pointer;
}
