.postmeta__content {
    position: relative;

    @include bp(sm-min) {
        position: relative;
        text-align: center;
    }
    @include bp(md-min) {
        position: relative;
        text-align: center;
    }
}

.postmeta__container {
    @include bp(md-min) {
        max-width: 110px;
        display: flex;
        flex-direction: column;
        align-items: left;
        padding-left: 20px;
    }
}

.postmeta__author {
    margin-bottom: 8px;
    display: flex;
    flex-direction: row;

    @include bp(sm-min) {
        flex-direction: column;
        margin-bottom: 56px;
    }
    @include bp(md-min) {
        flex-direction: column;
        margin-bottom: 36px;
    }
}

.postmeta__title {
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    color: $color-dark;
    text-transform: uppercase;
    display: block;
    margin-left: 18px;
    padding-top: 5px;

    @include bp(sm-min) {
        padding-top: 10px;
        margin-left: 0;
    }
    @include bp(md-min) {
        padding-top: 10px;
        margin-left: 0;
    }
}

.postmeta__title2 {
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    color: $color-dark;
    text-transform: uppercase;
    margin-left: 21px;
    padding-top: 3px;

    .section--recipe & {
        padding-top: 0;
    }

    @include bp(sm-min) {
        margin-left: 0;
        padding-top: 10px;

        .section--recipe & {
            padding-top: 0;
        }
    }
    @include bp(md-min) {
        margin-left: 0;
        padding-top: 10px;

        .section--recipe & {
            padding-top: 0;
        }
    }
}

.postmeta__subtitle {
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    color: $color-red-brand;
    display: block;
    margin-left: 18px;

    @include bp(sm-min) {
        margin-left: 0;
    }
    @include bp(md-min) {
        margin-left: 0;
    }
}

.postmeta__image {
    width: 48px;
    height: 48px;

    @include bp(sm-min) {
        width: 81px;
        height: 81px;
        margin: auto;
    }
}

.postmeta__img {
    border-radius: 50%;
}

.postmeta__icons {
    display: flex;
    flex-direction: row;
    align-items: center;

    @include bp(sm-min) {
        flex-direction: column;
    }
    @include bp(md-min) {
        flex-direction: column;
    }
}

.postmeta__icon {
    font-size: 50px;

    .section--recipe & {
        font-size: 86px;
    }

    @include bp(sm-min) {
        margin: auto;
    }
}
