.section {
    &--background {
        background-color: $color-white;
    }

    &--background-gray {
        background-color: $color-gray-bg;
    }

    &--background-light-gray {
        background-color: $color-gray-light2;
    }

    &--products {
        position: relative;
        padding: 86px 0 110px;

        @include bp(md-min) {
            padding: 86px 0 100px;
        }
    }

    &--blog {
        padding-top: 30px;
        padding-bottom: 160px;

        @include bp(md-min) {
            padding-top: 60px;
        }
    }

    &--blog-single {
        padding-top: 30px;

        @include bp(sm-min) {
            padding-top: 76px;
        }
    }

    &--accordion {
        padding: 31px 0;

        @include bp(sm-min) {
            padding: 86px 0 156px;
        }

        @include bp(md-min) {
            padding: 101px 0 156px;
        }
    }

    &--search {
        @include bp(sm-min) {
            padding-bottom: 64px;
        }
    }

    &--top-padding {
        padding-top: 50px;

        @include bp(sm-min) {
            padding-top: 85px;
        }
    }

    &--padding {
        padding-top: 156px;
        padding-bottom: 156px;

        @include bp(sm-min) {
            padding-top: 156px;
            padding-bottom: 156px;
        }
    }

    &--bottom-padding {
        padding-bottom: 116px;

        @include bp(sm-min) {
            padding-bottom: 156px;
        }
    }

    &--product-alt {
        margin-bottom: 31px;

        @include bp(sm-min) {
            margin-bottom: 156px;
        }
    }

    &--product-alt + .section--product-alt {
        margin-top: -31px;

        @include bp(sm-min) {
            margin-top: -156px;
        }
    }

    &--surf {
        margin-top: -35px;

        @include bp(sm-min) {
            margin-top: -72px;
            padding-top: 28px;
        }

        @include bp(md-min) {
            padding-top: 0;
        }
    }
}

.section__head {
    padding: 86px 0 32px;

    @include bp(sm-min) {
        padding: 156px 0 86px;
    }

    .section--related & {
        padding: 0 0 56px;
    }

    .section--brand &,
    .section--recipe & {
        padding-bottom: 0;
        padding-top: 52px;

        @include bp(sm-min) {
            padding-top: 66px;
        }
    }

    .section--narrow-head & {
        @include bp(sm-min) {
            padding-bottom: 46px;
        }
    }

    .section--brands &,
    .section--search & {
        padding: 56px 0 36px;

        @include bp(sm-min) {
            padding: 56px 0 66px;
        }
    }

    .section--training & {
        padding-top: 0;
    }

    .section--maintenance & {
        padding-top: 0;
        padding-bottom: 49px;

        @include bp(sm-min) {
            padding: 0 150px 49px 70px;
        }

        @include bp(md-min) {
            padding-left: 0;
            padding-right: 0;
        }
    }

    .section--blog + section &,
    .error404 & {
        // section--blog already has padding bottom
        padding-top: 0;
    }

    .section--cards-accordion & {
        @include bp(sm-min) {
            padding-top: 100px;
        }
    }
}

.section__grid {
    display: block;

    @include bp(sm-min) {
        max-width: 80%;
    }

    @include bp(md-min) {
        display: flex;
        max-width: 100%;
    }
}

.section__primary {
    margin-bottom: 16px;

    @include bp(md-min) {
        flex: 1 1 50%;
        margin-bottom: 0;
        padding-right: 45px;
    }

    .section--brand &,
    .section--recipe & {
        @include bp(md-min) {
            padding-left: calc(100%/12*2);
        }
    }

    .section--training & {
        @include bp(md-min) {
            flex: 1 1 55%;
            padding-right: 26px;
        }
    }

    .section--related-blog & {
        padding: 0 16px;
    }
}

.section__secondary {
    @include bp(md-min) {
        padding: 0 120px 0 45px;
        flex: 1 1 50%;
    }

    .section--training & {
        @include bp(md-min) {
            flex: 1 1 45%;
            max-width: auto;
            padding: 0;
        }
    }

    .section--office & {
        @include bp(md-min) {
            padding-right: 0;
        }
    }
}

.section__title {
    text-transform: uppercase;
    font-size: $font-size-mmidlarge;
    line-height: 1.4;

    @include bp(sm-min) {
        font-size: $font-size-xxlarge;
    }

    @include bp(md-min) {
        margin: 0;
    }

    .section--cards & {
        font-style: italic;
    }

    .section--maintenance &,
    .section--related & {
        font-size: $font-size-mxlarge;
    }

    .section--brand &,
    .section--recipe & {
        text-transform: none;
        font-size: $font-size-xlarge;
        font-weight: $font-weight-medium;
        margin-bottom: 46px;

        @include bp(sm-min) {
            font-size: $font-size-xxlarge;
        }
    }

    .section--related-blog &,
    .section--brands &,
    .section--search & {
        font-weight: $font-weight-bold;
        font-size: $font-size-xlarge;
    }

    .section--brands & {
        font-weight: $font-weight-medium;
    }
}

.section__title-count {
    font-size: 70%;
    color: $color-red-brand;
}

.section__footer {
    text-align: center;
    padding-top: 36px;

    @include bp(sm-min) {
        padding-top: 64px;
    }

    .section--search & {
        padding-top: 24px;

        @include bp(sm-min) {
            padding-top: 64px;
        }
    }
}

.section__show-all {
    box-shadow: none;
}
