@import 'import/css/jquery.fancybox.scss';

img {
    .lightbox__video-content & {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
}

.fancybox-bg {
    background: $color-black;

    .fancybox-is-open & {
        opacity: .8;
    }
}

.fancybox-toolbar {
    .lightbox--overview &,
    .lightbox--wireless &,
    .lightbox--add-contact & {
        display: none;
    }
}

.lightbox__title {
    @include bp(sm-min) {
        text-align: center;
    }
}

.lightbox__buttons {
    padding-top: 48px;
    display: flex;
    justify-content: space-between;
}

.lightbox__video {
    display: block;
    background-image: none;

    &:hover {
        background-image: none;
    }
}

.lightbox__video-content {
    overflow: hidden;
    @include aspect-ratio(70.59, 39.71, 'image');
}

.lightbox__video-content-overlay {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    cursor: pointer;
    z-index: 1;
}

.lightbox__video-content-overlay-play {
    display: block;
    position: relative;
    width: 28px;
    height: 28px;
    background-color: $color-white;
    border-radius: 50%;

    &:after {
        content: '';
        width: 0;
        height: 0;
        border-top: 7px solid transparent;
        border-bottom: 7px solid transparent;
        border-left: 12px solid $color-black;
        position: absolute;
        margin: auto;
        left: 5px;
        right: 0;
        top: 0;
        bottom: 0;
    }
}

.lightbox__close-button {
    font-weight: 300;

    .lightbox--sign & {
        padding: 0 20px;
    }
}

.fancybox-button--close {
    .lightbox--contract & {
        display: none;
    }
}

.lightbox__close {
    position: absolute;
    top: 10px;
    right: 10px;
    background-image: none;

    &:before {
        content: none;
    }

    &:hover {
        background-image: none;
    }
}

.lightbox__close-icon {
    font-size: 24px;
}

.lightbox__buttons-wrapper {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 16px;
    margin: 0 auto;
    max-width: 190px;
    z-index: 1;

    @include bp(md-min) {
        right: 30px;
        bottom: 32px;
        max-width: unset;
    }
}

.lightbox__reel-link {
    height: 100%;
}
