@import 'import/css/_select';

.form-select {
    z-index: auto;

    .form-textfield__inner {
        &:after {
            display: block;
            content: '';
            width: 8px;
            height: 8px;
            border-left: 2px solid $textfield-border;
            border-bottom: 2px solid $textfield-border;
            transform: rotate(-45deg);
            position: absolute;
            top: 21px;
            right: 2px;
            backface-visibility: hidden;
        }
    }

    &.is-invalid {
        .form-textfield__inner {
            &:after {
                border-color: $color-red;
            }
        }
    }

    .form-textfield__label {
        display: none;

        &:after {
            display: none;
        }
    }

    .form-textfield__input {
        appearance: none;

        &:after {
            background-color: darken($textfield-border,5%);
            bottom: 0;
            content: '';
            height: 3px;
            left: 0;
            opacity: 0;
            position: absolute;
            @include textfield-animation();
            visibility: hidden;
            width: 100%;
        }

        &:hover {
            &:after {
                visibility: visible;
                opacity: 1;
            }
        }

        option {
            color: $color-dark;
        }
    }

    .form-textfield__input.initialized {
        // hide <select> element if js plugin has initilized
        display: none;
    }

    .form-textfield__input.select-wrapper {
        // remove padding that is added to input
        padding-right: 0;
        border-bottom: 0;
    }

    .dropdown__content li.disabled:first-child {
        text-transform: uppercase;
        color: $color-red-brand;
        font-weight: 500;
        font-size: 12px;
        padding-top: 12px;

        span {
            color: inherit;
            background: transparent;
        }
    }

    .dropdown__content {
        width: calc(100% + 32px);
        z-index: $z-index-dropdown;
    }
}

.form-select.is-done {
    .form-textfield__inner:after {
        display: none;
    }
}

.form-select--inline {
    display: inline-block;
    width: auto;

    .form-textfield__inner {
        padding-top: 0;
        display: inline-block;

        &:after {
            top: 8px;
        }
    }

    .form-textfield__input {
        font-weight: 500;
    }
}

.form-select--simple {
    display: block;

    .form-textfield__inner {
        padding-top: 0;
        display: block;

        &:after {
            top: 8px;
            border-left: 2px solid $color-dark;
            border-bottom: 2px solid $color-dark;
        }
    }

    .form-textfield__input {
        border-bottom: 0;
        font-size: 20px;
        font-weight: 700;
        line-height: 20px;
        text-transform: uppercase;

        option {
            font-size: 16px;
            text-transform: none;
        }
    }
}

.form-select--light {
    .form-textfield__inner:after {
        border-color: $color-white;
        border-width: 1px;
    }
}

.form-select--naked {
    .form-textfield__input {
        height: auto;
        border-bottom: none;
        color: inherit;
        font-family: inherit;
        font-weight: 400;
        appearance: none;
        background: transparent;
    }

    .form-textfield__input:after {
        display: none;
    }

    .form-textfield__input input {
        height: auto;
        font-size: 12px;
        font-weight: 500;
        text-transform: uppercase;
        border-bottom: none;
    }

    .form-textfield__inner:after {
        border-left: 2px solid $color-dark;
        border-bottom: 2px solid $color-dark;
        width: 10px;
        height: 10px;
        top: 3px;
    }
}

.form-select--search {
    .form-textfield__inner {
        padding-top: 32px;
    }

    .form-textfield__label {
        z-index: 1000;
    }
}

.form-select--small {
    .form-textfield__input {
        font-size: 16px;
        height: 22px;
    }
}

.form-select--large {
    .form-textfield__input {
        font-size: 18px;
        height: 22px;
    }
}

.form-select--labelless {
    .form-textfield__inner {
        padding-top: 0;
    }
}

.form-select--lang {
    .form-textfield__inner {
        &:after {
            border-color: $color-red-brand;
        }
    }

    .form-textfield__input {
        width: auto;
        text-transform: uppercase;
        font-weight: 500;
        font-size: 16px;
    }
}

.form-select--special {
    display: inline-block;
    width: 72px;

    .form-textfield__inner {
        padding: 0;

        &:after {
            content: '';
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 5px 4px 0 4px;
            border-color: #000000 transparent transparent transparent;
            pointer-events: none;
            position: absolute;
            right: 0;
            top: 50%;
            margin-top: -4px;
            transform: rotate(0);
        }
    }

    .form-textfield__input input,
    .form-textfield__input {
        display: inline-block;
        border-bottom: 0;
        height: 72px;
        padding: 0;
        background-color: $color-white;
        font-weight: 500;
        -webkit-appearance: none;
        padding-right: 20px;

        &:after {
            display: none;
        }
    }
}
