.filter {
    &--machine {
        position: relative;
        background-color: $color-gray-bg;

        @include bp(md-min) {
            margin-top: 70px;
            padding-left: 20px;
        }
    }
}

.filter__arrow {
    margin-right: 6px;
}

.filter__close {
    display: none;

    @include bp(sm-min) {
        display: none;
    }

    .filter-open & {
        display: block;
        position: absolute;
        top: -52px;
        right: 0;
        font-size: 38px;
        color: $color-white;
        background: transparent;
    }
}

.filter__row {
    background-color: rgba($color-brown-dark2, .05);
    padding: 10px 0 0 16px;

    @include bp(sm-min) {
        padding: 29px 36px;
    }

    .filter--machine &,
    .filter--search & {
        background-color: rgba($color-gray-warm2, .05);
    }

    &:nth-child(n+2) {
        background-color: rgba($color-brown-dark2, .09);

        .filter--machine &,
        .filter--search & {
            background-color: rgba($color-gray-warm2, .15);
        }
    }

    &:nth-child(n+3) {
        background-color: rgba($color-brown-dark2, .14);

        .filter--machine &,
        .filter--search & {
            background-color: rgba($color-gray-warm2, .25);
        }
    }

    &:nth-child(n+4) {
        background-color: rgba($color-brown-dark2, .18);

        .filter--machine &,
        .filter--search & {
            background-color: rgba($color-gray-warm2, .35);
        }
    }

    .filter--search & {
        @include bp(sm-min) {
            padding-left: 16px;
            padding-right: 16px;
        }

        @include bp(md-min) {
            padding-left: 36px;
            padding-right: 36px;
        }
    }
}

.filter__checkbox-item,
.filter__select-item {
    padding: 0 0 16px;
    display: block;

    @include bp(sm-min) {
        display: inline-block;
        vertical-align: middle;
        padding: 0 0 0 16px;
    }

    &:last-child {
        padding-bottom: 0;
    }

    .filter--machine &,
    .filter--machine & {
        padding-left: 0;
        padding-right: 16px;

        @include bp(sm-min) {
            padding-left: 0;
            padding-right: 8px;
        }

        @include bp(md-min) {
            padding-left: 32px;
        }
    }
}

.filter__label {
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    text-transform: uppercase;

    .filter--calculator & {
        line-height: 1.5;
    }
}

.filter__field {
    @include bp(sm-min) {
        text-align: right;
    }

    .filter--machine &,
    .filter--search & {
        text-align: left;
    }
}

.filter__header {
    padding: 21px 16px;
    background-color: $color-white;

    @include bp(sm-min) {
        padding: 48px 36px;
    }

    .filter--calculator & {
        padding: 16px 48px 16px 16px;

        @include bp(sm-min) {
            padding: 48px 36px 16px;
        }
    }

    .filter--machine & {
        display: none;
        width: 100%;
        height: auto;
        padding: 0;
        text-align: right;
        background-color: transparent;

        @include bp(sm-min) {
            display: block;
        }

        @include bp(md-min) {
            position: absolute;
            top: 0;
            left: 0;

            display: inline-block;
            width: 20px;
            height: 100%;
        }
    }

    .filter--search & {
        background-color: transparent;

        @include bp(sm-min) {
            padding-bottom: 0;
            padding-right: 24px;
            text-align: right;
        }

        @include bp(md-min) {
            text-align: left;
            padding-left: 0;
        }
    }
}

.filter__title {
    font-size: $font-size-semi;
    line-height: 1.4;
    color: $color-red-brand;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: $font-weight-bold;

    @include bp(sm-min) {
        font-size: $font-size-mlarge;
        line-height: 1;
    }

    .filter--machine & {
        display: inline-block;

        @include bp(md-min) {
            transform: rotate(-90deg) translateX(-100%);
            transform-origin: left top;
        }
    }
}

.filter__footer {
    background-color: $color-red-brand;
    color: $color-white;
    padding: 0;

    @include bp(sm-min) {
        padding: 12px 12px 12px 36px;
    }

    .filter--search & {
        @include bp(sm-min) {
            display: none;
        }
    }

    .filter--calculator & {
        padding: 0;

        @include bp(sm-min) {
            padding: 12px 36px;
        }
    }

    .filter--machine & {
        @include bp(sm-min) {
            display: none;
        }
    }
}

.filter__result {
    vertical-align: top;
    display: flex;
    padding: 20px 16px 16px;

    @include bp(sm-min) {
        padding: 0;
    }

    .filter--calculator & {
        padding: 20px 16px 16px;
    }
}

.filter__result-text {
    display: inline-block;
    font-size: $font-size-tiny;
    text-transform: uppercase;
    letter-spacing: 1px;
    max-width: 280px;
    line-height: 1.5;
    font-weight: $font-weight-bold;
    vertical-align: top;
    @include font-smoothing(true);

    @include bp(sm-min) {
        margin-top: 6px;
        font-size: $font-size-small;
    }
}

.filter__result-nr {
    font-size: 36px;
    font-weight: $font-weight-bold;
    display: inline-block;
    font-style: italic;
    line-height: 1;
    padding-left: 16px;
    @include font-smoothing(true);

    @include bp(sm-min) {
        font-size: $font-size-xxxlarge;
    }

    .filter--calculator & {
        padding-left: 0;
    }

    span {
        font-size: 46%;
        text-transform: uppercase;
        letter-spacing: 1px;
    }
}

.filter__submit {
    text-align: right;
}

.filter__submit-button {
    font-size: $font-size-semi;
    letter-spacing: 4px;

    .filter--calculator & {
        padding: 28px 40px 26px;
    }
}

.filter__header-content {
    margin-top: 12px;

    @include bp(sm-min) {
        margin-top: 32px;
    }
}

.filter__data-list {
    font-size: $font-size-tiny;
    margin-top: 12px;
}

.filter__data,
.filter__data-label {
    display: inline-block;
}

.filter__data-label {
    text-transform: uppercase;
    letter-spacing: 1px;
}

.filter__data {
    font-weight: $font-weight-medium;
}

.form-check__label,
.select-dropdown {
    .filter & {
        font-size: $font-size-input-label;
    }
}

.filter__toggle-header {
    padding-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: inherit;
    text-decoration: none;

    &:hover {
        text-decoration: none;
    }

    @include bp(sm-min) {
        display: block;
        pointer-events: none;
        padding-bottom: 0;
    }
}

.filter__toggle-content {
    display: none;
    padding-bottom: 24px;

    @include bp(sm-min) {
        padding: 0;
        display: inline-block;
    }

    html.no-js &,
    .filter__toggle.is-open > & {
        display: block;
    }
}

.filter__icon {
    font-size: 42px;
    color: $color-black;
    @include transition(transform, $transition-duration, $transition-easing);

    @include bp(sm-min) {
        display: none;
    }

    .filter__toggle.is-open & {
        transform: rotate(180deg);
    }
}
