.split-content {
    display: block;
    width: 100%;
}

.split-content__row {
    display: block;

    @include bp(sm-min) {
        display: flex;
        align-items: flex-start;
    }

    @include bp(md-min) {
        align-items: center;
    }

    &:nth-child(odd) {
        flex-direction: row-reverse;
    }

    & + .split-content__row {
        margin-top: 50px;

        @include bp(sm-min) {
            margin-top: 105px;
        }
    }

    .split-content--video & {
        & + .split-content__row {
            margin-top: 36px;

            @include bp(sm-min) {
                margin-top: 105px;
            }
        }
    }

    &--reverse {
        flex-direction: column !important;

        @include bp(sm-min) {
            flex-direction: row !important;
        }
    }
}

.split-content__figure {
    position: relative;
    display: inline-block;
    width: 100%;

    @include bp(sm-min) {
        max-width: 348px;
        margin-right: 12px;
    }

    @include bp(md-min) {
        max-width: 470px;
        margin-right: 30px;
    }

    &:before {
        content: '';
        position: absolute;
        top: 7px;
        right: 7px;
        bottom: 7px;
        left: 7px;

        display: block;
        border: 4px solid $color-white;

        @include bp(sm-min) {
            top: 10px;
            right: 10px;
            bottom: 10px;
            left: 10px;
        }
    }

    .split-content__row:not(.split-content__row--reverse):nth-child(odd) & {
        @include bp(sm-min) {
            margin: 0 0 0 12px;
        }

        @include bp(md-min) {
            margin: 0 0 0 30px;
        }
    }

    .split-content--video & {
        max-width: none;

        @include bp(sm-min) {
            width: 50%;
        }

        @include bp(sm-min) {
            width: 48%;
        }
    }
}

.split-content__image {
    display: block;
    width: 100%;
}

.split-content__content {
    display: inline-block;
    width: 100%;
    margin-top: 20px;

    @include bp(sm-min) {
        margin: 0 0 0 12px;
    }

    @include bp(md-min) {
        margin: 0 0 0 30px;
    }

    .split-content__row:not(.split-content__row--reverse):nth-child(odd) & {
        @include bp(sm-min) {
            margin: 0 12px 0 0;
        }

        @include bp(md-min) {
            margin: 0 30px 0 0;
        }
    }

    .split-content--video & {
        max-width: none;

        @include bp(sm-min) {
            width: 50%;
        }

        @include bp(sm-min) {
            width: 52%;
        }
    }
}

.split-content__title {
    font-size: $font-size-mmidlarge;
    text-transform: uppercase;

    @include bp(sm-min) {
        font-size: $font-size-mxlarge;
        line-height: 36px;
    }
}

.split-content__text {
    margin-top: 16px;

    @include bp(sm-min) {
        margin-top: 26px;
    }
}
