.badge {
    display: inline-block;
    text-align: center;
    background-color: $color-red-brand;

    &:before {
        content: '';
        position: absolute;
        top: 100%;
        left: 0;

        display: inline-block;
        width: 0;
        height: 0;
        border-top: 10px solid $color-red-brand;
        border-left: 12px solid $color-red-brand;
        border-right: 12px solid transparent;
        border-bottom: 10px solid transparent;
    }

    &:after {
        content: '';
        position: absolute;
        top: 100%;
        right: 0;

        display: inline-block;
        width: 0;
        height: 0;
        border-top: 10px solid $color-red-brand;
        border-right: 12px solid $color-red-brand;
        border-left: 12px solid transparent;
        border-bottom: 10px solid transparent;
    }
}

.badge__inner {
    display: inline-block;
    width: 36px;
    height: 36px;
    margin: 7px 7px 17px 7px;

    font-weight: 500;
    font-size: 18px;
    line-height: 32px;

    border: 2px solid $color-white;
    color: $color-white;
}
