.faq {
    display: block;
    padding: 42px 0 156px 0;

    @include bp(sm-min) {
        display: flex;
        flex-direction: row-reverse;
        padding-top: 86px;
    }
}

.faq__content {
    display: block;
    width: 100%;

    @include bp(sm-min) {
        display: inline-block;
        padding: 48px 40px 66px 40px;
        background-color: $color-white;
    }

    @include bp(md-min) {
        padding: 0;
    }
}

.faq__content-categories {
    margin-bottom: 16px;

    @include bp(sm-min) {
        margin: 0;
    }
}

.faq__content-categories-title {
    font-size: 14px;
    line-height: 20px;
}

.faq__content-categories-select {
    margin: 5px 0;
}

.faq__content-item {
    display: none;
    padding: 21px 16px;
    background-color: $color-white;

    @include bp(md-min) {
        padding: 48px 40px 66px 40px;
    }

    &.is-current {
        display: block;
    }
}

.faq__side {
    display: block;
    width: 100%;

    @include bp(sm-min) {
        max-width: 200px;
        padding: 52px 16px 0 0;
    }
}

.faq__side-title {
    margin-top: 40px;
    font-size: 24px;
    font-weight: 700;
    line-height: 46px;
    text-transform: uppercase;
}

.faq__categories {
    display: block;
    margin-top: 16px;
    padding: 16px 17px;
    background-color: $color-white;

    @include bp(sm-min) {
        padding: 0;
        background-color: transparent;
    }
}

.faq__category {
    display: block;

    & + .faq__category {
        margin-top: 24px;
    }
}

.faq__category-link {
    display: block;
    font-size: $font-size-medium;
    line-height: 1.2;
    color: $color-dark;

    &:hover {
        text-decoration: none;
        color: $color-red-brand;
    }

    .faq__category.is-current & {
        font-weight: 500;
        color: $color-red-brand;
    }
}
