.emo {
    position: relative;
    overflow: hidden;
    height: 730px;
    background-color: $color-white;
    color: $color-dark;

    @include bp(sm-min) {
        height: 650px;
    }

    // @include bp(md-min) {
    //     height: 640px;
    // }

    &--large {
        @include bp(sm-min) {
            height: 830px;
        }

        @include bp(md-min) {
            height: 1110px;
        }
    }

    &--dark {
        background-color: $color-black;
        color: $color-white;

        @include bp(sm-min) {
            height: 640px;
            background-color: $color-black;
        }
    }

    &--top-wave {
        clip-path: polygon(0 100%, 0 0, 24% 8px, 46% 0, 70% 4px, 100% 0, 100% 100%);

        @include bp(sm-min) {
            clip-path: polygon(0 100%, 0 0, 24% 15px, 46% 0, 70% 10px, 100% 0, 100% 100%);
        }

        @include bp(md-min) {
            clip-path: polygon(0 100%, 0 0, 24% 15px, 46% 0, 70% 10px, 100% 0, 100% 100%);
        }
    }

    &--bottom-wave {
        clip-path: polygon(0 0, 0 calc(100% - 35px), 78% 100%, 100% calc(100% - 35px), 100% 0);

        @include bp(sm-min) {
            clip-path: polygon(0 0, 0 calc(100% - 62px), 42% calc(100% - 72px), 90% 100%, 100% calc(100% - 11px), 100% 0);
        }

        @include bp(md-min) {
            clip-path: polygon(0 0, 0 calc(100% - 62px), 52% calc(100% - 72px), 78% 100%, 100% calc(100% - 11px), 100% 0);
        }

        &:first-child {
            z-index: 3;
            margin-bottom: -35px;

            @include bp(sm-min) {
                margin-bottom: -72px;
            }

            @include bp(md-min) {
                margin-bottom: -72px;
            }
        }
    }

    &--top-and-bottom-wave {
        clip-path: polygon(0 calc(100% - 35px), 0 0, 96% 15px, 100% 0, 100% calc(100% - 35px), 78% 100% );
        margin-top: -16px;

        @include bp(sm-min) {
            clip-path: polygon(0 100%, 0 0, 24% 8px, 46% 0, 70% 4px, 100% 0, 100% calc(100% - 11px), 90% 100%, 42% calc(100% - 72px), 0 calc(100% - 62px) );
            margin-top: -16px;
        }

        @include bp(md-min) {
            clip-path: polygon(0 100%, 0 0, 24% 15px, 46% 0, 70% 10px, 100% 0, 100% calc(100% - 11px), 78% 100%, 52% calc(100% - 72px), 0 calc(100% - 62px) );
            margin-top: -16px;
        }
    }

    &:not(first-child) + .emo--top-wave {
        z-index: 3;
        margin-top: -16px;

        @include bp(sm-min) {
            margin-top: -16px;
        }

        @include bp(md-min) {
            margin-top: -18px;
        }
    }
}

.emo__inner {
    position: relative;
    display: flex;
    height: 100%;
    align-items: center;

    @include bp(md-min) {
        display: flex;
        align-items: center;
    }

    .emo--reverse & {
        @include bp(md-min) {
            flex-direction: row-reverse;
        }
    }

    .emo--large & {
        justify-content: flex-end;

        @include bp(md-min) {
            justify-content: flex-start;
        }
    }

    .emo--dark & {
        @include bp(sm-min) {
            justify-content: flex-end;
        }

        @include bp(md-min) {
            justify-content: flex-start;
        }
    }
}

.emo__figure {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    left: 0;

    @include bp(sm-min) {
        position: relative;
        width: 50%;
    }

    &:after {
        content: '';
        display: none;
        width: 100%;
        height: 0;
        padding-bottom: 56.25%;
    }

    &.has-video {
        &:after {
            display: block;
        }
    }

    .emo--background & {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        z-index: 0;
        overflow: hidden;
    }

    .emo--dark & {
        @include bp(sm-min) {
            position: absolute;
        }
    }
}

.emo__picture {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
}

.emo__image,
.emo__video {
    display: block;
    object-fit: cover;
    width: 100%;
    height: 100%;

    .emo--background & {
        vertical-align: top;
    }

    .emo--dark & {
        @include bp(sm-min) {
            vertical-align: top;
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }
}

.emo__video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.emo__content {
    position: relative;
    display: block;
    width: 100%;
    padding: 0 16px;
    z-index: 2;

    @include bp(sm-min) {
        padding: 0 80px 0 26px;
        width: 65%;
    }

    @include bp(lg-min) {
        padding: 0 186px 0 146px;
        width: 50%;
    }

    .emo--large & {
        @include bp(sm-min) {
            padding: 80px 40px 80px 80px;
        }

        @include bp(lg-min) {
            padding: 0 186px 0 146px;
        }
    }

    .emo--reverse & {
        @include bp(sm-min) {
            padding: 80px 40px 80px 80px;
        }

        @include bp(md-min) {
            padding: 0 146px 0 186px;
        }
    }
}

.emo__title {
    text-transform: uppercase;
    line-height: 1.4;
    @include font-smoothing(true);
}

.emo__button {
    padding: 28px 40px 26px;
    letter-spacing: 4px;
    margin-top: 26px;
}

.emo__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: rgba($color-white,.8);

    @include bp(sm-min) {
        width: 65%;
    }

    @include bp(lg-min) {
        width: 50%;
    }

    .emo--large & {
        @include bp(sm-min) {
            width: 65%;
            right: 0;
            height: auto;
            left: auto;
        }

        @include bp(md-min) {
            height: 100%;
            right: auto;
            left: 0;
        }

        @include bp(lg-min) {
            width: 50%;
        }
    }

    .emo--reverse & {
        right: 0;
        left: auto;
    }

    .emo--dark & {
        background-color: rgba($color-dark,.8);

        @include bp(sm-min) {
            width: 64%;
            left: 36%;
        }

        @include bp(md-min) {
            width: 50%;
            left: auto;
        }
    }
}
