.form-group {
    display: block;
    width: 100%;
}

.form-group__label {
    display: block;
    margin-bottom: 26px;
    font-size: $font-size-small;
    font-weight: $font-weight-bold;
    color: $color-brown-medium;
}

.form-group__row {
    display: flex;

    &:not(.is-hidden) + & {
        margin-top: 16px;
    }

    &:not(.is-hidden) + &.form-group__row--double {
        margin-top: 32px;
    }

    &:not(.is-hidden).form-group__row--last + &,
    &:not(.is-hidden) + &.form-group__row--last,
    &.form-group__row--last {
        margin-top: 40px;
    }

    .form-group--checklist & + & {
        margin-top: 12px;
    }

    .form-group--onesie & {
        display: flex;
    }

    &:not(.is-hidden).form-group__row--clean {
        margin-top: 0;
    }

    &--xs {
        @include bp(xs-max) {
            &:not(.is-hidden) + & {
                margin-top: 10px;
            }

            &:not(.is-hidden).form-group__row--double + &,
            &:not(.is-hidden) + &.form-group__row--double {
                margin-top: 32px;
            }
        }
    }

    &--actions {
        padding-left: 12px;
        padding-right: 12px;

        @include bp(sm-min) {
            padding-left: 20px;
            padding-right: 20px;
        }
    }

    &--align-center {
        justify-content: center;
    }

    &--small-margin:not(.is-hidden) + & {
        margin-top: 8px;
    }
}

.form-group__row-item {
    &--quantity {
        background: $color-white;
        padding: 0 20px 0 40px;
    }

    .footer__newsletter & {
        flex: 1 1 100%;
    }
}

.form-group__row-shipping-info {
    .woocommerce-checkout-shipping & {
        margin-top: 4px !important;
    }
}

.form-group__row-item--action {
    .form-group--product & {
        flex: 1 1 auto;
    }

    .footer__newsletter & {
        flex: 1 1 52px;
    }
}
