.author {
    background-color: $color-gray-bg;
    width: 100%;
    margin-top: 16px;

    @include bp(sm-min) {
        margin-top: 56px;
    }
}

.author__content {
    padding: 20px;

    @include bp(sm-min) {
        padding: 25px;
    }

    .grid,
    .grid > * {
        @include bp(sm-min) {
            margin-bottom: 0;
        }
    }
}

.author__title {
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    color: $color-dark;
    text-transform: uppercase;
    margin-bottom: 5px;

    @include bp(sm-min) {
        padding-top: 8px;
    }
    @include bp(md-min) {
        padding-top: 10px;
    }
}

.author__subtitle {
    font-size: $font-size-tiny;
    font-weight: $font-weight-normal;
    color: $color-dark;
}

.author__tagline {
    font-size: $font-size-tiny;
    font-weight: $font-weight-normal;
    color: $color-red;
}

.author__description {
    font-size: $font-size-tiny;
    font-weight: $font-weight-normal;
    color: $color-dark;
    margin-top: 10px;

    @include bp(sm-min) {
        margin-top: 0;
    }
}

.author__image {
    width: 100px;
    height: 100px;
}

.author__img {
    border-radius: 50%;
}

.author__col {
    &--small {
        @include bp(xs-max) {
            max-width: 45%;
        }
    }

    &--large {
        @include bp(xs-max) {
            max-width: 55%;
        }
    }
}
