.brands-list {
    position: relative;
    padding: 74px 0;
    background-color: $color-dark;
    z-index: 2;

    &--top {
        margin-top: -54px;
    }

    &--clean {
        position: relative;
        padding: 56px 0;
        margin-bottom: -24px;
        clip-path: polygon(0 0, 0 94%, 15% 100%, 100% 94%, 100% 0);
        z-index: 3;
        margin-top: -1px; // fix subpixel issue caused by clip-path

        @include bp(sm-min) {
            padding: 76px 0 86px 0;
            clip-path: polygon(0 0, 0 90%, 3% 100%, 45% 91%, 100% 91%, 100% 0);
        }

        @include bp(md-min) {
            margin-bottom: -40px;
            padding: 56px 0 56px 0;
            clip-path: polygon(0 0, 0 84%, 20% 100%, 40% 90%, 75% 88%, 100% 100%, 100% 0);
        }

        &:before {
            display: none;
        }
    }

    &--red {
        position: relative;
        padding: 74px 0;
        background-color: $color-red-brand;
        clip-path: polygon(0% 3%, 30% 0%, 100% 3%, 100% 100%, 0% 100%);
        z-index: 2;

        @include bp(sm-min) {
            padding: 74px 0 24px;
            clip-path: polygon(0 0, 20% 8%, 80% 6%, 100% 8%, 100% 100%, 0% 100%);
        }

        @include bp(md-min) {
            padding: 74px 0;
            clip-path: polygon(0 5%, 35% 10%, 70% 8%, 100% 12%, 100% 90%, 70% 86%, 35% 94%, 0 90%);
        }

        &:before {
            background-color: $color-red-brand;
        }
    }
}

.brands-list__grid {
    display: block;
    // margin: 0 -16px;

    @include bp(md-min) {
        display: flex;
        flex-wrap: nowrap;
        align-items: center;
        justify-content: space-between;
    }

    .brands-list--clean & {
        position: relative;
        justify-content: flex-start;
    }
}

.brands-list__inner {
    display: flex;
    justify-content: center;
    // height: 50px;
    padding: 0 16px;
    margin-left: -16px;
    margin-right: -16px;
    overflow: hidden;

    @include bp(sm-min) {
        padding: 0;
    }

    @include bp(md-min) {
        flex: 0 1 auto;
        height: auto;
        overflow: auto;
        margin-left: -16px;
        margin-right: -16px;
    }

    .brands-list--clean & {
        @include bp(sm-min) {
            flex: 0 1 100%;
        }

        @include bp(md-min) {
            flex: 0 1 auto;
        }
    }
}

.brands-list__list {
    display: flex;
    align-items: center;
    margin: 0 -16px;
    overflow-x: auto;
    overflow-y: hidden;
    padding-bottom: 20px;
    margin-bottom: -20px;

    @include bp(sm-min) {
        width: 100%;
        margin: 0 -10px;
        padding: 0;
        // justify-content: space-around;
    }

    @include bp(md-min) {
        width: auto;
        min-width: 0;
        margin: 0;
    }

    .brands-list--clean & {
        @include bp(md-min) {
            margin: 0 60px 0 0;
        }
    }
}

.brands-list__item {
    display: flex;
    padding: 0 8px;
    flex-basis: calc(100%/3);
    flex-grow: 0;
    flex-shrink: 0;

    @include bp(sm-min) {
        padding: 0;
        flex: 1 1 auto;
    }

    @include bp(md-min) {
        padding: 0 16px;
    }

    .brands-list--clean & {
        @include bp(md-min) {
            padding: 0 32px;
        }
    }

    &:first-child {
        padding-left: 16px;

        @include bp(md-min) {
            padding-left: 0;
        }
    }

    &:last-child {
        padding-right: 16px;

        @include bp(md-min) {
            padding-right: 0;
        }
    }
}

.brands-list__img {
    display: block;
    width: 80px;
    height: 100%;
}

.brands-list__more {
    text-align: center;
    flex: 0 1 10%;
    padding: 0 16px;
    align-self: center;

    @include bp(md-min) {
        flex: 0 1 7%;
    }

    .brands-list--clean & {
        position: absolute;
        right: 0;

        padding: 0;
    }
}

.brands-list__more-icon {
    font-size: 26px;
    color: $color-white;

    .brands-list--clean & {
        font-size: 17px;
    }
}

.brands-list__cta {
    flex: 0 1 100%;
    text-align: center;
    margin-top: 30px;

    @include bp(md-min) {
        padding: 0 16px;
        margin-top: 0;
        flex: 0 1 auto;
        text-align: left;
    }
}

.brands-list__button {
    padding-left: 30px;
    padding-right: 30px;
    font-weight: 700;
    font-size: 14px;
    box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, .26);

    .button__icon {
        font-size: 39px;
    }
}
