.event-package {
    display: block;
    width: 100%;
    max-width: 374px;
    text-align: center;
}

.event-package__image {
    display: block;
    width: 100%;

    @include bp(md-min) {
        margin: 0 auto;
        padding: 0 20px;
    }
}

.event-package__title {
    display: block;
    margin-top: 36px;
}

.event-package__description {
    display: block;
    margin-top: 16px;
    font-style: italic;
    font-size: $font-size-small;
    line-height: 22px;
    color: $color-brown-light2;
}

.event-package__price-info {
    display: block;
    margin-top: 16px;
    font-weight: $font-weight-medium;
    font-size: $font-size-medium;
}

.event-package__order-button {
    display: block;
    width: 100%;
    max-width: 224px;
    margin: 34px auto 0 auto;
    padding: 17px 17px 16px;
}

.event-package__details-button {
    display: block;
    width: 100%;
    max-width: 224px;
    margin: 6px auto 0 auto;
    padding: 17px 17px 16px;
}
