// Brag bar

.brag-container {
    height: 36px;
    width: 100%;
    position: absolute;
    white-space: nowrap;
    background-color: $color-black-1-2;
}

.brag-bar {
    height: 36px;
    display: flex;
    justify-content: space-around;

    &.brag-bar--fixed {
        display: block;
    }
}

.brag-bar__item {
    color: $color-gray-bg;
    width: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: nowrap;

    .brag-bar--fixed & {
        width: 100%;
    }
}

.brag-bar__item-icon {
    margin-right: 16px;
    margin-bottom: -8px;
}

.brag-bar__item-text {
    font-size: 13px;
    line-height: 40px;
}

// Animations

.brag-bar__item-1,
.brag-bar__item-2,
.brag-bar__item-3 {
    .brag-bar--fixed & {
        position: absolute;
        width: 100%;

        animation-duration: 9s;
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite;
    }
}

.brag-bar__item-1 {
    animation-name: anim-1;
}

@keyframes anim-1 {
    0% {
        left: -100%;
        opacity: 0;
    }

    2.77%,
    30.55% {
        left: 0;
        opacity: 1;
    }

    33.33%,
    100% {
        left: 100%;
        opacity: 0;
    }
}

.brag-bar__item-2 {
    animation-name: anim-2;
}

@keyframes anim-2 {
    0%,
    33.33% {
        left: -100%;
        opacity: 0;
    }

    36.11%,
    63.88% {
        left: 0%;
        opacity: 1;
    }

    66.66%,
    100% {
        left: 100%;
        opacity: 0;
    }
}

.brag-bar__item-3 {
    animation-name: anim-3;
}

@keyframes anim-3 {
    0%,
    66.66% {
        left: -100%;
        opacity: 0;
    }

    69.44%,
    97.22% {
        left: 0%;
        opacity: 1;
    }

    100% {
        left: 100%;
        opacity: 0;
    }
}

