.article-grid {
    margin-bottom: -60px;

    @include bp(md-min) {
        margin-bottom: -104px;
    }

    &--page {
        margin-bottom: -16px; // reset to grid default
    }
}

.article-grid__column {
    margin-bottom: 60px;

    @include bp(md-min) {
        margin-bottom: 104px;
    }

    &--featured {
        margin-bottom: 56px;
    }

    .article-grid--square & {
        @include bp(md-min) {
            margin-bottom: 70px;
        }
    }

    .article-grid--page & {
        margin-bottom: 16px; // reset to grid default
    }
}
