.v404__top {
    padding-top: 60px;
    padding-bottom: 320px;
    position: relative;

    @include bp(sm-min) {
        padding-top: 123px;
        padding-bottom: 166px;
    }

    @include bp(md-min) {
        padding-top: 144px;
        padding-bottom: 281px;
    }

}

.v404__bg {
    position: absolute;
    top: 0;
    right: -16px;
    overflow: hidden;
    z-index: -1;

    @include bp(sm-min) {
        right: -26px;
    }

    img {
        transform: translateX(50%);

        @include bp(sm-min) {
            transform: translate(30%, -20%);
        }

        @include bp(md-min) {
            transform: translate(0, -20%);
        }
    }
}

.v404__title {
    font-size: $font-size-xxxlarge;
    color: $color-red-brand;
    line-height: 62px;
    margin-bottom: 8px;
    font-weight: $font-weight-normal;

    span {
        display: block;
        font-size: 44px;

        @include bp(sm-min) {
            font-size: $font-size-xxxlarge;
            display: inline;
        }
    }
}

.v404__subtitle {
    font-size: $font-size-small;
    font-weight: $font-weight-medium;
    margin-bottom: 36px;
    letter-spacing: 1px;
    line-height: 26px;
}

.v404__button {
    min-width: 100px !important;
    padding: 28px 86px 26px !important;

    @include bp(xs-max) {
        display: block !important;
        margin: 0 auto;
        padding: 28px 92px 26px !important;
    }
}

