.features {
    position: relative;
    color: $color-white;
    background-color: $color-dark;
    clip-path: polygon(0% 2%, 70% 0%, 100% 2%, 100% 100%, 0% 96%);
    z-index: 2;
    margin: -16px 0 30px 0;
    padding: 64px 0;

    @include bp(sm-min) {
        clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 70% 98%, 35% 90%, 0% 100%);
        z-index: 0;
        padding: 32px 0 64px;
        margin: 0;
    }

    &--gray {
        background-color: $color-gray-bg;
        color: $color-brown;

        &:after {
            background-color: $color-gray-bg;
        }
    }

    &--top-overlay {
        @include bp(sm-min) {
            margin-top: -35px;
            padding-top: 64px;
        }

        @include bp(md-min) {
            padding-top: 44px;
        }
    }
}

.features__icon {
    font-size: 84px;
    flex: 0 0 90px;
    margin-right: 16px;
    color: $color-gray-warm2;

    .features--gray & {
        color: $color-dark;
    }

    @include bp(sm-min) {
        margin-right: 0;
        flex: 1 1 100%;
    }

    @include bp(md-min) {
        font-size: 120px;
        margin-right: 24px;
        flex: 0 0 120px;
    }
}

.features__list {
    display: flex;
    flex-wrap: wrap;

    @include bp(sm-min) {
        flex-wrap: nowrap;
    }
}

.features__item {
    display: flex;
    width: 100%;

    &:not(:first-child) {
        margin-top: 26px;

        @include bp(sm-min) {
            margin-top: 0;
        }
    }

    @include bp(sm-min) {
        display: block;
        width: 33%;
        text-align: center;
    }

    @include bp(md-min) {
        display: flex;
        flex-wrap: nowrap;
        text-align: left;
        align-items: center;
    }
}

.features__text {
    flex: 1 1 auto;

    @include bp(sm-min) {
        margin-top: 16px;
    }

    @include bp(md-min) {
        margin-top: 0;
    }
}

.features__title {
    font-size: $font-size-small;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: $font-weight-bold;
}

.features__description {
    @include font-smoothing(true);
    margin-top: 8px;
    font-size: $font-size-small;
}
