.single-hero {
    position: relative;
    margin-top: -52px;

    &.single-hero__mid-hero {
        margin-top: 0;

        @include bp(sm-min) {
            height: 714px;
        }
    }
}

.slick-dots {
    .single-hero & {
        z-index: 10;
    }
}

.single-hero__image {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 70vh;
    background-size: cover;

    &:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: 70px;
        background: linear-gradient(180deg, transparent 0%, $color-black 100%);

        .single-hero__mid-hero & {
            content: none;
        }

        @include bp(sm-min) {
            height: 241px;
            background: linear-gradient(180deg, transparent 0%, $color-black 83%, $color-black 100%);
        }
    }

    @include bp(sm-min) {
        height: 100vh;
        max-height: 100vh;
    }

    .single-hero__mid-hero & {
        @include bp(sm-min) {
            height: 713px;
        }
    }
}

.single-hero__slider {
    height: 100%;
    position: relative;

    @include bp(sm-min) {
        display: none;
    }
}

.single-hero__info {
    padding: 23px 17.5px 0 17.5px;
    background-color: $color-black;
    color: $color-gray;

    .single-hero__mid-hero & {
        padding: 48px 17.5px 0 17.5px;
        background-color: $color-white;
        color: $color-dark;

        @include bp(sm-min) {
            background-color: transparent;
        }
    }

    @include bp(sm-min) {
        position: absolute;
        left: 50%;
        top: 0;
        bottom: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: transparent;
        color: $color-black-1-2;
    }

    @include bp(md-min) {
        margin-left: 190px;
    }
}

.single-hero__image-title {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 20px;
    line-height: 32px;
    letter-spacing: .5px;
    color: $color-white;

    @include bp(sm-min) {
        max-width: 324px;
        color: $color-black-1-2;
        font-size: 28px;
        line-height: 40px;
    }

    .single-hero--dark & {
        color: $color-white;
    }

    .single-hero__mid-hero & {
        color: $color-dark-darker;
    }
}

.image-info__info {
    display: flex;
    flex-flow: row;
    margin: 16px 0 26px 0;

    .single-hero--dark & {
        color: $color-gray-new;
    }
}

.info-price {
    margin-right: 26px;
}

.info__label {
    opacity: 1;
    font-size: 12px;
    line-height: 12px;
    font-weight: 500;
    margin-bottom: 5px;

    @include bp(sm-min) {
        margin-bottom: 10px;
    }
}

.info__price {
    position: relative;
    line-height: 22px;
    font-size: 22px;
    font-weight: 700;
    font-style: italic;
    opacity: .6;

    .price-unit {
        font-size: 12px;
        text-transform: uppercase;
    }

    .single-hero--dark & {
        color: $color-gray-light-new;
    }
}

.tooltip {
    .info__price & {
        position: absolute;
        right: -28px;
        top: 50%;
        transform: translateY(-50%);
    }
}

.single-hero__image-gallery {
    display: flex;
    max-width: 100vw;
    padding-top: 31.5px;
    padding-bottom: 31.5px;
    justify-content: center;

    @include bp(sm-min) {
        justify-content: flex-start;
    }
}

img {
    .lightbox__reel-link &,
    .lightbox__video-content-overlay & {
        min-width: 100%;
        height: 100%;
    }
}

.image-gallery__item {
    width: 70.59px;
    height: 39.71px;
    overflow: hidden;
    margin: 10px;

    @include bp(sm-min) {
        margin: 5px 10px 0 0;
    }
}

.single-hero__btn {
    width: 100%;
    font-size: 12px;
    font-weight: 500;
    padding: 20px 94.5px 20px 94.5px;

    @include bp(sm-min) {
        max-width: 224px;
        padding: 20px;
    }
}

.single-hero__features {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 54px 16px 54px 16px;

    @include bp(sm-min) {
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        padding-bottom: 65px;
        flex-direction: row;
        justify-content: space-evenly;
    }

    @include bp(md-min) {
        justify-content: center;
    }
}

.feature-item + .feature-item {
    &:before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 8px;
        height: 0;
        border-left: solid 2px $color-gray-seperator;

        @include bp(sm-min) {
            height: 36px;
        }
    }
}

.feature-item {
    display: flex;
    justify-content: center;
    text-align: center;
    position: relative;

    .feature-item__value {
        font-size: 24px;
        line-height: 24px;
        font-weight: 500;
        color: $color-black-1-2;

        @include bp(sm-min) {
            color: $color-white;
        }
    }

    .feature-item__title {
        font-size: 13px;
        line-height: 26px;
        color: $color-brown;
        margin-bottom: 32px;

        @include bp(sm-min) {
            margin-bottom: 0;
            font-size: 15px;
            color: $color-gray-seperator;
        }
    }

    @include bp(sm-min) {
        display: flex;
        flex-direction: column;
        justify-content: end;
        width: 33%;
    }

    @include bp(md-min) {
        max-width: 244px;
    }

}

.single-hero__text {
    max-width: 80%;
    align-self: center;
    text-align: center;

    @include bp(sm-min) {
        text-align: left;
    }
}

.single-hero__rating {
    flex-direction: column;

    .rating__star {
        width: 23.5px;
        height: 32px;
        line-height: 32px;
    }

    .rating__star:before {
        font-size: 20px;
        color: $color-gray-seperator;
    }

    .rating__star:after {
        font-size: 20px;
    }

    .rating__star--half:after {
        width: 50%;
        color: $color-black-1-2;

        @include bp(sm-min) {
            color: $color-white;
        }
    }

    .rating__star--half:before {
        @include bp(sm-min) {
            color: transparent;
        }
    }

    .rating__star--full:after {
        color: $color-black-1-2;

        @include bp(sm-min) {
            color: $color-white;
        }
    }

    .rating__review-text {
        font-size: 13px;
        line-height: 26px;
        color: $color-brown;

        @include bp(sm-min) {
            font-size: 15px;
            color: $color-gray-seperator;
        }
    }
}

.single-hero__arrow {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 41px;
    text-align: center;

    svg {
        height: 14px;
        width: 24px;
    }
}

.single-hero__bulletpoints {
    padding-top: 24px;
    padding-bottom: 56px;
}

.bullentpoint {
    display: flex;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 8px;

    @include bp(sm-min) {
        padding-left: 0;
    }
}

.bulletpoints-icon {
    width: 26px;
    margin-right: 16px;
    display: flex;
}

.bulletpoints-text {
    font-size: 12px;
    font-style: italic;
    color: $color-gray-new;
}

.icon {
    .bulletpoints-icon & {
        width: 24px;
        height: 24px;
        align-self: center;
        color: $color-gray-new;
    }
}
