.single-product__slider-section {
    height: 700px;
}

.single-hero__specs-container {
    padding: 25px;
}

.slider__item {
    position: relative;

    .section-slider__overlay & {
        &:after {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            bottom: -1px;
            height: 70px;
            background: linear-gradient(180deg, transparent 0%, $color-black 100%);

            @include bp(sm-min) {
                height: 241px;
            }
        }
    }
}

.slide-copy {
    .section-slider__overlay & {
        z-index: 10;
    }
}

.section-related__btn-footer {
    width: 100%;
    text-align: center;
    padding-top: 46px;
}
