$simple-link-color: #a39679;

.tabs__nav-list {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
    padding: 0 16px;

    overflow-x: auto;
    background-color: rgba($color-white, .5);

    @include bp(sm-min) {
        padding: 0 24px;
    }

    @include bp(md-min) {
        background-color: transparent;
    }

    .tabs--simple & {
        justify-content: space-between;

        &:after {
            display: none;
        }
    }

    .tabs--checkout & {
        padding: 0;
    }
}

.tabs__nav-item {
    margin: 0 13px;
    flex: 0 0 auto;
    position: relative;

    @include bp(sm-min) {
        margin: 0 20px;
        flex: 0 1 auto;
    }

    &:first-child {
        margin-left: 0;
    }

    &:last-child {
        margin-right: 0;
    }
}

.tabs__nav-link {
    display: inline-block;
    width: auto;
    padding: 24px 0 9px 0;
    font-size: 12px;
    font-weight: 500;
    line-height: 1;
    text-transform: uppercase;
    white-space: nowrap;
    color: $color-dark;
    transition: color $transition-duration $transition-easing;

    @include bp(md-min) {
        padding: 0 0 22px 0;
    }

    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;

        display: block;
        width: 100%;
        height: 4px;
        background-color: $color-red-brand;
        opacity: 0;
        transition: opacity $transition-duration $transition-easing;
    }

    &:hover,
    &.is-current {
        text-decoration: none;
        color: $color-red-brand;

        &:after {
            opacity: 1;
        }
    }

    .tabs--simple & {
        font-weight: $font-weight-bold;
        color: $simple-link-color;

        &:hover,
        &.is-current {
            text-decoration: none;
            color: $color-dark;
        }

        &:after {
            display: none;
        }
    }

    .tabs--checkout & {
        text-transform: none;
        font-size: $font-size-small;
        padding-top: 0;
    }
}

.tabs__content-item {
    display: none;
    background-color: $color-gray-light2;

    &--white {
        background-color: $color-white;
    }

    .tabs--checkout & {
        background-color: transparent;
    }

    html.no-js &,
    &.is-open {
        display: block;
    }
}

.tabs__content-inner {
    display: block;
    width: 100%;
    max-width: $container-max-width;
    margin: 0 auto;
    padding: 86px 0 106px 0;

    @include bp(sm-min) {
        padding: 86px 0 106px 0;
    }

    @include bp(md-min) {
        padding: 86px 26px 106px 26px;
    }

    @include bp(lg-min) {
        padding: 86px $grid-gutter/2 106px $grid-gutter/2;
    }

    .tabs--checkout & {
        padding: 0;
    }
}
