.pagination {
    display: block;
    width: 100%;
    margin-top: 36px;

    @include bp(sm-min) {
        display: flex;
        justify-content: space-between;
        margin-top: 76px;
    }

    @include bp(md-min) {
        margin-top: 130px;
    }
}

.pagination__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    list-style: none;
    padding: 0;
    margin: 0;
}

.pagination__item {
    display: none;
    margin: 0 5px;
    flex: 1 1 auto;

    @include bp(sm-min) {
        display: inline-block;
    }

    &:first-child {
        margin-left: 0;
    }

    &:last-child {
        margin-right: 0;
    }

    &--separator {
        display: none;

        @include bp(sm-min) {
            display: inline-block;
        }
    }

    &--prev,
    &--next {
        display: inline-block;
    }

    &--select {
        display: inline-block;

        @include bp(sm-min) {
            display: none;
        }
    }
}

.pagination__select {
    position: relative;
    height: 51px;
    font-size: 16px;
    font-weight: $font-weight-medium;
    text-transform: uppercase;
    text-align: center;
    color: $color-dark;
    background-color: $color-gray-light4;
}

.pagination__select-inner {
    position: relative;
    display: inline-block;

    &:after {
        position: absolute;
        top: 50%;
        right: 0;
        width: 5px;
        height: 5px;
        border-left: 1px solid $color-dark;
        border-bottom: 1px solid $color-dark;
        transform: rotate(-45deg);
        pointer-events: none;
        content: '';
        margin-top: -4px;
    }
}

.pagination__select-input {
    display: block;
    background: transparent;
    border: none;
    height: 51px;
    text-align: center;
    width: auto;
    margin: 0 auto;
    padding-top: 2px;
    padding-right: 12px;
    -webkit-appearance: none;

    &::-ms-clear {
        display: none;
    }

    &:focus {
        outline: none;
    }
}

.pagination__span,
.pagination__link {
    display: inline-block;
    width: 100%;
    height: 51px;

    line-height: 51px;
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    text-transform: uppercase;
    text-align: center;

    color: $color-dark;
    background-color: $color-gray-light4;

    @include bp(sm-min) {
        width: 51px;
    }

    .pagination__item:not(.pagination__item--separator) &:hover,
    .pagination__item--current & {
        line-height: 43px;
        text-decoration: none;
        border: 4px solid $color-gray-light4;
        box-shadow: inset 0 0 0 3px $color-white;
    }
}

.pagination__item-icon {
    font-size: 14px;

    .pagination__item--prev & {
        transform: rotate(-180deg);
    }
}

.pagination__all {
    display: block;
    width: 100%;
    height: 51px;
    padding: 0 46px;
    margin-top: 24px;

    line-height: 51px;
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    text-transform: uppercase;
    text-align: center;
    white-space: nowrap;

    color: $color-dark;
    background-color: $color-gray-light4;

    @include bp(sm-min) {
        display: inline-block;
        width: auto;
        margin-top: 0;
    }
}
