.footer {
    position: relative;
    padding: 56px 0;
    font-size: $font-size-tiny;

    color: $color-dark;
    background-color: $color-gray-bg;

    @include bp(sm-min) {
        padding-top: 136px;
        padding-bottom: 16px;
    }

    @include bp(md-min) {
        padding-top: 136px;
        padding-bottom: 0;
    }

    &.footer--white {
        background-color: $color-white2;
    }
}

.footer__title {
    position: relative;
    margin-bottom: 20px;
    font-size: $font-size-small;
    font-weight: $font-weight-bold;
    letter-spacing: 1px;
    text-transform: uppercase;

    @include bp(sm-min) {
        white-space: nowrap;
    }

    @include bp(md-min) {
        margin-bottom: 20px;
        white-space: initial;
    }
}

.footer__grid {
    margin-left: -16px;
    margin-right: -16px;
    padding-bottom: 16px;
    margin-bottom: 0;

    @include bp(sm-min) {
        padding-bottom: 80px;
    }

    .footer--checkout & {
        @include bp(sm-min) {
            max-width: 525px;
            margin: auto;
            padding-bottom: 0;
        }

        @include bp(md-min) {
            max-width: 100%;
            padding-bottom: 80px;
        }
    }
}

.footer__grid-item {
    padding: 0 16px;

    &:first-child {
        @include bp(md-min) {
            order: 0;
        }
    }

    &:last-child {
        order: 5;

        @include bp(sm-min) {
            order: 0;
        }
    }

    .footer--checkout & {
        margin-bottom: 48px;

        @include bp(md-min) {
            flex-basis: 20%;
            max-width: 20%;
            margin-bottom: 60px;
        }

        @include bp(md-min) {
            margin-bottom: initial;
        }
    }
}

.footer__content {
    line-height: 26px;

    @include bp(md-min) {
        max-width: 190px;
    }
}

.footer__menu-item {
    margin-top: -1px;
    margin-left: -16px;
    padding-left: 16px;
    margin-right: -16px;
    padding-right: 16px;
    position: relative;
    line-height: 32px;

    &:after {
        // arrow for mobile
        content: '';
        transform: rotate(225deg);
        width: 8px;
        height: 8px;
        position: absolute;
        top: 50%;
        right: 20px;
        margin-top: -4px;

        @include bp(sm-min) {
            display: none;
        }
    }

    @include bp(sm-min) {
        border: none;
        margin-top: 5px;
        margin-left: 0;
        margin-right: 0;
        padding: 0;
    }
}

.footer__menu-link {
    color: $color-dark;
    display: block;
    text-transform: uppercase;

    @include bp(sm-min) {
        display: inline-block;
    }
}

.footer__submenu {
    display: flex;
    justify-content: space-between;

    @include bp(sm-min) {
        justify-content: flex-start;
    }
}

.footer__submenu-item {
    @include bp(sm-min) {
        padding-right: 26px;
    }
}

.footer__submenu-link {
    color: $color-brown;
}

.footer__social {
    display: flex;
    margin-top: 15px;
}

.footer__social-item {
    display: inline-block;
    color: inherit;

    &:hover {
        text-decoration: none;
    }

    & + & {
        margin-left: 5px;
    }
}

.footer__social-icon {
    display: block;
    width: 39px;
    height: 34px;
    color: $color-brown-dark2;

    @include bp(sm-min) {
        width: 41px;
        height: 36px;
    }
}

.footer__newsletter {
    margin-top: 10px;
    margin-bottom: 20px;

    @include bp(sm-min) {
        max-width: 277px;
        margin-bottom: 0;
    }
}

.footer__disclaimer {
    text-align: center;
    margin: 16px auto 0;
    color: $color-brown;

    @include bp(sm-min) {
        text-align: right;
        margin: 0 0 0 auto;
    }

    @include bp(md-min) {
        text-align: center;
        margin: 0 auto;
    }
}

.footer__scroll-top {
    font-size: 24px;
    display: block;
    width: 24px;
    margin: 10px auto 0;
    cursor: pointer;

    @include bp(md-min) {
        display: none;
    }

    svg {
        transform: rotate(-90deg);
    }
}

.footer__image {
    vertical-align: top;
}
