.recipe-list__progress {
    width: 100%;

    @include bp(xs-max) {
        background-color: $color-white;
        padding-bottom: 10px;
        position: fixed;
        z-index: 20;
        bottom: 56px;
        left: 0;
        display: flex;
        flex-direction: column-reverse;
    }

    @include bp(sm-min) {
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    @include bp(md-min) {
        display: flex;
        flex-direction: row;
        align-items: center;
    }
}

.recipe-list__progress-bar {
    height: 6px;
    display: block;
    width: 100%;

    svg {
        height: 100%;
    }
}

.recipe-list__list {
    display: flex;
    flex-direction: column;
    @include bp(sm-min) {
        flex-direction: column;
    }
    @include bp(md-min) {
        flex-direction: row;
    }
}

.recipe-list__first-list {
    margin-bottom: 28px;
    @include bp(sm-min) {
        margin-bottom: 28px;
    }
    @include bp(md-min) {
        margin-bottom: 8px;
    }
}

.recipe-list__second-list {

    @include bp(sm-min) {
        margin-bottom: 15px;
    }
    @include bp(md-min) {
        margin-left: 32px;
    }
}

.recipe-list__title {
    font-size: $font-size-large;
    font-weight: $font-weight-bold;
    margin-bottom: 30px;
    @include bp(sm-min) {
        font-size: $font-size-xlarge;
    }
    @include bp(md-min) {
        font-size: $font-size-xlarge;
    }
}

.recipe-list__subtitle {
    font-size: $font-size-base;
    font-weight: $font-weight-bold;
    margin-bottom: 16px;
}

.recipe-list__checkbox-item {
    font-size: $font-size-base;
    margin-bottom: 8px;
}

.recipe-list__progress-bar-text {
    white-space: nowrap;
    font-size: $font-size-base;
    font-weight: $font-weight-medium;
    color: $color-red-brand;
    margin-left: 16px;

    @include bp(xs-max) {
        text-align: center;
    }
    @include bp(sm-min) {
        width: 100px;
    }
    @include bp(md-min) {
        width: 100px;
    }
}
