.properties {
    &--meeting {
        padding-top: 30px;

        @include bp(sm-min) {
            padding-top: 60px;
            padding-bottom: 60px;
        }

        .get-in-contact {
            display: none;

            @include bp(sm-min) {
                display: block;
            }
        }
    }

    &--product {
        padding: 91px 0 96px 0;
    }
}

.properties__producer {
    display: block;

    @include bp(sm-min) {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        padding-bottom: 60px;
        justify-content: center;
    }

    @include bp(md-min) {
        justify-content: space-between;
        padding-bottom: 26px;
    }
}

.properties__producer-logo {
    padding-right: 64px;
}

.properties__producer-links {
    padding-left: 64px;
}

.properties__title {
    text-transform: uppercase;
    line-height: 36px;

    // @include bp(sm-min) {
    //     padding-right: 30px;
    // }
}

.properties__content {
    display: block;
    margin-top: 26px;

    @include bp(md-min) {
        display: flex;
    }

    .properties--meeting & {
        max-width: 540px;
        margin-left: auto;
        margin-right: auto;

        @include bp(md-min) {
            max-width: none;
        }
    }
}

.properties__content-image {
    margin-left: -16px;
    margin-right: -16px;

    @include bp(sm-min) {
        margin-left: 0;
        margin-right: 0;
    }
}

.properties__text {
    display: block;
    width: 100%;

    @include bp(md-min) {
        width: 50%;
        padding-right: 57px;
    }
}

.properties__info {
    display: block;
    width: 100%;

    @include bp(md-min) {
        width: 50%;
        padding-left: 57px;
    }

    .properties--meeting & {
        margin-top: 32px;

        @include bp(md-min) {
            margin-top: 0;
        }
    }
}

.properties__description {
    margin-top: 46px;
    margin-bottom: 46px;

    @include bp(md-min) {
        margin-top: 0;
    }
}

.properties__list-title {
    font-size: $font-size-base;
    text-transform: uppercase;
    margin-bottom: 26px;
    font-weight: $font-weight-bold;
}

.properties__image {
    display: block;
    width: 100%;
    margin-top: 46px;
}
