.meeting-package__cards {
    min-height: 300px;
    background-color: $color-gray-bg;
    margin-bottom: 20px;
    padding-left: 16px;
    padding-right: 16px;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;

    @include bp(sm-min) {
        max-width: 720px;
        flex-direction: row;
        padding-left: 26px;
    }
    @include bp(md-min) {
        max-width: 961px;
        flex-direction: row;
        padding-left: 26px;
    }

    @include bp(lg-min) {
        padding-left: 86px;
    }

    &:before {
        content: '';
        width: 700px;
        height: 700px;
        background-color: $color-red-brand;
        transform: rotate(20deg);
        position: absolute;
        bottom: 0;
        left: 0;
        margin: 335px -380px -475px;

        @include bp(sm-min) {
            transform: rotate(25deg);
            position: absolute;
            bottom: 0;
            left: 0;
            margin: 150px 0 -550px 330px;
        }

        @include bp(md-min) {
            transform: rotate(125deg);
            position: absolute;
            bottom: 50px;
            left: 0;
            margin: 150px 0 -600px 432px;
        }

        @include bp(lg-min) {
            transform: rotate(121deg);
            margin: 150px 0 -600px 455px;
        }

        .meeting-package--maintenance & {
            transform: rotate(16deg);

            @include bp(sm-min) {
                transform: rotate(25deg);
                position: absolute;
                bottom: 0;
                left: 0;
                margin: 140px 0 -540px 320px;
            }

            @include bp(md-min) {
                margin: 140px 0 -530px 480px;
            }
        }
    }
}

.meeting-package__top-part {
    padding: 46px 0 90px;

    @include bp(sm-min) {
        padding: 46px 0;
    }
}

.meeting-package__bottom-part {
    margin-top: auto;
    margin-bottom: 17px;

    @include bp(sm-min) {
        margin-bottom: 27px;
        margin-left: auto;
        margin-right: 26px;
    }

    @include bp(md-min) {
        margin-bottom: 47px;
        margin-left: auto;
        margin-right: 96px;
    }

    @include bp(lg-min) {
        margin-bottom: 40px;
        margin-left: auto;
        margin-right: 106px;
    }

    .meeting-package--maintenance & {
        @include bp(sm-min) {
            padding-left: 40px;
            transform: rotate(25deg);
            flex: 0 0 250px;
        }
    }
}

.meeting-package__title {
    font-size: $font-size-xxlarge;
    font-weight: $font-weight-medium;
    color: $color-dark;
    line-height: 1.35;
    text-transform: uppercase;

    .meeting-package--maintenance & {
        text-transform: none;
    }
}

.meeting-package__subtitle {
    font-size: $font-size-base;
    font-weight: $font-weight-bold;
    color: $color-dark;
    margin-top: -8px;
    margin-bottom: 16px;

    @include bp(sm-min) {
        margin-bottom: 26px;
        margin-top: -2px;
    }

    @include bp(md-min) {
        margin-bottom: 26px;
        margin-top: -2px;
    }
}

.meeting-package__label,
.meeting-package__time {
    font-size: $font-size-small;
    font-weight: $font-weight-normal;
    color: $color-white;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.meeting-package__price {
    font-size: 48px;
    font-weight: $font-weight-bold;
    color: $color-white;
    text-transform: uppercase;
}

.meeting-package__price-extra {
    font-size: $font-size-small;
    font-weight: $font-weight-normal;
    color: $color-white;
    text-transform: uppercase;
    margin-left: 10px;
    letter-spacing: 3px;
}

.meeting-package__button {
    min-width: 100px;
    padding: 17px 36px 17px;
    color: $color-dark;
    font-size: $font-size-tiny;
    letter-spacing: 1px;

    @include bp(sm-min) {
        padding: 19px 15px 19px;
    }
}

.meeting-package__icon {
    font-size: 24px;
    margin-right: 8px;
    color: $color-yellow;
    position: relative;
    top: -2px;
}

.meeting-package__price-container {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    position: relative;
    margin-bottom: -10px;
}

.meeting-package__time-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    margin-bottom: -10px;
}
