$megamenu-border-color: #1c1b1b;

.navigation {
    flex: 1 1 auto;
    @include transition(transform, $transition-duration, $transition-easing);
    padding: 0;
    margin: 0;
    position: absolute;
    top: 0;
    width: 100%;
    padding-top: 60px;

    @include bp(sm-min) {
        width: 320px;
    }

    @include bp(md-min) {
        padding-top: 0;
        position: relative;
        overflow: visible;
        width: auto;
        height: auto;
        background-color: transparent;
    }

    .navigation__item.is-open & {
        transition-delay: 0ms;

        @include bp(md-max) {
            transform: translate3d(0, 0, 1px);
        }
    }
}

.navigation__logo {
    @include bp(md-min) {
        display: none;
    }
}

.navigation__back {
    position: absolute;
    background-color: transparent;
    color: $color-red-brand;
    font-size: 26px;
    width: 48px;

    @include bp(md-min) {
        display: none;
    }

    .navigation__item.is-open & {
        transform: none;
    }
}

.button__icon {
    .navigation__cta & {
        margin-bottom: 1px;
        margin-left: 6px;
        flex-shrink: 0;
    }

    .navigation__back & {
        transform: rotate(90deg);
        font-size: 14px;
    }
}

.button__inner {
    .navigation__cta & {
        flex: 1 1 auto;
    }
}

.navigation__title {
    text-align: center;
    font-size: $font-size-small;
    font-weight: $font-weight-bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    line-height: 55px;
    height: 52px;
    pointer-events: none;
    color: $color-red-brand;

    @include bp(md-min) {
        height: auto;
        display: none;
    }

    .navigation__list--megamenu & {
        margin-bottom: 10px;
    }
}

.navigation > .navigation__list {
    padding: 10px 0;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;

    @include bp(md-min) {
        flex-direction: row;
        justify-content: space-between;
        padding: 0;
    }

    @include bp(lg-min) {
        padding: 0 36px;
    }
}

.navigation__list {
    .header__navigation--mobile > & {
        @include bp(sm-max) {
            padding-bottom: 65px;
        }
    }
}

.navigation__list--megamenu {
    position: fixed;
    top: 0;
    left: 0;
    padding: 0;
    width: 100%;
    display: block;
    flex-direction: column;
    z-index: 4;
    height: 100%;
    background-color: $color-gray-light3;
    pointer-events: none;
    overflow-x: hidden;
    backface-visibility: hidden;

    @include bp(sm-max) {
        height: 100%;
        background-color: $color-white;
        transform: translate3d(100%, 0, 0);
        @include transition(transform, $transition-duration, $transition-easing);
    }

    @include bp(md-min) {
        position: absolute;
        top: 52px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: auto;
        height: auto;
        min-width: 320px;
        max-width: 1128px;
        padding: 45px 45px 60px;
        border-top: 1px solid rgba($megamenu-border-color, .06);
        opacity: 0;
        visibility: hidden;
        overflow: hidden;
        transform: translate3d(0, 25px, 0);
        @include transition(transform opacity, $transition-duration-2, ease);
    }

    .navigation__item.is-open & {
        pointer-events: auto;

        @include bp(md-max) {
            transform: translate3d(0, 0, 1px);
            transition-delay: 0ms;
        }

        @include bp(md-min) {
            opacity: 1;
            visibility: visible;
            transform: translate3d(0, 0, 1px);
        }
    }

    .navigation__item--special &,
    .navigation__item--special ~ .navigation__item & {
        @include bp(md-min) {
            right: 156px;
            left: auto;
            position: fixed;
        }
    }

    &:not(.navigation__list--full) {
        padding-bottom: 180px;

        @include bp(md-min) {
            margin-left: -50%;
        }
    }
}

.navigation__list--childmenu {
    position: absolute;
    top: 0;
    left: 0;
    padding: 0;
    width: 100%;
    display: block;
    z-index: 3;
    height: 100%;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;

    @include bp(xs-max) {
        padding-bottom: 65px;
    }

    @include bp(sm-max) {
        background-color: $color-gray-light3;
        transform: translate3d(100%, 0, 0);
        transition-property: transform;
        transition-duration: $transition-duration;
        transition-timing-function: $transition-easing;
    }

    @include bp(md-min) {
        height: auto;
        display: block;
        position: relative;
        width: auto;
        min-width: 260px;
        background-color: transparent;
    }

    .navigation__item.is-open > & {
        @include bp(md-max) {
            transform: translate3d(0, 0, 0);
            transition-delay: $transition-delay;
        }
    }
}

.navigation__list--full {
    @include bp(md-min) {
        min-width: 1024px;
    }

    @include bp(lg-min) {
        min-width: 1140px;
    }
}

.navigation__list--links {
    display: none;

    @include bp(md-min) {
        display: block;
    }
}

.navigation__item {
    @include transition(background-color, $transition-duration, ease);

    .navigation > .navigation__list > & {
        padding: 11px 16px;
        display: flex;
        align-items: center;

        @include bp(md-min) {
            padding: 0;
            position: relative;
        }

        &:hover {
            > .navigation__link {
                @include bp(md-min) {
                    color: $color-red-brand;
                }
            }

            &:first-child {
                @include bp(md-min) {
                    background-color: $color-gray-light3;
                }
            }
        }

        &:first-child {
            @include bp(md-min) {
                margin-right: 17px;
            }

            &:after {
                @include bp(md-min) {
                    content: '';
                    position: absolute;
                    top: 50%;
                    margin-top: -5px;
                    right: -10px;
                    width: 8px;
                    height: 8px;
                    border-right: 2px solid $color-red-brand;
                    border-bottom: 2px solid $color-red-brand;
                    border-radius: 2px;
                    display: block;
                    transform: rotate(-45deg);
                }
            }
        }

        &--special {
            @include bp(md-min) {
                margin-left: auto;
            }
        }

        .logged-in &--login {
            display: none;
        }

        &--login {
            @include bp(sm-min) {
                display: none;
            }
        }

        &--profile {
            display: none;

            .logged-in & {
                display: flex;

                @include bp(sm-min) {
                    display: none;
                }
            }

            @include bp(sm-min) {
                display: none;
            }
        }
    }

    .header--new & {
        .navigation > .navigation__list > & {
            &:hover {
                > .navigation__link {
                    color: $color-red-brand;
                }

                &:first-child {
                    background-color: transparent;
                }
            }
        }
    }

    .header__navigation--mobile > .navigation__list > & {
        height: 56px;
        margin: 6px 12px 6px 0;
        position: relative;
        background-color: $color-gray-light6;
    }

    &.has-image,
    &.is-offers,
    &.is-business,
    &.is-contact,
    &.is-services {
        .header__navigation--mobile > .navigation__list & {
            height: 80px;
        }
    }

    &.has-image {
        .header__navigation--mobile > .navigation__list & {
            background-color: $color-gray-bg;
            overflow: hidden;
        }
    }

    &.is-offers {
        .header__navigation--mobile > .navigation__list & {
            background-color: $color-red-brand;
            color: $color-white;
        }
    }

    &.is-business {
        .header__navigation--mobile > .navigation__list & {
            background-color: $color-black;
            color: $color-white;
        }
    }

    &.is-services {
        .header__navigation--mobile > .navigation__list & {
            background-color: $color-yellow;
        }
    }

    &.is-contact {
        .header__navigation--mobile > .navigation__list & {
            margin-top: 28px;
        }
    }

    &.is-spacer {
        .header__navigation--mobile > .navigation__list & {
            margin-top: 56px;
        }
    }

    .navigation__list--childmenu > &,
    .navigation__list--megamenu > & {
        padding: 11px 16px;
        display: flex;
        align-items: center;

        @include bp(md-min) {
            flex: 0 1 auto;
            display: block;
            padding: 0;
        }
    }

    .navigation__list--megamenu > & {
        @include bp(md-min) {
            margin-bottom: 26px;
        }
    }

    .logged-in &--login {
        display: none;
    }

    &--login {
        @include bp(md-min) {
            display: none;
        }
    }

    &--profile {
        display: none;

        .logged-in & {
            display: flex;

            @include bp(md-min) {
                display: none;
            }
        }

        @include bp(md-min) {
            display: none;
        }
    }
}

.navigation__item-pointer {
    position: absolute;
    bottom: -1px;
    left: 50%;

    display: inline-block;
    width: 9px;
    height: 9px;

    opacity: 0;
    visibility: hidden;
    transform: translateX(-5px);
    z-index: 3;

    @include bp(md-min) {
        transform: translate3d(0, 25px, 0);
        @include transition(transform opacity, $transition-duration-2, ease);
        margin-left: -8px;
    }

    .navigation__item.is-open > & {
        @include bp(md-max) {
            transition-delay: 0ms;
        }
    }

    &:before,
    &:after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        border-style: solid;
        border-color: transparent;
        border-top: 0;
    }

    &:before {
        bottom: 1px;
        left: -1px;
        border-bottom-color: rgba($megamenu-border-color, .06);
        border-width: 10px;
    }

    &:after {
        border-bottom-color: $color-gray-light3;
        border-width: 9px;
    }

    .navigation__item.is-open & {
        @include bp(md-min) {
            opacity: 1;
            visibility: visible;
            transform: translate3d(0, 0, 0);
        }
    }
}

.navigation__link {
    display: inline-block;
    @include transition(color, $transition-duration, ease);

    @include bp(md-min) {
        display: block;
    }

    .navigation > .navigation__list > .navigation__item > & {
        display: inline-block;
        font-size: $font-size-small;
        text-transform: uppercase;
        font-weight: $font-weight-medium;
        color: $color-dark;
        letter-spacing: 1px;

        @include bp(md-min) {
            height: 100%;
            padding: 0 10px;
            font-size: $font-size-tiny;
            line-height: 52px;
        }

        @include bp(lg-min) {
            padding: 0 12px;
        }

    }

    .header__navigation--mobile > .navigation__list > .navigation__item > & {
        display: flex;
        height: 100%;
        width: 100%;
        align-items: center;
    }

    .navigation--dark > .navigation__list > .navigation__item > & {
        color: $color-white;
    }

    .navigation__item.is-current > & {
        color: $color-red-brand;
    }

    .navigation__list--megamenu > .navigation__item > & {
        font-size: $font-size-small;
        font-weight: $font-weight-medium;
        display: inline-block;
        color: $color-dark;
        text-transform: uppercase;
        text-align: left;
        padding-right: 8px;

        @include bp(md-min) {
            display: block;
            min-width: 260px;
            margin-bottom: 16px;
            text-transform: none;
            font-size: 16px;
            padding-right: 0;
            color: $color-red-brand;
            font-weight: $font-weight-bold;
        }
    }

    .navigation__list--childmenu > .navigation__item > & {
        display: block;
        width: 100%;
        font-size: $font-size-small;
        font-weight: $font-weight-medium;
        text-transform: uppercase;
        color: $color-dark;
        text-align: left;
        padding-right: 8px;

        @include bp(md-min) {
            font-weight: $font-weight-normal;
            text-transform: none;
            line-height: 36px;
            padding-right: 0;
        }

        &:hover {
            color: $color-red-brand;
        }
    }

    .navigation__list--links > .navigation__item > & {
        font-weight: $font-weight-medium;
        font-size: $font-size-small;
        color: $color-dark;
        line-height: 32px;

        &:hover {
            color: $color-red-brand;
        }
    }

    .navigation > .navigation__list > .navigation__item.is-business > &,
    .navigation > .navigation__list > .navigation__item.is-offers > & {
        color: $color-white;
    }

    &:hover {
        text-decoration: none;
    }
}

.navigation__cta {
    position: absolute;
    bottom: 0;
    right: 0;
    display: none;
    padding: 36px 36px 36px 110px;
    max-width: 305px;
    text-transform: uppercase;
    text-align: left;
    font-weight: $font-weight-bold;
    line-height: 1.4;
    letter-spacing: 1px;
    justify-content: flex-start;
    align-items: center;
    color: $color-white;
    opacity: 1;
    transition: $transition-duration $transition-easing-2 $transition-delay;
    transition-property: opacity, z-index;

    @include bp(md-min) {
        display: flex;
    }

    &:after {
        content: '';
        position: absolute;
        top: -20px;
        left: 40px;

        width: 350px;
        height: 250px;

        background-color: $color-red-brand;
        transform: rotate(30deg);
        transition: transform $transition-duration $transition-easing-2;
    }

    &:hover {
        &:after {
            transform: rotate(30deg) scale(1.1);
        }
    }

    &--left {
        right: auto;
        left: 0;
        max-width: 255px;
        padding: 36px 56px 36px 36px;

        &:after {
            right: 35px;
            left: auto;
            width: 250px;
            transform: rotate(-30deg);
        }

        &:hover {
            &:after {
                transform: rotate(-30deg) scale(1.1);
            }
        }
    }

    .button__inner,
    .button__icon {
        pointer-events: none;
    }

    .navigation__item.is-open & {
        display: flex;
    }

    .navigation__list.is-open-parent > & {
        @include bp(xs-max) {
            opacity: 0;
            z-index: -5;
        }
    }
}

.navigation__icon {
    font-size: 18px;
    margin-top: -4px;
    margin-left: auto;

    @include bp(md-min) {
        display: none;
    }

    &--sub {
        transform: rotate(-90deg);
        font-size: 14px;
        color: $color-red-brand;
    }
}

.navigation__img {
    position: absolute;
    top: 0;
    right: 0;
}
