.dropdown {
    margin: 0;
    padding: 8px 0;
    font-weight: 400;
    font-size: $font-size-small;
    line-height: 22px;

    overflow-y: auto;
    z-index: 999;
    will-change: width, height;

    &:before {
        content: '';
        position: fixed;
        background-color: rgba($color-black, .5);
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        pointer-events: none;
        z-index: $z-index-overlay;
        opacity: 0;
        visibility: hidden;
        @include transition(opacity visibility, $transition-duration, $transition-easing);
    }

    &.is-open {
        &:before {
            opacity: 1;
            visibility: visible;
        }
    }
}

.dropdown__content {
    position: absolute;
    top: 0;
    overflow-y: auto;
    display: none;
    background-color: $dropdown-bg-color;
    box-shadow: $dropdown-shadow;
    z-index: 999;
    opacity: 0;
    margin-top: -16px;
    margin-left: -16px;

    .dropdown__label ~ & {
        padding-top: 42px;
    }

    .submenu & {
        min-width: 238px;
    }
}

.dropdown__option {
    clear: both;
    position: relative;
    color: $dropdown-color;
    cursor: pointer;
    min-height: $dropdown-item-height;
    width: 100%;
    text-align: left;
    text-transform: none;
    font-weight: 400;
}

.dropdown__option-link {
    display: block;
    padding: 8px 16px;
    color: $color-dark;
    line-height: 1.3;

    &:hover {
        text-decoration: none;
        color: $color-red-brand;
        background-color: $dropdown-hover-bg-color;
    }

    .dropdown__option.active &,
    .dropdown__option.selected & {
        background-color: $dropdown-hover-bg-color;
    }

    .dropdown__option.active.selected & {
        background-color: darken($dropdown-hover-bg-color, 5%);
    }
}

.dropdown__label {
    position: relative;
    padding-right: 22px;
    color: inherit;

    &.is-active {
        z-index: 1000;
        pointer-events: none;
        color: $color-red-brand;

        &:after {
            display: none;
        }
    }

    &:after {
        display: block;
        content: '';
        width: 10px;
        height: 10px;
        border-left: 2px solid $color-dark;
        border-bottom: 2px solid $color-dark;
        transform: rotate(-45deg);
        position: absolute;
        top: -3px;
        right: 2px;
        backface-visibility: hidden;
    }
}
