$languages-border-color: #1c1b1b;

.languages {
    position: relative;
    display: none;

    .header__actions-list--mobile & {
        display: inline-flex;
    }

    @include bp(md-min) {
        height: 52px;
        padding: 0;
        display: inline-block;
        padding-right: 16px;
    }
}

.languages__select {
    .header__actions-list--mobile & {
        display: none;
    }

    @include bp(md-min) {
        display: none;
    }
}

.language__toggle {
    display: none;
    background-color: rgba($color-dark, .55);
    font-size: 16px;

    .header__actions-list--mobile & {
        display: inline-block;

        @include bp(md-min) {
            display: none;
        }
    }

    @include bp(md-min) {
        display: block;
        background-color: rgba(#1d1c1b, .65);
    }
}

.languages__pointer {
    position: absolute;
    bottom: -1px;
    left: 50%;

    display: inline-block;
    width: 9px;
    height: 9px;
    margin-left: -18px;

    opacity: 0;
    visibility: hidden;
    z-index: 3;
    @include transition(transform opacity, $transition-duration-2, ease);
    transform: translate3d(0, 25px, 0);

    @include bp(md-min) {
        margin-left: -9px;
    }

    &:before,
    &:after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        border-style: solid;
        border-color: transparent;
        border-top: 0;
    }

    &:before {
        bottom: 1px;
        left: -1px;
        border-bottom-color: rgba($languages-border-color, .06);
        border-width: 10px;
    }

    &:after {
        border-bottom-color: $color-gray-light3;
        border-width: 9px;
    }

    .languages.is-open &,
    .languages:hover & {
        opacity: 1;
        visibility: visible;
        transform: translate3d(0, 0, 0);
    }
}

.languages__list {
    position: absolute;
    top: 52px;
    left: 0;
    padding: 8px 0;
    background-color: $color-gray-light3;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    transform: translate3d(0, 25px, 0);
    z-index: 2;
    @include transition(transform opacity, $transition-duration-2, ease);
    display: none;
    width: 100%;

    @include bp(md-min) {
        display: block;
    }

    .header__actions-list--mobile & {
        display: block;
    }

    .languages:hover & {
        @include bp(md-min) {
            opacity: 1;
            visibility: visible;
            transform: translate3d(0, 0, 0);
        }
    }

    .languages.is-open & {
        opacity: 1;
        visibility: visible;
        transform: translate3d(0, 0, 0);
    }
}

.languages__link {
    display: block;
    text-transform: uppercase;
    font-weight: $font-weight-medium;
    color: $color-dark;
    text-align: center;

    .languages__item.is-current > & {
        @include bp(md-min) {
            text-decoration: underline;
        }
    }
}
