@keyframes loader-rolling {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.loader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: none;
    z-index: 999;

    &.is-loading,
    .product-poster.is-loading &,
    .cart--popout.is-loading &,
    .section--products .is-loading & {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(255,255,255,.9);
    }
}

.loader__inner {
    display: flex;
    align-items: center;

    /*
    .loader--center & {
        @include bp(sm-min) {
            position: relative;
            top: 50%;
            transform: translate(0, -50%);
        }
    }
    */
}

.loader__rolling {
    display: inline-block;
    position: relative;
    width: 36px;
    height: 36px;
    // transform: translate(-100px, -100px) scale(1) translate(100px, 100px);
}

.loader__rolling div,
.loader__rolling div:after {
    position: absolute;
    width: 36px;
    height: 36px;
    border: 4px solid $color-red-brand;
    border-top-color: transparent;
    border-radius: 50%;
}

.loader__rolling div {
    animation: loader-rolling 1s linear infinite;
    top: 0;
    left: 0;
}

.loader__rolling div:after {
    transform: rotate(90deg);
}

.loader__text {
    display: inline-block;
    margin: 10px 0 0 15px;
    color: $color-red-brand;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    font-size: 12px;
    font-weight: 600;
}
