/*!
    Modaal - accessible modals - v0.3.1
    by Humaan, for all humans.
    http://humaan.com
 */

$modaal-overlay-color: $color-black;

$modaal-radius: 0;
$modaal-main-bg: #ffffff;
$modaal-main-text: #000000;
$modaal-max-width: 764px;
$modaal-shadow: 6px 4px 10px rgba(0, 0, 0, .25);
$modaal-padding-xs: 16px;
$modaal-padding: 56px;
$modaal-hover-color: rgba(0,0,0, .7);

$modaal-light: #ffffff;
$modaal-primary: #555555;
$modaal-grey: #afb7bc;

.modaal__close {
    position: absolute;
    right: 30px;
    top: 30px;
    color: $color-black;
    padding: 0;
    background: none;
    -webkit-appearance: none;
    border: none;
    z-index: 11;

    svg {
        width: 12px;
        height: 12px;
    }
}

.modaal__title {
    margin-bottom: 16px;
    color: $color-red-brand;
    font-size: $font-size-mlarge;
    font-weight: $font-weight-bold;
    letter-spacing: 1px;
    line-height: 1.4;
    text-transform: uppercase;
    @include font-smoothing(true);

    @include bp(sm-min) {
        line-height: 1;
        margin-bottom: 36px;
    }

    .modaal--terms & {
        text-transform: none;
    }

    .modaal--large & {
        color: $color-red-brand;
    }

    .modaal--bordered & {
        line-height: 1.2;
        padding: 38px 28px 0;
        margin-bottom: 16px;

        span {
            font-weight: $font-weight-normal;
        }
    }

    .modaal--centered & {
        line-height: 1.4;
    }

    .modaal--collapse-title & {
        margin-bottom: 16px;
    }

    .modaal--products & {
        color: $color-black;
    }

    &--small {
        font-size: $font-size-large;
        color: $color-white;
    }
}

.modaal__content-inner {
    width: 100%;
    padding: 26px 16px 60px;

    @include bp(sm-min) {
        padding: 66px 56px;
    }

    &--header {
        padding-bottom: 0;

        @include bp(md-min) {
            width: 50%;
        }
    }

    &--secondary {
        background-color: $color-red-brand;
        color: $color-white;
        padding: 26px 16px;
        display: flex;
        flex-direction: column;

        @include bp(sm-min) {
            padding: 66px 20px 66px 26px;
        }

        @include bp(md-min) {
            box-shadow: none;
            position: relative;
            right: -1px;
        }
    }

    &--footer {
        text-align: center;

        @include bp(md-min) {
            text-align: left;
        }
    }

    // &:not(.modaal__content-inner--header) {
    //     padding-top: 0;
    // }
}

.modaal-open {
    // prevent modal jumping on input focus (iOS Safari)
    // this also requires body scroll position
    position: fixed;
    left: 0;
    right: 0;
}

.modaal-noscroll {
    // overflow: hidden;
}

.modaal-accessible-hide {
    position: absolute !important;
    clip: rect(1px, 1px, 1px, 1px);
    padding: 0 !important;
    border: 0 !important;
    height: 1px !important;
    width: 1px !important;
    overflow: hidden;
}

.modaal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    opacity: 0;
    transform: translateZ(9999px);
}

.modaal-wrapper {
    @include modaal-transition(all, .3s);
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10000;
    overflow: auto;
    opacity: 1;
    box-sizing: border-box;
    -webkit-overflow-scrolling: touch;
    transform: translateZ(10000px);

    & * {
        -webkit-backface-visibility: hidden;
    }

    .modaal-close {
        border: none;
        background: transparent;
        padding: 0;
        -webkit-appearance: none;
    }

    &.modaal-start_none {
        display: none;
        opacity: 1;
    }

    &.modaal-start_fade {
        opacity: 0;
    }

    & *[tabindex='0'] {
        outline: none !important;
    }

    &.modaal--fullscreen {
        overflow: hidden;
    }
}

.modaal-outer-wrapper {
    display: table;
    position: relative;
    width: 100%;
    height: 100%;

    .modaal--fullscreen & {
        display: block;
    }
}

.modaal-inner-wrapper {
    display: table-cell;
    width: 100%;
    height: 100%;
    position: relative;
    vertical-align: middle;
    text-align: center;
    padding: 60px 16px;

    @include bp(md-min) {
        padding: 80px 32px;
    }

    .modaal--collapsed & {
        padding: 60px 0;

        @include bp(sm-min) {
            padding: 80px 32px;
        }
    }

    .modaal--popup & {
        padding: 60px 16px;

        @include bp(sm-min) {
            padding: 80px 32px;
        }
    }

    .modaal--fullscreen & {
        padding: 0;
        display: block;
        vertical-align: top;
    }
}

.modaal-container {
    position: relative;
    display: inline-block;
    width: 100%;
    margin: auto;
    text-align: left;
    color: $modaal-main-text;
    max-width: $modaal-max-width;
    border-radius: $modaal-radius;
    background: $modaal-main-bg;
    box-shadow: $modaal-shadow;
    cursor: auto;

    &.is_loading {
        height: 100px;
        width: 100px;
        overflow: hidden;
    }

    .modaal--large & {
        max-width: 960px;

        @include bp(md-min) {
            max-width: 960px;
        }
    }

    .modaal--small & {
        max-width: 570px;

        @include bp(md-min) {
            max-width: 570px;
        }
    }

    .modaal--tiny & {
        max-width: 374px;

        @include bp(md-min) {
            max-width: 374px;
        }
    }

    .modaal--bordered & {
        border: 14px solid $color-red-brand;
    }

    .modaal--fullscreen & {
        max-width: none;
        height: 100%;
        overflow: auto;
    }
}

.modaal-close {
    @include modaal-transition(all);
    position: fixed;
    right: 16px;
    top: 16px;
    color: $modaal-light;
    cursor: pointer;
    opacity: 1;
    width: 26px;
    height: 26px;
    background: rgba(0,0,0, 0);

    &:focus,
    &:hover {
        outline: none;
    }

    span {
        @include modaal-hidetext;
    }

    &:before,
    &:after {
        display: block;
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        width: 4px;
        height: 32px;
        border-radius: 3px;
        background: #ffffff;
        @include modaal-transition(background);
        -webkit-backface-visibility: hidden;
    }

    &:before {
        transform: rotate(-45deg);
    }

    &:after {
        transform: rotate(45deg);
    }

    .modaal--fullscreen & {
        background: $modaal-grey;
        right: 10px;
        top: 10px;
    }
}

.modaal-content-container {
    padding: 26px 16px;

    @include bp(sm-min) {
        padding: $modaal-padding;
    }

    & .modaal__video {
        margin: -26px -16px;
    }

    .modaal--split & {
        padding: 0;
    }

    .modaal--collapsed & {
        padding: 0;
    }

    .modaal--popup & {
        padding: 0;
    }

    .modaal--bordered & {
        padding: 4px;
    }

    .modaal--centered & {
        text-align: center;
    }

    .modaal--iconed & {
        @include bp(sm-min) {
            padding-top: 46px;
        }
    }
}

.modaal__footer-meta {
    text-align: center;
    padding: 25px;
}

.modaal__last-order {
    padding-bottom: 36px;
}

.modaal__list {
    display: flex;
    flex: 1 1 auto;
    flex-wrap: wrap;

    @include bp(sm-min) {
        flex-direction: column;
    }
}

.modaal__list-item {
    padding-left: 0;
    @include font-smoothing(true);
    flex: 1 1 50%;
    max-width: 50%;

    @include bp(sm-min) {
        flex: 1 1 auto;
        max-width: 100%;
    }

    &:not(.modaal__list-item--last) {
        padding-bottom: 8px;
    }

    &--total {
        font-size: $font-size-mlarge;
        font-style: italic;
        font-weight: $font-weight-bold;
        line-height: 1;
    }

    &--last {
        font-size: $font-size-small;
        margin-top: auto;
    }
}

.modaal__list-label {
    font-weight: $font-weight-bold;
    font-style: normal;
    font-size: $font-size-base;

    .modaal__list-item--total & {
        line-height: 1.8;
    }
}

.modaal__split-grid {
    flex-direction: column-reverse;

    @include bp(sm-min) {
        flex-direction: row;
    }
}

.form-group__row:not(.form-group__row--last) {
    .modaal--bordered & {
        padding: 0 28px;
    }
}

.modaal__header-icon {
    font-size: 72px;
    color: $color-red-brand;
    margin-bottom: 26px;
}

// Confirm Modaal
// --------------------------------------------------------

.modaal__submit-button {
    letter-spacing: 3px;
    padding: 28px 36px 26px;
}

.modaal-confirm-wrap {
    padding: 30px 0 0;
    text-align: center;
    font-size: 0;
}

.modaal-confirm-btn {
    font-size: 14px;
    display: inline-block;
    margin: 0 10px;
    vertical-align: middle;
    cursor: pointer;
    border: none;
    background: transparent;

    // Ok Button

    &.modaal-ok {
        @include modaal-transition(background);

        padding: 10px 15px;
        color: $modaal-light;
        background: $modaal-primary;
        border-radius: 3px;

        &:hover {
            background: darken($modaal-primary, 15%);
        }
    }

    &.modaal-cancel {
        text-decoration: underline;

        &:hover {
            text-decoration: none;
            color: darken($modaal-primary, 15%);
        }
    }
}

@keyframes instaReveal {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

// Instagram Photo
// --------------------------------------------------------

.modaal-instagram {
    .modaal-container {
        width: auto;
        background: transparent;
        box-shadow: none !important;
    }

    .modaal-content-container {
        padding: 0;
        background: transparent;
    }

    .modaal-content-container > blockquote {
        width: 1px !important;
        height: 1px !important;
        opacity: 0 !important;
    }

    iframe {
        opacity: 0;
        margin: -6px !important;
        border-radius: 0 !important;
        width: 1000px !important;
        max-width: 800px !important;
        box-shadow: none !important;

        animation: instaReveal 1s linear forwards;
    }
}

// Gallery
// --------------------------------------------------------

.modaal-image {
    .modaal-inner-wrapper {
        padding-left: 140px;
        padding-right: 140px;
    }

    .modaal-container {
        width: auto;
        max-width: 100%;
    }
}

.modaal-gallery-wrap {
    position: relative;
    color: $modaal-light;
}

.modaal-gallery-item {
    display: none;

    img {
        display: block;
    }

    &.is_active {
        display: block;
    }
}

.modaal-gallery-label {
    position: absolute;
    left: 0;
    width: 100%;
    margin: 20px 0 0;
    font-size: 18px;
    text-align: center;
    color: #ffffff;

    &:focus {
        outline: none;
    }
}

.modaal-gallery-control {
    @include modaal-transition(all);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    opacity: 1;
    cursor: pointer;
    color: $modaal-light;
    width: 50px;
    height: 50px;
    background: rgba(0,0,0, 0);
    border: none;
    border-radius: 100%;

    &.is_hidden {
        opacity: 0;
        cursor: default;
    }

    &:focus,
    &:hover {
        outline: none;

        &:before,
        &:after {
            background: $color-red-brand;
        }
    }

    .modaal--gallery & span {
        @include modaal-hidetext;
    }

    &:before,
    &:after {
        display: block;
        content: '';
        position: absolute;
        top: 16px;
        left: 25px;
        width: 4px;
        height: 18px;
        border-radius: 4px;
        background: #ffffff;
        @include modaal-transition(background);
    }

    &:before {
        margin: -5px 0 0;
        transform: rotate(-45deg);
    }

    &:after {
        margin: 5px 0 0;
        transform: rotate(45deg);
    }
}

.modaal-gallery-next {
    left: 100%;
    margin-left: 40px;
}

.modaal-gallery-prev {
    right: 100%;
    margin-right: 40px;

    &:before,
    &:after {
        left: 22px;
    }

    &:before {
        margin: 5px 0 0;
        transform: rotate(-45deg);
    }

    &:after {
        margin: -5px 0 0;
        transform: rotate(45deg);
    }
}

// Video
// --------------------------------------------------------

.modaal-video-wrap {
    margin: auto 50px;
    position: relative;
}

.modaal-video-container {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0,0,0, .3);
    background: #000000;
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;

    iframe,
    object,
    embed {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

// Modaal iFrame
// --------------------------------------------------------

.modaal-iframe .modaal-content {
    width: 100%;
    height: 100%;
}

.modaal-iframe-elem {
    width: 100%;
    height: 100%;
    display: block;
}

// Responsive styles
// --------------------------------------------------------

@media only screen and (min-width: 1400px) {
    .modaal-video-container {
        padding-bottom: 0;
        height: 731px;
    }
}

@media only screen and (max-width: 1140px) {
    .modaal-image {
        .modaal-inner-wrapper {
            padding-left: 25px;
            padding-right: 25px;
        }
    }

    .modaal-gallery-control {
        top: auto;
        bottom: 20px;
        transform: none;
        background: rgba(0,0,0, .7);

        &:before,
        &:after {
            background: #ffffff;
        }
    }

    .modaal-gallery-next {
        left: auto;
        right: 20px;
    }

    .modaal-gallery-prev {
        left: 20px;
        right: auto;
    }
}

@media screen and (max-width: 900px) {
    .modaal-instagram iframe {
        width: 500px !important;
    }
}

@media screen and (max-height: 1100px) {
    .modaal-instagram iframe {
        width: 700px !important;
    }
}

@media screen and (max-height: 1000px) {
    .modaal-instagram iframe {
        width: 600px !important;
    }
}

@media screen and (max-height: 900px) {
    .modaal-instagram iframe {
        width: 500px !important;
    }

    .modaal-video-container {
        max-width: 900px;
        max-height: 510px;
    }
}

@media only screen and (max-width: 600px) {
    .modaal-instagram iframe {
        width: 280px !important;
    }
}

@media only screen and (max-height: 820px) {
    .modaal-gallery-label {
        display: none;
    }
}

// CSS loading gif
// --------------------------------------------------------

.modaal-loading-spinner {
    background: none;
    position: absolute;
    width: 200px;
    height: 200px;
    top: 50%;
    left: 50%;
    margin: -100px 0 0 -100px;
    transform: scale(.25);
}

@-ms-keyframes modaal-loading-spinner {
    0% {
        opacity: 1;
        transform: cale(1.5);
    }

    100% {
        opacity: .1;
        transform: scale(1);
    }
}

.modaal-loading-spinner > div {
    width: 24px;
    height: 24px;
    margin-left: 4px;
    margin-top: 4px;
    position: absolute;
}

.modaal-loading-spinner > div > div {
    width: 100%;
    height: 100%;
    border-radius: 15px;
    background: #ffffff;
}

.modaal-loading-spinner > div:nth-of-type(1) > div {
    animation: modaal-loading-spinner 1s linear infinite;
    animation-delay: 0;
}

.modaal-loading-spinner > div:nth-of-type(2) > div,
.modaal-loading-spinner > div:nth-of-type(3) > div {
    animation: modaal-loading-spinner 1s linear infinite;
}

.modaal-loading-spinner > div:nth-of-type(1) {
    transform: translate(84px, 4px) rotate(45deg) translate(70px, 0);
}

.modaal-loading-spinner > div:nth-of-type(2) > div {
    animation: modaal-loading-spinner 1s linear infinite;
    animation-delay: .12s;
}

.modaal-loading-spinner > div:nth-of-type(2) {
    transform: translate(84px, 84px) rotate(90deg) translate(70px, 0);
}

.modaal-loading-spinner > div:nth-of-type(3) > div {
    animation: modaal-loading-spinner 1s linear infinite;
    animation-delay: .25s;
}

.modaal-loading-spinner > div:nth-of-type(4) > div,
.modaal-loading-spinner > div:nth-of-type(5) > div {
    animation: modaal-loading-spinner 1s linear infinite;
}

.modaal-loading-spinner > div:nth-of-type(3) {
    transform: translate(84px, 84px) rotate(135deg) translate(70px,0);
}

.modaal-loading-spinner > div:nth-of-type(4) > div {
    animation: modaal-loading-spinner 1s linear infinite;
    animation-delay: .37s;
}

.modaal-loading-spinner > div:nth-of-type(4) {
    transform: translate(84px, 84px) rotate(180deg) translate(70px, 0);
}

.modaal-loading-spinner > div:nth-of-type(5) > div {
    animation: modaal-loading-spinner 1s linear infinite;
    animation-delay: .5s;
}

.modaal-loading-spinner > div:nth-of-type(6) > div,
.modaal-loading-spinner > div:nth-of-type(7) > div {
    animation: modaal-loading-spinner 1s linear infinite;
}

.modaal-loading-spinner > div:nth-of-type(5) {
    transform: translate(84px, 84px) rotate(225deg) translate(70px, 0);
}

.modaal-loading-spinner > div:nth-of-type(6) > div {
    animation: modaal-loading-spinner 1s linear infinite;
    animation-delay: .62s;
}

.modaal-loading-spinner > div:nth-of-type(6) {
    transform: translate(84px, 84px) rotate(270deg) translate(70px, 0);
}

.modaal-loading-spinner > div:nth-of-type(7) > div {
    animation: modaal-loading-spinner 1s linear infinite;
    animation-delay: .75s;
}

.modaal-loading-spinner > div:nth-of-type(7) {
    transform: translate(84px,84px) rotate(315deg) translate(70px,0);
}

.modaal-loading-spinner > div:nth-of-type(8) > div {
    animation: modaal-loading-spinner 1s linear infinite;
    animation-delay: .87s;
}

.modaal-loading-spinner > div:nth-of-type(8) {
    transform: translate(84px, 84px) rotate(360deg) translate(70px, 0);
}
