.rating,
.stars {
    display: flex;
    align-items: center;
}

.rating__stars {
    .rating--clean & {
        margin-left: -4px;

        @include bp(md-min) {
            margin-left: 0;
        }
    }
}

.rating__stars,
.stars > span {
    display: flex;

    .rating--static & {
        flex-direction: row;
    }
}

.rating__star,
.stars a {
    position: relative;
    display: inline-block;
    width: 24px;
    height: 24px;
    font-size: 24px;
    line-height: 24px;

    &:first-child {
        padding-left: 0;
    }

    &:last-child {
        padding-right: 0;
    }

    &:before {
        content: '\2605';
        position: absolute;
        top: 0;
        left: 3px;
        display: block;
        color: $color-gray-warm2;
        overflow: hidden;
        font-size: 19px;

        .rating--clean & {
            @include bp(md-min) {
                font-size: 24px;
            }
        }
    }

    &:after {
        content: '\2605';
        position: absolute;
        top: 0;
        left: 3px;
        display: none;
        color: $color-yellow;
        overflow: hidden;
        font-size: 19px;

        .rating--clean & {
            @include bp(md-min) {
                font-size: 24px;
            }
        }
    }

    &:hover {
        cursor: pointer;

        .rating--static & {
            cursor: default;
        }
    }

    &--half {
        &:after {
            display: block;
            width: 40%;
        }
    }

    &--full {
        &:before {
            display: none;
        }

        &:after {
            display: block;
        }
    }
}

.stars a {
    font-size: 0;

    &:before {
        color: $color-yellow;
    }

    &:after {
        color: $color-gray-warm2;
    }

    &.active + a,
    &.active ~ a {
        &:before {
            color: $color-gray-warm2;
        }
    }
}

.rating__star-input {
    display: none;
    opacity: .5;

    &:checked + .rating__star,
    &:checked ~ .rating__star {
        &:before {
            color: $color-gray-warm2;
        }
    }
}

.rating__review {
    display: inline-block;
    margin-left: 10px;
}

.rating__review-text {
    display: block;
    font-weight: $font-weight-medium;
    font-size: $font-size-tiny;
    color: $color-gray-warm2;
}

.rating__result {
    display: inline;
    margin-left: 10px;
    font-weight: $font-weight-medium;
    font-size: $font-size-tiny;
    color: $color-gray-warm2;
}

.comment-form-rating {
    margin-bottom: 20px;
    display: flex;
}
