$radio-default: $color-black;
$radio-border: $color-gray;
$radio-background: $color-white;
$radio-valid: $color-green;

$radio-color-shadow: #dddddd;

.form-radio {
    position: relative;
    display: inline-block;

    &--gift-custom {
        display: inline-block;
        width: 168px;

        &.is-selected {
            background-color: $color-red-brand;
        }
    }
}

.form-radio__value {
    display: flex;
    height: 41px;
    width: 100%;
    padding: 0 10px;
    margin: 0;

    font-size: $font-size-mlarge;
    font-weight: $font-weight-bold;
    font-style: italic;
    text-align: center;
    line-height: 41px;

    border: 0;
    color: $color-red-brand;
    background-color: $color-white;
    transition: padding $transition-duration $transition-easing-2;

    &:focus {
        outline: 0;
    }

    &::placeholder {
        font-weight: 500;
        font-size: 12px;
        color: $color-red-brand;
        transition: opacity $transition-duration $transition-easing-2;
    }

    .form-radio--gift-custom.is-focused &,
    .form-radio--gift-custom.is-dirty & {
        padding-right: 36px;

        &::placeholder {
            opacity: 0;
        }
    }
}

.form-radio__trigger {
    position: absolute;
    top: 0;
    right: 0;

    display: none;
    width: 41px;
    height: 41px;

    line-height: 41px;
    text-align: center;

    color: $color-white;
    background-color: $color-gray-warm2;
    cursor: pointer;

    .form-radio--gift-custom.is-focused & {
        display: inline-block;
    }

    .form-radio--gift-custom.is-dirty & {
        display: inline-block;
        background-color: $color-green;
    }
}

.form-radio__icon--check {
    display: inline-block;
}

.form-radio__icon--edit {
    display: none;
}

.form-radio__input {
    position: absolute;
    top: 0;
    left: 0;

    display: none;
    width: 100%;
    height: 100%;
    margin: 0;
    opacity: 1;
    z-index: 2;
    cursor: default;

    &:checked {
        outline: 0;

        & + .form-radio__label {
            &:before {
                background-color: $color-red-brand;
                box-shadow: 0 0 0 2px $color-red-brand;
            }

            &:after {
                border-color: $color-white;
            }

            .form-radio--gift & {
                background-color: $color-red-brand;
                color: $color-white;
                box-shadow: inset 0 0 0 2px $color-white;
                border: 2px solid $color-red-brand;
            }

            .form-radio__color {
                .form-radio--color & {
                    box-shadow: 0 0 0 5px $color-gray-warm2;
                    background-color: $radio-color-shadow;
                }
            }
        }

        & + .form-radio__value {
            background-color: $color-red-brand;
            color: $color-white;
            box-shadow: inset 0 0 0 2px $color-white;
            border: 2px solid $color-red-brand;
        }

        & + .form-radio__value + .form-radio__trigger {
            background-color: transparent;

            & > .form-radio__icon--check {
                display: none;
            }

            & > .form-radio__icon--edit {
                display: inline-block;
            }
        }
    }

    &:hover {
        & + .form-radio__label .form-radio__color {
            box-shadow: 0 0 0 5px $color-gray-warm2;
        }
    }

    .form-radio--gift-custom.is-dirty & {
        right: 0;
        left: auto;

        display: block;
        width: 36px;
        height: 36px;
        cursor: pointer;
    }
}

.form-radio__label {
    display: block;
    padding-top: 4px;
    padding-bottom: 4px;
    padding-left: 40px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    position: relative;
    color: inherit;

    // span {
    //     font-size: 50%;
    // }

    // banklink images

    img {
        display: block;
        margin-top: 8px;
        max-width: 88px;
    }

    .is-invalid & {
        color: $color-red;
    }

    &:before {
        content: '';
        display: block;
        box-shadow: 0 0 0 2px $radio-border;
        margin: 2px;
        background: $radio-background;
        position: absolute;
        top: 0;
        left: 0;
        width: 24px;
        height: 24px;
        border-radius: 50%;

        .form-radio--color &,
        .form-radio--gift & {
            display: none;
        }

        .is-invalid & {
            box-shadow: 0 0 0 2px $color-red;
        }
    }

    &:after {
        position: absolute;
        top: 9px;
        left: 8px;
        content: '';
        display: block;
        border-left: 2px solid $color-white;
        border-bottom: 2px solid $color-white;
        width: 12px;
        height: 6px;
        transform: rotate(-45deg);

        .form-radio--color &,
        .form-radio--gift & {
            display: none;
        }
    }

    .form-radio--gift & {
        display: inline-block;
        width: auto;
        padding: 10px 20px 7px;
        background-color: $color-white;
        text-align: center;
        color: $color-red-brand;
        font-size: $font-size-mlarge;
        font-style: italic;
        font-weight: $font-weight-bold;
        @include font-smoothing(true);
    }

    .form-radio--color & {
        padding: 0;
    }
}

.form-radio__label-link {
    color: $color-dark;
}

.form-radio__color {
    position: relative;
    display: block;
    width: 24px;
    height: 24px;
    margin: 5px;

    border-radius: 50%;
    box-shadow: 0 0 0 5px transparent;
    background: $radio-background;
    transition: box-shadow $transition-duration $transition-easing;
}

.form-radio__image {
    margin: 8px 0 0 40px;
}

.form-radio__hidden {
    display: none;
    margin-top: 5px;
    padding-left: 40px;

    .form-radio__input:checked ~ & {
        display: block;
        width: 100%;
    }
}

.form-radio__bubble {
    pointer-events: none;
    position: absolute;
    top: 12px;
    left: 12px;
    z-index: 3;
    transform: translate(-50%, -50%) translateZ(0);
    width: 0;
    height: 0;
    border-radius: 150px;
    background: rgba($color-red-brand, .4);

    .form-radio__input:checked ~ & {
        width: 66px;
        height: 66px;
        opacity: 0;
        transition: $transition-duration-2 ease-out;
        transition-property: width, height, opacity;

        .form-radio--gift & {
            display: none;
        }
    }
}
