.intro {
    display: block;
    width: 100%;
    background-color: $color-gray-bg;
    position: relative;

    &--manuals {
        height: 248px;
        background-color: $color-white;

        @include bp(sm-min) {
            margin-top: -52px;
            height: 295px;
        }

        @include bp(md-min) {
            height: 316px;
        }
    }

    &--faq,
    &--account {
        background-color: $color-white;
    }
}

.intro__inner {
    position: relative;
    display: block;
    width: 100%;
    max-width: $container-max-width;
    margin: 0 auto;
    overflow: hidden;
    z-index: 1;

    @include bp(md-min) {
        padding: 0 $grid-gutter/2;
    }

    .intro--faq &,
    .intro--manuals & {
        height: 100%;
        display: flex;
        flex-direction: row-reverse;
        justify-content: flex-end;

        @include bp(sm-min) {
            padding: 0 26px;
        }
    }
}

.intro__figure {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0;
    overflow: hidden;

    @include bp(md-min) {
        top: auto;
    }
}

.intro__image {
    display: block;
    object-fit: cover;
    height: 100%;

    @include bp(sm-min) {
        width: 100%;
        height: auto;
    }
}

.intro__content {
    position: relative;
    display: block;
    max-width: 615px;
    padding: 48px 16px 80px 16px;
    z-index: 2;

    @include bp(sm-min) {
        padding: 46px 24px 104px 24px;
    }

    @include bp(md-min) {
        max-width: 600px;
        padding: 104px 0;
    }

    .intro--account & {
        max-width: 415px;
        padding: 36px 16px 46px 16px;

        @include bp(sm-min) {
            padding: 48px 0 46px 24px;
        }

        @include bp(md-min) {
            padding-top: 56px;
            padding-bottom: 46px;
            padding-left: 0;
        }
    }

    .intro--manuals & {
        padding: 0 16px;
        align-self: center;
        width: 100%;

        @include bp(sm-min) {
            width: 50%;
            padding: 0 15px 0 0;
        }

        @include bp(md-min) {
            max-width: 46%;
            padding: 0 150px 0 0;
        }
    }

    .intro--faq & {
        padding: 46px 16px 44px 16px;

        @include bp(sm-min) {
            padding: 46px 24px 56px 24px;
        }

        @include bp(md-min) {
            padding: 66px 24px 92px 24px;
        }
    }
}

.intro__title {
    text-transform: uppercase;
    font-weight: $font-weight-bold;
    font-size: 28px;

    .intro--faq &,
    .intro--manuals &,
    .intro--account & {
        text-transform: none;
        color: $color-red-brand;
        font-weight: $font-weight-medium;
        @include font-smoothing(true);
    }
}

.intro__text {
    display: block;
    margin-top: 16px;
}

.intro__navigation-item {
    &:nth-child(3) {
        @include bp(sm-min) {
            margin-left: auto;
        }
    }

    &:last-child {
        .intro__navigation & {
            padding-right: 30px;
        }
    }
}

.intro__navigation-icon {
    font-size: 24px;
    position: absolute;
    right: 0;
    bottom: 8px;
    top: auto;

    @include bp(sm-min) {
        top: auto;
        bottom: 4px;
    }

    @include bp(md-min) {
        top: 2px;
        bottom: auto;
    }
}

.intro__navigation {
    position: relative;

    &:after {
        content: '';
        position: absolute;
        right: 0;
        bottom: 0;
        display: inline-block;
        width: 40px;
        height: 46px;
        background: linear-gradient(to right, rgba($color-white, 0) 0%,rgba($color-white, 1) 100%);
        pointer-events: none;

        @include bp(sm-min) {
            display: none;
        }
    }
}

.tabs__nav-list {
    .intro__navigation & {
        align-items: flex-end;

        @include bp(lg-min) {
            padding-left: 0;
            padding-right: 0;
        }
    }
}

.intro__navigation-link {
    padding-bottom: 12px;
    letter-spacing: 1px;

    &.is-current {
        font-weight: $font-weight-bold;
    }

    @include bp(sm-min) {
        padding-bottom: 8px;
    }

    @include bp(md-min) {
        padding-bottom: 20px;
    }

}
