.order-details {
    display: block;
    width: 100%;
    margin: 0 auto;
    padding: 40px 0;

    @include bp(md-min) {
        padding: 40px 56px;
    }
}

.order-details__header {
    display: block;

    @include bp(sm-min) {
        display: flex;
        justify-content: space-between;
    }
}

.order-details__title {
    font-size: $font-size-large;
}

.order-details__button {
    @include bp(sm-min) {
        text-align: right;
    }

    .button__icon {
        font-size: 16px;
    }

    & + .order-details__button {
        margin-top: 12px;
    }
}

/* Columns */

.order-details__columns {
    display: flex;
    flex-wrap: wrap;
    margin-top: 27px;

    @include bp(sm-min) {
        margin-top: 0;
    }

    &--end {
        justify-content: flex-end;
    }
}

.order-details__column {
    display: inline-block;
    width: 50%;
    font-size: $font-size-tiny;

    @include bp(sm-min) {
        width: 22%;
    }

    &--fluid {
        width: auto;
    }
}

.order-details__column-title {
    margin-bottom: 8px;
}

/* Products */

.order-details__table {
    display: block;
    width: 100%;
    padding: 26px 0;
    margin: 26px 0;
    font-size: $font-size-small;
    border-top: 1px solid $color-gray-warm2;
    border-bottom: 1px solid $color-gray-warm2;
}

.order-details__table-head {
    display: none;

    @include bp(sm-min) {
        display: block;
    }
}

.order-details__table-body {
    @include bp(sm-min) {
        margin-top: 26px;
    }
}

.order-details__table-row {
    display: flex;
    align-items: center;

    & + .order-details__table-row {
        margin-top: 27px;
    }
}

.order-details__table-column {
    display: inline-block;

    .order-details__table-head & {
        &:nth-child(1) {
            width: calc(96px + 40%);
        }

        &:nth-child(n+2) {
            width: 20%;
            text-align: right;
        }
    }

    .order-details__table-body & {
        &:first-child {
            width: 96px;
            padding-right: 16px;
        }

        &:last-child {
            width: 100%;
        }
    }
}

.order-details__product {
    display: flex;
    align-items: center;
}

.order-details__product-image {
    display: block;
    max-width: 100%;
}

.order-details__product-info {
    display: block;
    width: 100%;

    @include bp(sm-min) {
        display: flex;
        align-items: center;
    }
}

.order-details__product-title {
    display: block;
    width: 100%;

    @include bp(sm-min) {
        display: inline-block;
        width: 40%;
    }
}

.order-details__product-quantity,
.order-details__product-price,
.order-details__product-sum {
    display: block;
    width: 100%;

    @include bp(sm-min) {
        display: inline-block;
        width: 20%;
        text-align: right;
    }
}
