.header {
    height: 52px;
    // background-color: $color-white;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    position: fixed;
    top: 0;
    z-index: $z-index-header;
    background-color: $color-white;
    transform: translate3d(0, 0, 12px);
    transition-property: transform, background-color;
    transition-duration: $transition-duration;
    transition-timing-function: $transition-easing;

    @include bp(sm-min) {
        justify-content: space-between;
    }

    body.h-menu-open & {
        @include bp(xs-max) {
            position: sticky;
        }

        @include bp(sm-max) {
            background-color: $color-black;
        }
    }

    body:not(.h-menu-open) &--hidden {
        transform: translate3d(0, -100%, 12px);
        transition-delay: $transition-duration;

        &.header--brag {
            transform: translate3d(0, calc(-100% - 36px), 12px);
        }
    }

    &--fixed {
        transform: translate3d(0, 0, 12px);
    }

    &--transparent {
        background-color: transparent;
    }

    &--filled {
        background-color: $color-white;

        &.header--new.header--dark {
            background-color: $color-black;
        }
    }

    &--brag {
        top: 36px;
    }

    &--shop {
        justify-content: flex-start;
        align-items: center;
    }

    .filter-open & {
        transform: translate3d(0, 0, -1px);
    }
}

.header__navigation:not(.header__navigation--mobile) {
    @include bp(sm-max) {
        display: none;
    }
}

.header__navigation--mobile {
    @include bp(md-min) {
        display: none;
    }
}

.header__logo {
    display: block;
    flex: 0 0 52px;

    &--new {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
    }

    .header:not(.header--shop) & {
        @include bp(md-min) {
            position: relative;
            margin: 0;
            top: 0;
            left: 0;
            right: 0;
            transform: none;
        }
    }
}

.header__logo:not(.header__logo--new) {
    display: block;
    flex: 0 0 52px;

    .header:not(.header--shop) & {
        @include bp(sm-min) {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            margin: auto;
        }

        @include bp(md-min) {
            position: relative;
            margin: 0;
            transform: none;
        }
    }
}

a {
    .header__logo--new & {
        display: inline-block;
        width: 100%;
        height: 100%;
    }
}

img {
    .header__logo--new & {
        position: absolute;
        width: 32px;
        height: 32px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.header__toggle {
    position: relative;
    width: 36px;
    height: 34px;
    padding: 0;
    margin: auto auto auto 13px;

    background: none;
    border: 0;
    outline: 0;
    cursor: pointer;
    z-index: 3;

    @include bp(md-min) {
        display: none;
    }

    &:before {
        content: '';
        position: absolute;
        top: 10px;
        left: 0;

        display: block;
        width: 16px;
        height: 2px;
        background-color: $color-red-brand;
        transition: transform 300ms ease;

        .h-menu-open & {
            height: 3px;
            transform: rotate(45deg);
            border-radius: 3px;
            top: 50%;
            width: 23px;
            margin-top: 0;
        }
    }

    &:after {
        content: '';
        position: absolute;
        top: 22px;
        left: 0;

        display: block;
        width: 16px;
        height: 2px;
        background-color: $color-red-brand;
        transition: transform 300ms ease;

        .h-menu-open & {
            height: 3px;
            transform: rotate(-45deg);
            border-radius: 3px;
            width: 23px;
            top: 50%;
            margin-top: 0;
        }
    }

    body:not(.h-menu-open) .header--shop & {
        display: none;
    }

    .h-menu-open & {
        width: 52px;
        height: 52px;
        margin: auto auto auto 13px;
    }

    @media only screen and (min-width: 375px) {
        .h-menu-open & {
            margin: auto auto auto 18px;
        }
    }

    @media only screen and (min-width: 414px) {
        .h-menu-open & {
            margin: auto auto auto 20px;
        }
    }
}

.header__toggle-inner {
    position: relative;
    display: block;
    width: 16px;
    height: 10px;

    &:before {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;

        display: block;
        width: 50%;
        height: 2px;
        background-color: $color-red-brand;
    }

    &:after {
        content: '';
        position: absolute;
        bottom: 2px;
        left: 0;

        display: block;
        width: 70%;
        height: 2px;
        background-color: $color-red-brand;
    }

    .h-menu-open & {
        display: none;
    }
}

.header__inner {
    position: fixed;
    top: 0;
    left: 0;
    width: 85%;
    overflow: hidden;
    transition: all 350ms ease-in-out;

    @include bp(sm-min) {
        width: 320px;
    }

    @include bp(sm-max) {
        height: 100vh;
        overflow: auto;
        text-align: right;
        transform: translate3d(-120%, 0, 0);
        background-color: $color-white;
        -webkit-overflow-scrolling: touch;

    }

    @include bp(md-min) {
        position: relative;
        display: flex;
        width: 100%;
        overflow: visible;

    }

    &.is-submenu-open {
        @include bp(sm-max) {
            overflow: hidden;
        }
    }

    .h-menu-open & {
        @include bp(sm-max) {
            display: block;
            z-index: 2;
            transform: translate3d(-100%, 0, 0);
        }
    }

    .h-canvas--brag & {
        top: -36px;

        @include bp(md-min) {
            top: 0;
        }
    }
}

.header__gradient {
    content: '';
    height: 60px;
    position: sticky;
    z-index: 3;
    bottom: 0;
    left: 0;
    width: 100%;
    background: linear-gradient(358.97deg, #000000 -31.2%, rgba(0, 0, 0, 0) 71.03%);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    transform: translate3d(0,0,1px);

    @include bp(md-min) {
        display: none;
    }
}

.header__gradient-icon {
    color: $color-red-brand;
    font-size: 16px;
    display: inline-block;
    margin: auto 12px 10px;
}

.header__actions {
    flex-shrink: 0;

    .header--shop & {
        position: absolute;
        top: 0;
        right: 0;

        @include bp(sm-min) {
            right: 52px;
        }
    }
}

.header__actions-item--new:not(.is-hidden) {
    width: 44.67px;
}

.header__actions-item {
    display: block;
    float: left;
    width: 52px;
    height: 52px;

    &--login {
        position: relative;

        @include bp(sm-max) {
            width: 0;
            visibility: hidden;
        }
    }

    &--active {
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            width: 80%;
            height: 80%;
            transform: translateZ(1px);
            z-index: 3;
            border: 3px solid $color-white;
            pointer-events: none;
        }
    }

    .search-open &--login,
    .search-open &--phone {
        opacity: 0;
    }

    .search-open &--cart {
        @include bp(sm-max) {
            display: none;
        }
    }
}

.header__actions-button {
    padding: 0;
    width: 52px;
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;

    .header__actions-item--new:not(.is-hidden) & {
        width: 44.67px;
    }

    .button__inner {
        flex: 1 1 100%;
    }

    .header__actions-item--cart & {
        font-size: 14px;

        .button__icon {
            font-size: 24px;
            left: -2px;
        }
    }
}

.header__help-phone {
    @include bp(sm-min) {
        display: none;
    }

    .header__actions--new & {
        background-color: transparent;
    }
}

.header__actions-button--phone,
.header__actions-button--lang,
.header__help-phone {
    background-color: rgba($color-dark, .65);

    @include bp(sm-min) {
        font-size: 18px;
    }

    .header__actions--new &,
    &.color-black {
        background-color: transparent;
    }
}

.header__actions-button--location {
    @include bp(sm-max) {
        background-color: rgba($color-dark, .35);
    }
}

.header__actions-button--lang {
    @include bp(sm-max) {
        background-color: rgba($color-dark, .55);
    }
}

.header__actions-button--profile {
    background-color: rgba($color-dark, .75);

    .header__actions & {
        @include bp(sm-max) {
            display: none;
        }
    }
}

.header__actions-item-modal {
    position: absolute;
    top: 100%;
    right: 0;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    transform: translate3d(0, 25px, 0);
    @include transition((transform, opacity), $transition-duration-2, ease);

    .header .header__actions-item:hover > &,
    &.is-fixed,
    &.is-active {
        @include bp(md-min) {
            opacity: 1;
            visibility: visible;
            transform: translate3d(0, 0, 0);
        }
    }

    .header__actions-item--login & {
        @include bp(sm-max) {
            position: fixed;
            visibility: visible;
            opacity: 1;
            transform: translateX(-100%);
            left: 0;
            top: 0;
            right: auto;
            height: 100vh;
            z-index: 3;
            width: 85%;
            // transition: none;
            display: block;
            background-color: $color-white;
        }

        @include bp(md-min) {
            overflow: visible;
        }

        .h-canvas--brag & {
            @include bp(sm-max) {
                top: -36px;
            }
        }
    }

    .header__actions-item--cart & {
        @include bp(md-min) {
            overflow: visible;
        }
    }

    .header--shop .header__actions-item--login & {
        width: 85%;

        @include bp(sm-min) {
            height: auto;
            width: 342px;
        }
    }

    &.is-hidden {
        @include bp(sm-max) {
            // display: none;
            transform: translateX(120%);
            // opacity: 0;
            // visibility: hidden;
        }
    }
}

.header__help {
    @include bp(sm-min) {
        display: flex;
        align-items: center;
        margin-left: 24px;
    }

    @include bp(md-min) {
        margin-left: 46px;
    }
}

.header__steps {
    display: flex;
    margin-left: auto;
    align-items: center;
    counter-reset: section;
    top: 72px;
    position: absolute;
    justify-content: center;
    opacity: 1;
    @include transition(opacity);

    @include bp(xs-max) {
        width: 100%;
        left: 0;
        right: 0;
    }

    @include bp(sm-min) {
        display: flex;
        top: 0;
        position: static;
    }

    .header--filled & {
        @include bp(xs-max) {
            opacity: 0;
        }
    }
}

.header__steps-item,
.header__help-text {
    text-transform: uppercase;
    font-size: 10px;
    font-weight: $font-weight-medium;
    letter-spacing: 1px;

    @include bp(sm-min) {
        font-size: $font-size-tiny;
    }
}

.header__help-text {
    display: none;

    @include bp(sm-min) {
        display: inline-block;
    }
}

.header__steps-item {
    @include bp(sm-min) {
        padding: 0 8px;
    }

    @include bp(md-min) {
        padding: 0 26px;
    }

    &:first-child {
        padding-left: 0;
    }

    &:last-child {
        padding-right: 0;
    }
}

.header__steps-item::before {
    @include bp(sm-min) {
        counter-increment: section;
        content: counter(section)'. ';
    }
}

.header__help-icon {
    font-size: 16px;
    margin-right: 10px;
}

.header__steps-item--active {
    color: $color-red-brand;
    font-size: $font-size-tiny;
}

.header__steps-icon {
    font-size: 10px;
    transform: rotate(-90deg);
}

.header__steps-icon-xs {
    font-size: 24px;
}

.header__abort-button {
    margin-left: auto;
    display: flex;
    align-items: center;
    justify-content: center;

    @include bp(sm-min) {
        margin-left: 16px;
    }

    @include bp(md-min) {
        margin-left: 90px;
    }

    .button__icon {
        color: $color-white;
        font-size: 28px;
    }
}

.header__steps-xs {
    margin-right: 26px;
    display: flex;
    flex-direction: column;
    align-items: center;

    &:last-child {
        margin-right: 0;
    }
}

.header__notification {
    top: 52px;
}

.header__brag-bar {
    position: fixed;
    top: 0;
    z-index: 10;
    transition-property: transform;
    transition-duration: $transition-duration;
    transition-timing-function: $transition-easing;
}

.header__actions-list {
    .header__inner & {
        display: inline-block;
        position: relative;
        z-index: 1;

        @include bp(md-min) {
            display: none;
        }
    }
}
