.review-slider {
    display: block;
    width: 100%;
    background-color: $color-gray-bg;
    padding: 32px 0 56px 0;

    .editor &,
    &--inverse {
        background-color: $color-white;

        .review {
            background-color: $color-gray-bg;
        }
    }

    .editor & {
        .h-container {
            padding: 0;
        }
    }

    @include bp(md-min) {
        padding: 75px 0 72px 0;
    }
}

.review-slider__title {
    font-size: $font-size-mmidlarge;

    @include bp(sm-min) {
        font-size: $font-size-mxlarge;
    }
}
