.blogi-signup-bar {
    display: none;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 20;
    transform: translate3d(0, 100%, 0);
    @include transition(transform, $transition-duration-2, $transition-easing);

    @include bp(md-min) {
        display: block;
    }

    &.is-visible {
        transform: translate3d(0, 0, 0);
    }
}

.blogi-signup-bar__content {
    width: 100%;
    height: 84px;
    padding-top: 16px;
    background-color: $color-gray-bg;
}

.blogi-signup-bar__grid {
    max-width: 1440px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.blogi-signup-bar__title {
    font-size: $font-size-tiny;
    font-weight: $font-weight-normal;
    color: $color-dark;
}

.blogi-signup-bar__icon-notification {
    font-size: 24px;
    color: $color-red-brand;
    margin: 10px;
}

.blogi-signup-bar__icon-close {
    font-size: 40px;
    color: $color-red-brand;
}

.blogi-signup-bar__icon-container {
    width: 46px;
    background-color: $color-gray-light;
    border-radius: 60px;
    margin-right: 16px;
}

.blogi-signup-bar__button {
    padding: 17px 53px 18px;
    margin-left: auto;
    margin-right: auto;
}

.blogi-signup-bar__close {
    position: absolute;
    right: -60px;
    margin: -20px auto auto;
    top: 50%;
}
