.blog-poster {
    position: relative;
    width: 100%;

    @include bp(sm-min) {
        height: 520px;
    }

    @include bp(md-min) {
        height: 660px;
    }

    @include bp(lg-min) {
        height: 660px;
    }
}

.blog-poster__container {
    height: 100%;
}

.blog-poster__inner {
    display: flex;
    align-items: flex-end;
    height: 100%;
}

.blog-poster__content {
    color: $color-white;
    width: 100%;
    position: relative;
    display: block;

    @include bp(sm-min) {
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;
        flex-direction: row;
    }
}

.blog-poster__title {
    font-size: $font-size-xlarge;
    font-weight: $font-weight-medium;
    line-height: 36px;
    color: $color-dark;
    flex: 0 1 85%;
    margin-top: 26px;

    @include bp(sm-min) {
        padding-right: 40px;
        margin-top: 0;
        font-size: $font-size-xxlarge;
        line-height: 48px;
        color: $color-white;
        margin-bottom: 22px;
    }

    @include bp(md-min) {
        padding-right: 60px;
        padding-left: calc(100%/12*2);
        font-size: $font-size-xxlarge;
    }
}

.blog-poster__back {
    position: absolute;
    top: 16px;
    left: 16px;
}

.blog-poster__back-button {
    background-color: $color-dark;
    color: $color-brown-light;
    padding: 10px 30px 8px;
}

.button__icon {
    .blog-poster__back-button & {
        font-size: 10px;
        margin-right: 5px;
        top: -1px;
    }
}

.blog-poster__meta {
    flex: 0 1 15%;
    font-size: $font-size-tiny;
    line-height: 1.5;
    order: -1;
    color: $color-brown-light2;

    @include bp(sm-min) {
        order: initial;
        color: $color-white;
        margin-bottom: 22px;
    }
}

.blog-poster__meta-link,
.blog-poster__meta-cat a {
    color: $color-brown-light2;

    @include bp(sm-min) {
        color: $color-white;
    }
}

.blog-poster__meta-label {
    font-weight: $font-weight-bold;
}

.blog-poster__bg {
    @include bp(sm-min) {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: -1;

        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            height: 280px;
            background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,.1) 18%, #000000 100%);
        }
    }
}

.blog-poster__img {
    vertical-align: top;
    object-fit: cover;
    width: 100%;
    height: 100%;
    max-height: 350px;

    @include bp(sm-min) {
        max-height: 100%;
    }
}
