.card {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%;
    min-width: 285px;
    background-color: $color-white;

    &--testimonial,
    &--training {
        background-color: transparent;
        background-repeat: no-repeat;
        background-size: cover;
    }

    &--testimonial {
        width: calc(100vw - 50px);

        @include bp(sm-min) {
            width: calc(50vw - 50px);
        }

        @include bp(md-min) {
            width: 100%;
        }
    }

    &--training {
        @include bp(sm-min) {
            min-width: 360px;
        }
    }

    &:hover {
        text-decoration: none;
    }

    &.shop__summary {
        height: auto;
    }

    .checkout__column & {
        display: flex;

        @include bp(sm-min) {
            height: auto;
        }
    }
}

.card__background {
    display: block;
    width: 100%;
    height: 100%;

    &:before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: $color-red-brand;
        opacity: 0;
        z-index: 2;
        mix-blend-mode: multiply;
        @include transition(opacity mix-blend-mode, $transition-duration, $transition-easing);

        .card:hover & {
            opacity: .5;
        }
    }

    &:after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: $gradient-card;
        opacity: .7;
        z-index: 3;
    }

    .card--testimonial & {
        &:after {
            right: -1px;
            left: -1px;
            width: auto;
        }
    }

    .card--category & {
        overflow: hidden;

        &:before {
            display: none;
        }

        &:after {
            bottom: auto;
            height: 60%;
            opacity: 1;
            background: linear-gradient(180deg, $color-gray-bg 0%, rgba($color-gray-bg, 0) 100%);
        }
    }
}

.card__image {
    display: block;
    width: 100%;
    height: auto;

    .card--testimonial & {
        height: 100%;
        object-fit: cover;
    }
}

.card__content {
    position: relative;
    display: block;
    width: 100%;
    padding: 36px;
    z-index: 4;
    color: $color-dark;
    @include font-smoothing(true);

    .card--category & {
        position: absolute;
        top: 0;
        padding: 26px;
        text-align: center;
        color: $color-white;
    }

    .card--testimonial &,
    .card--training & {
        position: absolute;
        bottom: 0;
        left: 0;
        padding: 26px;
        color: $color-white;
    }

    .shop__summary & {
        padding: 26px 16px;

        @include bp(sm-min) {
            padding: 36px 68px;
        }

        @include bp(md-min) {
            padding: 36px;
        }
    }

    .card--order & {
        padding: 36px 0;

        @include bp(md-min) {
            padding: 36px 18px;
        }
    }

    .card--form & {
        padding: 36px 16px;

        @include bp(sm-min) {
            padding: 36px 26px;
        }

        @include bp(md-min) {
            padding: 36px;
        }
    }

    .checkout__column & {
        padding: 32px 16px;
        max-height: 100%;
        overflow: auto;

        @include bp(sm-min) {
            padding: 36px;
        }
    }
}

.card__title {
    font-size: $font-size-semi;
    font-weight: $font-weight-medium;

    .card--category & {
        display: inline-block;
        padding-bottom: 7px;
        font-size: $font-size-large;
        text-transform: uppercase;
        color: $color-brown;
        line-height: 1.1;
        border-bottom: 1px solid $color-brown-light;
    }

    .card--training & {
        text-transform: uppercase;
        letter-spacing: 4px;
    }

    .card--order & {
        padding-left: 16px;
        font-size: $font-size-medium;

        @include bp(sm-min) {
            padding-left: 26px;
        }
    }

    .card--form & {
        font-size: $font-size-small;
    }
}

.card__description {
    display: block;

    .card--category & {
        margin-top: 3px;
        font-size: $font-size-small;
        font-style: italic;
        line-height: 22px;
        color: $color-brown;
    }

    .card--training & {
        line-height: 22px;
        margin-bottom: 8px;
    }
}

.card__link {
    font-weight: $font-weight-bold;
    color: $color-red-brand;
}
