.editable {
    display: block;
    padding: 28px 37px 38px 37px;
    transition: $transition-duration $transition-easing-2;
    transition-property: color, background-color;
    cursor: pointer;

    &--compact {
        padding: 14px 37px;

        &.is-editing {
            padding: 14px 22px 14px 37px;
        }
    }

    &:hover {
        color: $color-red-brand;
        background-color: $color-gray-light3;
    }

    .card__content--padding-large & {
        margin-left: -36px;
        margin-right: -36px;
    }
}

.editable__value {
    position: relative;
    display: block;
    width: 100%;
    padding-right: 24px;

    .editable.is-editing & {
        display: none;
    }
}

.editable__value-title {
    display: block;
    font-size: $font-size-small;
    font-weight: $font-weight-bold;
    color: $color-brown-medium;
    transition: color $transition-duration $transition-easing-2;

    .editable:hover & {
        color: $color-red-brand;
    }
}

.editable__value-content {
    display: block;
    font-size: 14px;
    line-height: 20px;
}

.editable__value-status {
    display: block;
    font-size: 14px;
    line-height: 20px;
    color: $color-brown-medium;
    margin-top: 12px;
}

.editable__icon {
    position: absolute;
    top: 0;
    right: 0;
    color: $color-brown-medium;
    transition: color $transition-duration $transition-easing-2;

    .editable--compact & {
        top: 50%;
        margin-top: -8px;
    }

    .editable:hover & {
        color: $color-red-brand;
    }
}

.editable__edit {
    display: none;

    .editable.is-editing & {
        display: block;
    }

    .editable--compact.is-editing & {
        display: flex;
        justify-content: space-between;
    }
}

.editable__edit-top {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.editable__edit-title {
    font-weight: 700;
}

.editable__edit-content {
    margin-top: 20px;

    .editable--compact & {
        margin-top: 0;
    }
}

.editable__edit-buttons {
    display: flex;
    margin-left: 40px;
}

.editable__edit-button {
    display: inline-block;
    width: 36px;
    height: 36px;
    color: $color-white;

    & + .editable__edit-button {
        margin-left: 6px;
    }
}
