.form-textfield {
    position: relative;
    font-size: $font-size-small;
    width: 100%;
    max-width: 100%;

    &.is-disabled {
        color: $textfield-border;
    }

    &.is-focused {
        z-index: 2;
    }

    &.is-edit {
        cursor: pointer;
    }
}

.form-textfield__inner {
    position: relative;
    padding: 14px 0 0;

    .form-textfield--classic & {
        padding-top: 0;
    }
}

.form-textfield__input input,
.form-textfield__input {
    border: none;
    border-bottom: 2px solid $color-brown-light;
    border-radius: 0;
    display: block;
    line-height: 16px;
    font-size: 16px; // font-size should be at least 16px on mobile to prevent zoom-in on focus in iOS.
    margin: 0;
    height: 28px;
    width: 100%;
    background: none;
    text-align: left;
    color: inherit;
    padding-right: 20px;
    padding-left: 0;

    @include bp(sm-min) {
        font-size: $font-size-small;
    }

    &--center {
        text-align: center;
        padding-right: 0;
        padding-left: 10px;
    }

    .form-textfield--classic & {
        height: 52px;
        font-size: $font-size-tiny;
        border: none;
        background-color: $color-white;
        padding: 0 18px;
    }

    .form-textfield--giftcode.is-done & {
        border-color: $color-green;
    }

    .form-textfield.is-edit & {
        border: none;
    }

    .form-textfield.is-focused & {
        outline: none;
    }

    .form-textfield.is-invalid & {
        border-color: $textfield-error;
    }

    .form-textfield.is-disabled & {
        background-color: transparent;
        color: $textfield-border;
        -webkit-text-fill-color: $textfield-border;
        outline: none;
    }

    &::-ms-clear {
        display: none;
    }

    &:focus {
        outline: none;

        + .form-textfield__label {
            top: 0;
            font-weight: $font-weight-bold;
            color: $color-brown-medium;

            &:after {
                visibility: visible;
                opacity: 1;
            }
        }
    }
}

span.form-textfield__input {
    padding-top: 5px;
    border-color: transparent;
    height: auto;
}

.form-textfield__label {
    top: 0;
    bottom: 0;
    color: $textfield-default;
    font-size: $font-size-small;
    left: 0;
    right: 0;
    pointer-events: none;
    position: absolute;
    display: block;
    width: 100%;
    max-height: 42px;
    overflow: hidden;
    white-space: nowrap;
    text-align: left;
    text-overflow: ellipsis;
    @include font-smoothing(true);
    @include textfield-animation();

    .form-textfield--classic & {
        line-height: 54px;
        vertical-align: middle;
        font-size: $font-size-tiny;
        padding: 0 18px;
        letter-spacing: 1px;
    }

    .form-textfield.is-dirty & {
        color: $textfield-border;
    }

    .form-textfield--floating-label & {
        top: 16px;
        font-size: $font-size-small;
    }

    .form-textfield--floating-label.is-focused &,
    .form-textfield--floating-label.is-dirty & {
        color: $color-brown-medium;
        font-size: $font-size-small;
        line-height: $font-size-small;
        font-weight: $font-weight-bold;
        top: 0;
        visibility: visible;
    }

    .form-textfield--classic.is-focused &,
    .form-textfield--classic.is-dirty &,
    .form-textfield.is-done & {
        visibility: hidden;
        opacity: 0;
    }

    .form-textfield--floating-label.is-dirty & {
        visibility: hidden;
        opacity: 0;
    }

    .form-textfield.is-edit &,
    .form-textfield--floating-label.is-dirty.is-focused & {
        visibility: visible;
        opacity: 1;
    }

    .form-textfield--floating-label.is-invalid & {
        color: $textfield-error;
    }

    .form-textfield--floating-label.is-invalid.is-focused & {
        color: $textfield-error;
    }

    &:after {
        background-color: $color-brown-medium;
        bottom: 0;
        content: '';
        height: 3px;
        left: 0;
        opacity: 0;
        position: absolute;
        @include textfield-animation();
        visibility: hidden;
        width: 100%;

        .form-textfield:hover & {
            visibility: visible;
            opacity: 1;
        }

        .form-textfield--classic &,
        .form-textfield.is-edit & {
            display: none;
        }
    }

    .form-textfield.is-focused &:after {
        visibility: visible;
        opacity: 1;
    }

    .form-textfield--giftcode.is-done &:after {
        background-color: $color-green;
    }

    .form-textfield.is-invalid &:after {
        background-color: $textfield-error;
    }
}

.form-textfield__icon {
    position: absolute;
    top: 20px;
    right: 0;
    display: none;
    font-size: 16px;
    pointer-events: none;
    color: $color-gray-warm2;

    .form-textfield.is-valid & {
        display: inline-block;
    }

    .form-textfield.is-invalid & {
        color: $color-red;
    }

    .form-textfield--giftcode &,
    .form-textfield--giftcode.is-invalid & {
        color: $color-white;
        position: relative;
        display: inline-block;
        top: auto;
        right: auto;
        font-size: 24px;
    }
}

.form-textfield__edit-icon {
    margin: auto;
    font-size: 16px;
    color: $color-brown-light;
}

.form-textfield__close-icon {
    margin: auto;
    font-size: 22px;
    color: $color-dark;
    cursor: pointer;
}

.form-textfield__close-icon,
.form-textfield__edit-icon {
    .form-textfield--giftcode & {
        color: $color-white;
    }
}

.form-textfield__icon-container {
    position: absolute;
    bottom: 0;
    right: 0;
    opacity: 0;
    visibility: hidden;
    width: 36px;
    height: 36px;
    display: flex;

    .form-textfield--giftcode & {
        background-color: $color-gray-warm2;
        align-items: center;
        justify-content: center;
    }

    .form-textfield.is-done &,
    .form-textfield.is-edit &,
    .form-textfield--giftcode &,
    .form-textfield.is-dirty & {
        opacity: 1;
        visibility: visible;
    }

    .form-textfield--giftcode.is-done & {
        background-color: $color-green;
    }

    .form-textfield--giftcode.is-invalid & {
        background-color: $textfield-error;
    }
}

.form-textfield__message {
    padding-top: 5px;
}

.form-textfield__message-text {
    color: $textfield-error;
    letter-spacing: 1px;
    font-size: $font-size-tiny;
}

.form-textfield--special {
    display: inline-block;
    width: 72px;

    .form-textfield__inner {
        padding: 0;
    }

    .form-textfield__input input,
    .form-textfield__input {
        display: inline-block;
        border-bottom: 0;
        height: 72px;
        padding: 0;
        background-color: $color-white;
        font-weight: 500;
        -webkit-appearance: none;
        padding-right: 20px;

        &:after {
            display: none;
        }
    }
}
