.meeting {
    @include bp(sm-min) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    @include bp(md-min) {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .meeting-card {
        width: 100%;
        max-width: 720px;

        @include bp(md-min) {
            max-width: 961px;
        }
    }
}

.meeting__container {
    margin-bottom: 36px;
    margin-left: 16px;
    margin-right: 16px;

    @include bp(sm-min) {
        margin-left: 0;
        margin-right: 0;
        width: 534px;
        text-align: center;
        margin-bottom: 56px;
    }

    @include bp(md-min) {
        width: 667px;
        text-align: center;
    }
}

.meeting__title {
    font-size: $font-size-mmidlarge;
    text-transform: uppercase;
    font-weight: $font-weight-medium;
    color: $color-dark;
    margin: 0 0 16px 0;

    @include bp(sm-min) {
        font-size: 26px;
    }

    @include bp(md-min) {
        font-size: 26px;
    }
}

.meeting__subtitle {
    font-size: $font-size-base;
    font-weight: $font-weight-normal;
    color: $color-dark;
}
