.reel {
    max-width: 100%;
    margin: 30px;
    overflow: hidden;
}

.reel__list {
    display: none;
}

.reel ul {
    width: 100%;
    height: 100%;

    @include bp(xl-min) {
        width: 50%;
        margin: auto;
    }
}

.reel ul li img {
    display: block;
    width: 100%;
}
