$light-accordion-open-background: #fff7f2;

.accordion {
    &--transparent {
        @include bp(sm-min) {
            margin-top: -72px;
        }
    }

    .single-hero__mid-hero & {
        &:not(.single-hero__mid-hero) {
            background-color: $color-gray-new-dark;
        }
    }
}

.accordion__item {
    & + .accordion__item {
        margin-top: 10px;
    }

    .accordion--faq & {
        & + .accordion__item {
            margin-top: 32px;

            @include bp(sm-min) {
                margin-top: 40px;
            }
        }
    }

    .accordion--full &,
    .accordion--light & {
        margin: 0;
    }
}

.accordion__header {
    position: relative;
    display: flex;
    align-items: center;
    color: $color-gray-warm2;

    &:hover {
        text-decoration: none;
    }

    .accordion--simple &,
    .accordion--faq & {
        &:before {
            content: '';
            position: absolute;
            top: 50%;
            right: 0;

            display: inline-block;
            width: 16px;
            height: 3px;
            margin-top: -2px;

            background-color: $color-dark;
        }

        &:after {
            content: '';
            position: absolute;
            top: 50%;
            right: 7px;

            display: inline-block;
            width: 3px;
            height: 16px;
            margin-top: -8px;

            background-color: $color-dark;
        }
    }

    .accordion__item.is-open & {
        &:after {
            display: none;
        }
    }

    .accordion--full & {
        justify-content: center;
        height: 72px;
        padding: 14px 0;
        color: $color-white;
        background-color: $color-dark;

        @include bp(md-min) {
            padding: 16px 0;
        }

        &:hover {
            color: $color-yellow;
        }
    }

    .accordion--full .accordion__item:nth-child(2) & {
        background-color: rgba($color-dark, .9);
    }

    .accordion--full .accordion__item:nth-child(3) & {
        background-color: rgba($color-dark, .8);
    }

    .accordion--full .accordion__item:nth-child(4) & {
        background-color: rgba($color-dark, .6);
    }

    .accordion--light .accordion__item:nth-child(odd) & {
        background-color: rgba($color-gray-bg, .23);
    }

    .accordion--light .accordion__item.is-open & {
        background-color: $light-accordion-open-background;
    }

    .accordion__item--second & {
        background-color: rgba($color-dark, .9);
    }

    .accordion--transparent & {

        @include bp(sm-min) {
            background-color: rgba($color-dark, .6);
        }
    }
}

.accordion__header-second {
    position: relative;
    display: flex;
    align-items: center;
    color: $color-gray-warm2;

    &:hover {
        text-decoration: none;
    }

    .accordion--simple &,
    .accordion--faq & {
        &:before {
            content: none;
        }

        &:after {
            content: none;
        }
    }

    .accordion__item.is-open & {
        &:after {
            display: none;
        }
    }

    .accordion--full & {
        justify-content: center;
        height: 72px;
        padding: 14px 0;
        color: $color-white;
        background-color: $color-dark;

        @include bp(md-min) {
            padding: 16px 0;
        }

        &:hover {
            color: $color-yellow;
        }
    }

    .accordion--full .accordion__item:nth-child(2) & {
        background-color: rgba($color-dark, .8);
    }

    .accordion--full .accordion__item:nth-child(3) & {
        background-color: rgba($color-dark, .6);
    }

    .accordion--full .accordion__item:nth-child(4) & {
        background-color: rgba($color-dark, .4);
    }

    .accordion--light .accordion__item:nth-child(odd) & {
        background-color: rgba($color-gray-bg, .23);
    }

    .accordion--light .accordion__item.is-open:nth-child(odd) & {
        background-color: $light-accordion-open-background;
    }
}

.accordion__content {
    display: none;

    html.no-js &,
    .accordion__item.is-open > & {
        display: block;
    }
}

.accordion__inner {
    padding-left: 42px;

    &--no-padding {
        padding: 0;
    }

    .accordion--simple & {
        padding: 37px 0;
    }

    .accordion--faq & {
        max-width: 80%;
        padding: 24px 0;
        font-size: $font-size-medium;
        line-height: 32px;
    }

    .accordion--full & {
        padding: 0;
    }

    .accordion--content & {
        padding-top: 96px;

        @include bp(sm-min) {
            padding: 96px 90px 0;
        }

        @include bp(md-min) {
            padding: 96px 0 0;
        }
    }

    .accordion--meeting & {
        padding-top: 40px;

        @include bp(sm-min) {
            padding: 96px 26px 0;
        }

        @include bp(md-min) {
            padding: 96px 0 0;
        }
    }

    .accordion--padded & {
        padding: 36px 0;

        @include bp(sm-min) {
            padding: 96px 0;
        }
    }

    .accordion--light & {
        .card--order & {
            padding: 0 16px;

            @include bp(sm-min) {
                padding: 0 56px;
            }
        }
    }
}

.accordion__title {
    position: relative;
    display: block;
    width: 100%;
    padding: 3px 0 1px 42px;
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    font-style: italic;
    color: $color-gray-warm2;

    .accordion--simple & {
        padding: 0;
        font-size: $font-size-medium;
        color: $color-dark;
    }

    .accordion--faq & {
        max-width: 95%;
        padding: 0;
        font-size: $font-size-medium;
        font-weight: 700;
        font-style: normal;
        line-height: 32px;
        text-transform: uppercase;
        color: $color-dark;

        @include bp(sm-min) {
            max-width: 80%;
            font-size: $font-size-xlarge;
            line-height: 46px;
        }
    }

    .accordion--full & {
        display: inline-block;
        width: auto;
        padding: 0 40px;
        text-align: center;
        color: inherit;
        font-style: normal;
        font-size: $font-size-small;
        line-height: 1.2;
    }

    .accordion--light & {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        padding: 13px 42px 13px 16px;
        font-style: normal;
        font-size: $font-size-base;
        font-weight: $font-weight-normal;
        color: $color-dark;

        @include bp(sm-min) {
            padding: 13px 62px 13px 26px;
        }
    }

    .single-hero__mid-hero & {
        color: $color-gray-new;

        .accordion__icon--plus {
            color: $color-gray-new;
        }
    }

    .accordion--light .accordion__item.is-open & {
        font-weight: $font-weight-bold;
    }

    .package-terms & {
        font-style: normal;
    }
}

.accordion__title-manuals {
    font-style: normal;
}

.accordion__arrow {
    margin-left: 5px;

    .accordion--simple &,
    .accordion--faq & {
        position: absolute;
        right: 0;
    }
}

.accordion__order-date {
    display: inline-block;
    padding-right: 15px;
    font-size: $font-size-medium;
    font-weight: $font-weight-normal;
    line-height: 17px;
    flex-basis: 30%;

    @include bp(sm-min) {
        flex-basis: auto;
    }

    @include bp(md-min) {
        flex-basis: 9%;
    }
}

.accordion__order-year {
    display: block;
    font-size: $font-size-tiny;
}

.accordion__order-code {
    display: inline-block;
    font-size: $font-size-small;
    font-weight: $font-weight-bold;
    flex-basis: 70%;
    text-align: right;

    @include bp(sm-min) {
        flex-basis: 24%;
        padding: 0 15px;
        font-weight: $font-weight-normal;
        text-align: left;
    }

    @include bp(md-min) {
        flex-basis: 20%;
    }

    .accordion__item.is-open & {
        font-weight: $font-weight-bold;
    }
}

.accordion__order-code-sum {
    font-weight: $font-weight-normal;
}

.accordion__order-content {
    flex-basis: 100%;
    display: inline-block;
    font-size: $font-size-small;
    font-weight: $font-weight-normal;
    margin-top: 16px;

    @include bp(sm-min) {
        padding: 0 15px;
        flex-basis: 50;
        margin: 0;
    }

    @include bp(md-min) {
        flex-basis: 54%;
    }
}

.accordion__order-sum {
    flex-basis: 100%;
    display: none;
    padding-left: 15px;
    font-size: $font-size-small;
    font-weight: $font-weight-normal;

    @include bp(sm-min) {
        flex-basis: 16%;
        display: inline-block;
    }

    @include bp(md-min) {
        flex-basis: 16%;
    }

    .accordion__item.is-open & {
        font-weight: $font-weight-bold;
    }
}

.accordion__icon {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0 16px 0 0;
    font-size: 26px;
    color: $color-gray-warm2;
    pointer-events: none;
    @include transition(transform, $transition-duration, $transition-easing);

    .accordion--full & {
        position: absolute;
        top: 50%;
        right: 10px;
        left: auto;
        font-size: 13px;
        margin: 0;
        color: inherit;
        transform: translateY(-50%);
    }

    .accordion--light & {
        top: 50%;
        right: 12px;
        left: auto;
        margin: 0;
        font-size: 10px;
        transform: translateY(-50%);

        @include bp(sm-min) {
            right: 20px;
        }
    }

    .accordion--full .accordion__item.is-open &,
    .accordion--light .accordion__item.is-open & {
        transform: translateY(-50%) rotate(180deg);
    }
}

.accordion__icon--plus {
    display: inline-block;

    .accordion__item.is-open & {
        display: none;
    }
}

.accordion__icon--minus {
    display: none;

    .accordion__item.is-open & {
        display: inline-block;
    }
}

.accordion__inner-title {
    color: $color-brown-dark3;
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    margin-top: 59px;
    text-transform: uppercase;
}

.accordion__specs-container {
    .accordion--transparent & {
        padding: 24px;

        @include bp(xl-min) {
            max-width: 1154px;
            margin-left: auto;
            margin-right: auto;
        }
    }
}

.specs-item__value {

    @include bp(sm-min) {
        width: 50%;
        margin-left: 16px;
    }
}

.specs-item__title {
    color: #1d1c1b;
    font-size: 18px;
    font-weight: 700;
    margin-top: 24px;

    @include bp(sm-min) {
        width: 50%;
        font-size: inherit;
        font-weight: 300;
        max-width: 276.5px;
        margin-top: 0;
        margin-right: 83.5px;
    }

    @include bp(xl-min) {
        max-width: 276.5px;
        margin-right: 113.5px;
    }
}

.break {
    height: 25px;
    width: 100%;
    border-bottom: 1px $color-brown-dark3 solid;
    margin-bottom: 39px;
}

.specs-item {
    display: flex;
    flex-flow: column;
    text-align: center;
    margin-bottom: 40px;

    @include bp(sm-min) {
        width: 100%;
        flex-flow: row;
        text-align: left;
    }
}
