.article {
    position: relative;
    display: block;
    width: 100%;
    max-width: 380px;

    &--wide {
        max-width: 569px;
    }

    &--page {
        max-width: none;
        display: inline-block;
        width: auto;
    }

    &--featured {
        display: block;
        max-width: 100%;

        @include bp(sm-min) {
            display: flex;
        }
    }

    &:hover {
        text-decoration: none;
    }
}

.article__figure {
    display: block;
    width: 100%;
    height: auto;
    overflow: hidden;
    @include aspect-ratio(1, 1, 'article__image');

    .article--featured & {
        @include aspect-ratio(773, 352, 'article__image');
        width: auto;
        margin-left: -15px;
        margin-right: -15px;

        @include bp(sm-min) {
            width: 67%;
            margin-left: 0;
            margin-right: 0;
        }
    }

    .article--wide & {
        overflow: hidden;
        @include aspect-ratio(570, 352, 'article__image');
    }
}

.article__image {
    display: block;
    width: 100%;
    height: auto;
}

.article__content {
    margin-top: 14px;

    .article--wide & {
        margin-top: 22px;
    }

    .article--featured & {
        margin-top: 0;

        @include bp(sm-min) {
            width: 33%;
            margin-left: 15px;
        }
    }

    .article--page & {
        margin-top: 0;
    }
}

.article__title {
    font-size: $font-size-large;
    font-weight: $font-weight-medium;
    color: $color-dark;
    transition: color $transition-duration $transition-easing-2;

    .article:hover &,
    &:hover {
        color: $color-red-brand;
    }

    .article--featured & {
        margin-top: 25px;
        font-size: $font-size-xxmidlarge;
        font-weight: $font-weight-medium;
        line-height: 42px;

        @include bp(sm-min) {
            margin-top: 0;
            font-size: $font-size-large;
            line-height: 30px;
        }

        @include bp(md-min) {
            font-size: $font-size-xxlarge;
            line-height: 48px;
        }
    }

    .article--page & {
        font-size: 14px;
        text-transform: uppercase;
    }
}

.article__date {
    font-weight: $font-weight-medium;
    font-size: $font-size-tiny;
    color: $color-red-brand;

    .article--page & {
        display: none;
    }
}

.article__description {
    display: block;
    margin-top: 8px;
    color: $color-dark;
}
