$general-section-background: #544244;

.contact {
    @include font-smoothing(true);
    display: block;
    width: 100%;

    @include bp(sm-min) {
        display: flex;
    }

    a {
        @include bp(sm-min) {
            color: inherit;
        }
    }
}

.contact__section {
    display: block;
    width: 100%;
    padding: 47px 16px;
    color: $color-white;
    background-size: cover;
    background-position: center;
    @include transition(background-color, $transition-duration-2, $transition-easing-2);

    @include bp(sm-min) {
        display: inline-block;
        width: 50%;
        padding: 91px 24px;
    }

    @include bp(md-min) {
        padding: 91px 56px;
    }

    &--general {
        @include bp(xs-max) {
            background-image: none !important;
        }

        @include bp(sm-min) {
            background-color: rgba($general-section-background, .8);
            background-blend-mode: multiply;
        }
    }

    &--locations {
        background-color: $color-red-brand;
        background-blend-mode: multiply;
    }

    &:hover {
        @include bp(sm-min) {
            &.contact__section--general {
                background-color: rgba($general-section-background, .6);
            }

            &.contact__section--locations {
                background-color: rgba($color-red-brand, .8);
            }
        }
    }
}

.contact__section-inner {
    display: block;
    width: 100%;
    max-width: 500px;
    margin: 0 auto;

    .contact__section--general & {
        margin-left: auto;
    }
}

.contact__section-title {
    font-size: 30px;
    font-weight: $font-weight-medium;
    line-height: 46px;
    color: $color-white;

    @include bp(sm-min) {
        font-size: 58px;
        font-weight: $font-weight-normal;
        line-height: 68px;
    }

    .contact__section--general & {
        color: $color-red-brand;

        @include bp(sm-min) {
            color: $color-white;
        }
    }
}

.contact__groups {
    margin-top: 36px;

    @include bp(sm-min) {
        margin-top: 26px;
    }

    @include bp(md-min) {
        margin-top: 56px;
    }
}

.contact__group {
    & + .contact__group {
        margin-top: 26px;
    }
}

.contact__group-title {
    font-size: 18px;
    font-weight: 500;
    line-height: 26px;
    letter-spacing: 1px;
    color: $color-dark;

    @include bp(sm-min) {
        color: $color-white;
    }

    @include bp(sm-min) {
        display: none;
    }

    .contact__group:first-child & {
        display: block;
    }
}

.contact__group-inner {
    display: flex;
    flex-direction: column;
    justify-content: column-reverse;
    margin-top: 16px;

    @include bp(sm-min) {
        display: block;
    }

    & + & {
        @include bp(sm-min) {
            margin-top: 0;
        }
    }
}

.contact__group-link-prepend {
    display: none;

    @include bp(sm-min) {
        display: inline-block;
    }
}

.contact__group-link {
    color: $color-red-brand;
    line-height: 1;

    @include bp(sm-min) {
        color: $color-white;
    }
}

.contact__group-link-icon {
    display: inline-block;
    margin-right: 15px;
    font-size: 22px;

    @include bp(sm-min) {
        display: none;
    }
}

.contact__navigation-button {
    display: block;
    margin-top: 36px;

    @include bp(sm-min) {
        display: none;
    }
}

.contact__links {
    display: none;
    margin-top: 36px;

    @include bp(sm-min) {
        display: block;
    }
}

.contact__link {
    color: $color-white;
    font-weight: 700;
}

.contact__locations {
    display: block;
    margin-top: 28px;

    @include bp(md-min) {
        display: flex;
        margin-top: 56px;
    }
}

.contact__location {
    display: none;

    @include bp(md-min) {
        display: inline-block;
        padding: 0 28px;
    }

    &:first-child {
        padding-left: 0;
    }

    &:last-child {
        padding-right: 0;
    }

    &.is-visible {
        display: block;
    }
}

.contact__locations-select {
    display: block;
    margin-top: 16px;
    color: $color-white;

    @include bp(md-min) {
        display: none;
    }
}

.form-textfield__input input {
    .contact__locations-select & {
        font-size: $font-size-medium;
        border-color: $color-white;
    }
}

.form-textfield__inner:after {
    .contact__locations-select & {
        border-color: $color-white;
    }
}

.select-dropdown.dropdown__options {
    .contact__locations-select & {
        background-color: $color-white;
        width: 100%;
        font-size: $font-size-medium;
        z-index: 3;
    }
}

.contact__location-title {
    font-size: 18px;
    font-weight: $font-weight-medium;
    line-height: 26px;
    letter-spacing: 1px;
    display: none;

    @include bp(md-min) {
        display: block;
    }
}

.contact__location-map {
    position: relative;
    display: block;
    max-width: 288px;
    height: auto;
    padding: 121px 30px 24px 30px;

    font-size: 14px;
    text-align: center;
    text-transform: uppercase;

    color: $color-white;
    background-size: cover;
    background-position: center;

    @include bp(md-min) {
        max-width: 179px;
        padding: 76px 10px 8px 10px;
    }

    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;

        display: block;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba($color-black, 0) 0%, $color-black 100%);
    }

    &:hover {
        text-decoration: none;
        color: $color-red-brand;
    }
}

.contact__location-map-inner {
    position: relative;
    display: block;
    z-index: 2;
    letter-spacing: 3px;
}
