.profile-box {
    position: relative;
    display: block;
    background-color: $color-white;

    .header__actions-item--login & {
        height: 100%;
        transform: translateX(0);
        transition: transform 350ms ease;

        @include bp(md-min) {
            height: auto;
        }

        &:before {
            @include bp(md-min) {
                content: '';
                position: absolute;
                bottom: 100%;
                right: 18px;
                z-index: 22;
                width: 0;
                height: 0;
                border-style: solid;
                border-color: transparent;
                border-top: 0;
                border-bottom-color: $color-red-brand;
                border-width: 9px;
            }
        }
    }

    // .header__actions-item-modal.is-hidden & {
    //     @include bp(sm-max) {
    //         transform: translateX(100%);
    //     }
    // }

    @include bp(md-min) {
        width: 342px;
        background-color: $color-gray-light3;
    }
}

.profile-box__head {
    display: flex;
    justify-content: space-between;
    padding: 21px;

    background-color: $color-red-brand;
    clip-path: polygon(0 0, 0 95%, 35% 100%, 100% 95%, 100% 0);
}

.profile-box__inner {
    &--request {
        @include bp(md-min) {
            margin-bottom: -36px;
        }
    }

    &--requested {
        margin-top: -20px;
    }
}

.profile-box__head-link {
    display: block;
    width: 100%;

    font-size: $font-size-small;
    font-weight: $font-weight-medium;
    text-transform: uppercase;
    text-align: center;

    color: $color-white;

    &:hover,
    &.is-current {
        text-decoration: none;
        color: $color-yellow;
    }
}

.profile-box__content {
    display: block;
    padding: 46px 26px 66px 26px;

    &--center {
        text-align: center;
    }

    .profile-box--padding-large & {
        padding: 46px 26px 66px 26px;
    }
}

.profile-box__icon {
    display: inline-block;
    margin-bottom: 20px;
    font-size: 48px;
    color: $color-red-brand;
}

.profile-box__title {
    font-size: 14px;
    text-transform: uppercase;
}

.profile-box__description {
    display: block;
    margin: 10px 0 20px 0;
    font-size: $font-size-small;
    line-height: 22px;
}

.profile-box__input {
    margin-top: 19px;

    &:first-child {
        margin-top: 0;
    }
}

.profile-box__button {
    margin-top: 45px;
}

.profile-box__link {
    display: block;
    margin-top: 16px;
    text-align: center;

    .profile-box__inner--request & {
        text-align: left;

        .button__icon {
            font-size: 10px;
            top: -1px;
            margin-right: 3px;
        }
    }
}

.profile-box__back {
    position: absolute;
    bottom: 26px;
    left: 26px;
}

.profile-box__error {
    padding-top: 10px;
    line-height: 1.25;
    font-size: 14px;
}

.profile-box__recaptcha-label {
    font-size: 12px;
}
