@font-face {
    font-family: 'GothamPro';
    src: url('../fonts/GothamPro.woff2') format('woff2'),
        url('../fonts/GothamPro.woff') format('woff');
    font-weight: 400;
    font-display: auto;
    font-style: normal;
}

@font-face {
    font-family: 'GothamPro';
    src: url('../fonts/GothamPro-Italic.woff2') format('woff2'),
        url('../fonts/GothamPro-Italic.woff') format('woff');
    font-weight: 400;
    font-display: auto;
    font-style: italic;
}

@font-face {
    font-family: 'GothamPro';
    src: url('../fonts/GothamPro-Medium.woff2') format('woff2'),
        url('../fonts/GothamPro-Medium.woff') format('woff');
    font-weight: 500;
    font-display: auto;
    font-style: normal;
}

@font-face {
    font-family: 'GothamPro';
    src: url('../fonts/GothamPro-MediumItalic.woff2') format('woff2'),
        url('../fonts/GothamPro-MediumItalic.woff') format('woff');
    font-weight: 500;
    font-display: auto;
    font-style: italic;
}

@font-face {
    font-family: 'GothamPro';
    src: url('../fonts/GothamPro-Bold.woff2') format('woff2'),
        url('../fonts/GothamPro-Bold.woff') format('woff');
    font-weight: 700;
    font-display: auto;
    font-style: normal;
}

@font-face {
    font-family: 'GothamPro';
    src: url('../fonts/GothamPro-BoldItalic.woff2') format('woff2'),
        url('../fonts/GothamPro-BoldItalic.woff') format('woff');
    font-weight: 700;
    font-display: auto;
    font-style: italic;
}

.text {
    > * + * {
        margin-top: 1.6em;
    }

    &--narrow > * + * {
        margin-top: 1em;
    }
}

h1,
.h1 {
    font-size: $font-size-xxlarge;
    font-weight: $font-weight-medium;
}

h2,
.h2 {
    font-size: $font-size-xlarge;
    font-weight: $font-weight-medium;
}

h3,
.h3 {
    font-size: $font-size-large;
    font-weight: $font-weight-medium;
}

.h3--alt {
    font-size: $font-size-mxlarge;
    text-transform: uppercase;
    line-height: 1.4;
}

h4,
.h4 {
    font-size: $font-size-medium;
    font-weight: $font-weight-medium;
}

h5,
.h5 {
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    color: $color-gray-warm2;
}

i,
em,
.italic {
    font-style: italic;
}

b,
strong,
.bold {
    font-weight: $font-weight-bold;
}

a,
.link {
    color: #ff0000;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}

/* Text sizes */

.text-tiny {
    font-size: $font-size-tiny;
}

.text-medium > *:not(.list) {
    font-size: $font-size-base;

    @include bp(sm-min) {
        font-size: $font-size-medium;
    }
}

/* Text transform */

.text-uppercase {
    text-transform: uppercase;
}

/* Text align */

.text-centered {
    text-align: center;
}

/* Text colors */

.text-red {
    color: $color-red-brand;
}

hr {
    margin: 36px 0;
}

blockquote {
    position: relative;
    font-size: inherit;
    line-height: inherit;
    text-align: left;
    margin: 0 auto;
    padding: 1em 0 1em 45px;
    color: $color-red-brand;
    quotes: '\201C''\201D';

    @include bp(sm-min) {
        padding: 1em 0 1em 70px;
        font-weight: 500;
    }

    p {
        color: inherit;
    }

    &:after {
        display: block;
        position: absolute;
        left: 0;
        top: 44px;
        content: '\201D';
        color: $color-red-brand;
        font-size: 4em;
        height: 20px;
        width: 100%;
        text-align: left;
        line-height: 0;
        font-style: normal;
        font-weight: 700;
        font-family: serif;

        @include bp(sm-min) {
            font-size: 5em;
            top: 55px;
        }
    }
}
