$separator-margin-default: 23px;
$separator-margin-small: 13px;
$separator-margin-medium: 33px;

hr {
    @extend .separator;
}

.separator {
    padding: $separator-margin-default 0;
    border: 0;
    margin: 0;

    &--small {
        padding: $separator-margin-small 0;
    }

    &--medium {
        padding: $separator-margin-medium 0;
    }

    &--section {
        padding: 58px 0;

        @include bp(sm-min) {
            padding: 78px 0;
        }
    }
}
