.loan__title {
    margin-top: 48px;
    font-size: $font-size-xlarge;
    font-weight: $font-weight-medium;
    margin-bottom: 46px;

    @include bp(sm-min) {
        font-size: $font-size-xxlarge;
        margin-top: 56px;
    }
}

.loan__subtitle {
    margin-top: 28px;
    margin-bottom: 56px;
    font-size: $font-size-base;

    @include bp(sm-min) {
        margin-top: 43px;
        margin-bottom: 36px;
        font-size: $font-size-medium;
    }

    @include bp(md-min) {
        margin-top: 36px;
    }
}

.loan__list-title {
    font-size: $font-size-large;
    font-weight: $font-weight-bold;
    margin-bottom: 16px;

    @include bp(sm-min) {
        font-size: $font-size-xlarge;
    }
}

.loan__list-text {
    line-height: 28px;
}

.loan__example {
    font-size: $font-size-tiny;
    margin-top: 16px;

    &-first {
        margin-bottom: 16px;
    }
}

.loan__subinfo {
    font-size: $font-size-base;
    margin: 36px 0 156px;
    font-style: italic;

    @include bp(sm-min) {
        font-size: $font-size-medium;
    }
}

.loan__image {
    width: 100%;
}

.loan__container {
    @include bp(sm-min) {
        max-width: 588px !important;
    }
    @include bp(md-min) {
        max-width: 783px !important;
    }
}

