.product-grid {
    display: block;
    width: 100%;
    margin: 0;

    &:after {
        content: '';
        background: $gradient-grid;
        width: 45px;
        height: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;

        @include bp(sm-min) {
            width: 115px;
        }

        @include bp(md-min) {
            display: none;
        }
    }
}

.product-grid__item {
    width: 80vw;
    padding: 0 16px;

    @include bp(sm-min) {
        width: auto;
    }
}
