.hero {
    position: relative;
    width: 100%;
    height: 520px;

    @include bp(sm-min) {
        height: 520px;
    }

    @include bp(md-min) {
        height: 660px;
    }

    @include bp(lg-min) {
        height: 660px;
    }

    &--front {
        text-align: center;
        height: auto;
        padding-top: 90px;
        padding-bottom: 190px;

        @include bp(sm-min) {
            padding: 0;
            height: 620px;
        }

        @include bp(md-min) {
            text-align: left;
        }

        @include bp(lg-min) {
            height: 660px;
        }
    }

    &--packages {
        height: auto;
        padding-top: 62px;
        padding-bottom: 82px;

        @include bp(sm-min) {
            height: 620px;
            padding: 0;
        }

        @include bp(md-min) {
            height: 660px;
        }
    }

    &--portrait {
        height: auto;
        padding-top: 62px;
        padding-bottom: 82px;

        @include bp(sm-min) {
            height: 620px;
            padding: 0;
        }

        @include bp(md-min) {
            height: 660px;
        }
    }
}

.hero__container {
    height: 100%;
}

.hero__inner {
    display: flex;
    align-items: center;
    height: 100%;
}

.hero__content {
    color: $color-white;
    width: 100%;
    position: relative;

    @include bp(md-min) {
        width: 60%;
    }

    .hero--front & {
        border: 5px solid $color-white;
        padding: 18px;
        display: inline-block;
        text-align: left;
        width: auto;
        margin-left: auto;
        margin-right: auto;

        @include bp(sm-min) {
            border: 8px solid $color-white;
            padding: 38px;
        }

        @include bp(md-min) {
            margin-left: 0;
            margin-right: 0;
        }
    }

    .hero--dark & {
        border-color: $color-dark;
        color: $color-dark;
    }

    .hero--packages & {
        @include bp(md-min) {
            flex: 0 1 45%;
        }
    }

    .hero--top-content & {
        @include bp(md-min) {
            margin-bottom: 70px;
        }
    }
}

.hero__title {
    font-size: $font-size-xxmidlarge;
    font-weight: $font-weight-medium;
    line-height: 46px;

    @include bp(sm-min) {
        font-size: $font-size-xxxlarge;
        font-weight: $font-weight-normal;
        line-height: 64px;
    }

    .hero--packages & {
        font-size: $font-size-xxmidlarge;
        line-height: 46px;
        margin-bottom: 16px;

        @include bp(sm-min) {
            max-width: 80%;
            font-size: $font-size-xxxlarge;
            line-height: 64px;
            margin-bottom: 0;
        }

        @include bp(md-min) {
            max-width: 100%;
        }
    }

    .hero--large-title & {
        font-size: 48px;
        line-height: 64px;
        word-wrap: break-word;

        @include bp(sm-min) {
            word-wrap: normal;
            font-size: $font-size-xxxlarge;
            font-weight: $font-weight-normal;
            line-height: 64px;
        }
    }

    .hero--portrait & {
        margin-bottom: 16px;

        @include bp(sm-min) {
            margin-bottom: 0;
        }
    }
}

.hero__description {
    font-weight: $font-weight-medium;

    @include bp(sm-min) {
        font-size: $font-size-small;
    }
}

.hero__button {
    padding: 28px 52px 26px;
    float: left;

    @include bp(sm-min) {
        margin-top: 36px;
    }

    &:not(:last-child) {
        margin-top: 36px;
        margin-bottom: 16px;

        @include bp(sm-min) {
            margin-right: 16px;
        }
    }

    .hero--portrait &,
    .hero--packages & {
        margin-top: 32px;
    }

    & + & {
        @include bp(xs-max) {
            margin-top: 0;
        }
    }
}

.hero__bubble {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    position: absolute;
    bottom: 5px;
    right: 15px;

    @include bp(sm-min) {
        display: none;
    }

    .hero--portrait & {
        bottom: 20px;
    }
}

.hero__bg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;

    .hero--front &,
    .hero--video & {
        display: block;
        width: 100%;
        overflow: hidden;
    }
}

.hero__picture {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;

    .hero--video & {
        @include bp(md-min) {
            display: none;
        }
    }
}

.hero__img,
.hero__video {
    vertical-align: top;
    width: 100%;
    height: 100%;
}

.hero__img {
    object-fit: cover;
}

.hero__video {
    position: absolute;
    width: 100vw;
    height: 56.25vw;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @include bp(sm-max) {
        display: none;
    }
}

.hero__nav {
    color: $color-white;

    .hero--dark & {
        color: $color-dark;
    }
}

.hero__cats {
    line-height: 1;
}

.hero__nav-item {
    line-height: 52px;

    .hero--front & {
        line-height: 48px;

        @include bp(sm-min) {
            line-height: 52px;
        }
    }
}

.hero__nav-icon {
    font-size: 21px;
    margin-top: -16px;
    margin-left: 17px;

    @include bp(sm-min) {
        font-size: 28px;
        margin-top: -20px;
        margin-left: 20px;
        display: inline-block;
    }
}

.hero__nav-link {
    font-size: 36px;
    color: $color-white;
    position: relative;
    @include font-smoothing(true);

    @include bp(sm-min) {
        font-size: 58px;
    }

    .hero--dark & {
        color: $color-dark;
    }

    .hero--front & {
        font-size: 32px;

        @include bp(sm-min) {
            font-size: 58px;
        }
    }

    &:after {
        content: attr(data-hover);
        position: absolute;
        color: $color-red;
        left: 0;
        width: 0;
        transition: width $transition-duration-2 $transition-easing-2;
        overflow: hidden;
        white-space: nowrap;
        border-bottom: 2px solid $color-red;
    }

    &:hover {
        text-decoration: none;
    }

    &:hover:after {
        width: 100%;
    }
}

.hero__cats-item {
    display: inline-block;

    &:not(:last-child) {
        &:after {
            content: '/';
            color: $color-white;
            font-size: 90%;

            .hero--dark & {
                color: $color-dark;
            }
        }
    }
}

.hero__cats-link {
    font-size: 12px;
    font-weight: $font-weight-medium;
    padding: 0 10px;
    color: $color-white;

    @include bp(sm-min) {
        font-size: 14px;
    }

    .hero--dark & {
        color: $color-dark;
    }

    .hero__cats-item:first-child & {
        padding-left: 0;
    }
}

.hero__packages {
    flex: 1 1 50%;
    display: none;
    height: 100%;
    justify-content: flex-end;

    @include bp(lg-min) {
        display: flex;
    }
}

.hero__package {
    flex: 0 1 auto;
    width: 130px;
    height: 100%;
    text-align: center;
    color: $color-brown-light;
    background-color: rgba($color-gray-dark, .2);
    position: relative;
    overflow: hidden;
    @include transition(width background-color color, $transition-duration-2, $transition-easing-2);

    &:first-child {
        background-color: rgba($color-gray-dark, .3);
    }

    &:nth-child(2) {
        background-color: rgba($color-gray-dark, .5);
    }

    &:nth-child(3) {
        background-color: rgba($color-gray-dark, .7);
    }

    &:nth-child(4) {
        background-color: rgba($color-gray-dark, .9);
    }

    &:hover {
        width: 205px;
        background-color: rgba($color-red-brand, .6);
        color: $color-white;
    }
}

.hero__package-title {
    font-size: $font-size-small;
    letter-spacing: 3px;
    text-transform: uppercase;
    line-height: 18px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform: translate3d(0, 78%, 0);
    max-width: 130px;
    margin: auto;
    @include transition(transform, $transition-duration-2, $transition-easing-2, $transition-delay);

    .hero__package:hover & {
        transform: translate3d(0, 30%, 0);
        transition-delay: 0s;
    }
}

.hero__package-nr {
    font-size: $font-size-xxxlarge;
    line-height: 1;
    letter-spacing: 0;
}

.hero__package-content {
    visibility: hidden;
    opacity: 0;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 65px;
    max-width: 160px;
    margin: auto;
    @include transition(visibility opacity, $transition-duration-2, $transition-easing-2, 0s);

    .hero__package:hover & {
        visibility: visible;
        opacity: 1;
        transition-delay: $transition-delay;
    }
}

.hero__package-description {
    font-size: $font-size-small;
    line-height: 22px;
    font-style: italic;
    margin-top: 56px;
    color: $color-white;
    @include font-smoothing(true);
}

.hero__package-pricing {
    font-size: 18px;
    font-weight: $font-weight-medium;
    line-height: 22px;
    color: $color-white;
    @include font-smoothing(true);
    margin-top: 16px;
}

.hero__package-btn {
    padding: 20px 32px 19px;
    font-size: $font-size-tiny;
    min-width: 160px;
    letter-spacing: 0;
    margin-top: 16px;
}

.hero__package-details {
    color: $color-white;
    font-style: italic;
    text-decoration: underline;
    display: block;
    margin-top: 16px;
    @include font-smoothing(true);
}

.package-details__title {
    color: $color-red-brand;
    font-size: $font-size-mlarge;
    letter-spacing: 1px;
    margin-top: 16px;
    margin-bottom: 26px;
    line-height: 1.2;
}

.package-details__content {
    font-size: $font-size-medium;
    font-weight: $font-weight-medium;
    margin-bottom: 16px;
}

.package-contents__header {
    font-weight: $font-weight-bold;
    font-size: $font-size-small;
}

.package-additions__title,
.package-contents__title {
    color: $color-red-brand;
    font-size: $font-size-mlarge;
    letter-spacing: 1px;
    margin-bottom: 32px;
    line-height: 1.2;
}

.package-contents__header,
.package-contents__list-item {
    display: flex;
    font-size: $font-size-small;
}

.package-contents__list-item {
    padding: 4px 0;
}

.package-contents__product-figure {
    flex: 1 1 50%;
    display: flex;
    align-items: center;
    font-weight: $font-weight-medium;
}

.package-contents__product-image {
    margin-right: 16px;
}

.package-contents__product-amount {
    flex: 1 1 15%;
    display: flex;
    align-items: center;
}

.package-contents__product-price {
    flex: 1 1 35%;
    display: flex;
    align-items: center;
}

.package-details {
    margin-bottom: 36px;
}

.package-additions,
.package-terms {
    margin-top: 56px;
}

.hero__hidden-title {
    opacity: 0;
    visibility: hidden;
    height: 0;
    width: 0;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
}
