// https://github.com/Dogfalo/materialize/blob/master/sass/components/forms/_select.scss

/* stylelint-disable */
select {
    display: none;
    background-color: $select-background;
    width: 100%;
    border: $select-border;
    border-radius: $select-radius;

    &[data-default] {
        display: block;
    }
}

.select-label {
    position: absolute;
}

.select-wrapper {
    position: relative;

    span.caret {
        display: none;
    }
}

.select-dropdown {
    &.dropdown__options {
        display: none;
    }
}

// Disabled styles
.select-wrapper input.select-dropdown:disabled {
    color: rgba(0,0,0,.3);
    cursor: default;
    -webkit-user-select: none; /* webkit (safari, chrome) browsers */
    -moz-user-select: none; /* mozilla browsers */
    -ms-user-select: none; /* IE10+ */
}

.select-dropdown li.disabled,
.select-dropdown li.disabled > span,
.select-dropdown li.optgroup {
    background-color: transparent;
}

// Prefix Icons
.prefix ~ .select-wrapper {
    margin-left: 3rem;
    width: 92%;
    width: calc(100% - 3rem);
}

.prefix ~ label {
    margin-left: 3rem;
}

// Icons
.select-dropdown li {
    img {
        height: $dropdown-item-height - 10;
        width: $dropdown-item-height - 10;
        margin: 5px 15px;
        float: right;
    }
}

// Optgroup styles
.select-dropdown li.optgroup {
    // border-top: 1px solid $dropdown-hover-bg-color;

    &.selected > span {
        color: rgba(0, 0, 0, .7);
    }

    & > span {
        color: rgba(0, 0, 0, .4);
    }

    & ~ li.optgroup-option {
        padding-left: 1rem;
    }
}
/* stylelint-enable */
