ul:not(.list--icon) li,
ul:not(.list--icon) .list__item {
    .text > & {
        position: relative;
        padding-left: 20px;

        &:before {
            content: '';
            width: 8px;
            height: 8px;
            background-color: $color-red-brand;
            position: absolute;
            left: 0;
            top: 12px;
            bottom: auto;
            margin: auto;
        }
    }
}

ol li,
ol .list__item {
    .text > & {
        position: relative;
        padding-left: 20px;
        counter-increment: list;

        &:before {
            content: counter(list) '.';
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            margin: auto;
            font-weight: $font-weight-bold;
            color: $color-red-brand;
        }
    }
}

.list--icon {
    .list__item {
        position: relative;
        padding: 0 0 0 56px;
        font-weight: 500;
        font-style: italic;
        line-height: 1.5;
        min-height: 46px;
        display: flex;
        align-items: center;
        font-size: $font-size-small;

        @include bp(sm-min) {
            padding-left: 66px;
            font-size: $font-size-small;
        }

        & + .list__item {
            margin-top: 16px;
        }
    }
}

.list__icon {
    position: absolute;
    top: 50%;
    left: 0;

    display: inline-block;
    width: 46px;
    height: 46px;
    margin-top: -23px;

    text-align: center;
    line-height: 46px;

    background-color: $color-red-brand;
    border-radius: 100%;
}

.list__icon-svg {
    position: relative;
    font-size: 24px;
    color: $color-white;
}
