.quote {
    background-color: $color-red-brand;
    margin-bottom: -30px;
    padding: 44px 0;
    position: relative;
    color: $color-white;
    clip-path: polygon(0 0, 0 92%, 30% 100%, 100% 96%, 100% 0);
    z-index: 2;

    @include bp(sm-min) {
        padding: 56px 0 56px 0;
        clip-path: polygon(0 0, 0 85%, 30% 100%, 65% 92%, 100% 92%, 100% 0);
    }

    @include bp(md-min) {
        padding: 76px 0 56px 0;
        clip-path: polygon(0 0, 0 82%, 20% 100%, 40% 90%, 75% 88%, 100% 100%, 100% 0);
    }
}

.quote__quote {
    flex: 0 1 36px;
    margin-bottom: 18px;

    @include bp(sm-min) {
        margin-bottom: 0;
    }
}

.quote__icon {
    font-size: 36px;
}

.quote__grid {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
}

.quote__text {
    text-transform: uppercase;
    font-size: $font-size-small;
    font-weight: $font-weight-medium;
    @include font-smoothing(true);
    margin-right: auto;
    margin-bottom: 16px;
    line-height: 1.8;

    @include bp(sm-min) {
        flex: 1 1;
        line-height: 1.6;
        margin-bottom: 0;
        margin-left: 36px;
    }
}

.quote__cta {
    flex: 1 1 100%;

    @include bp(sm-min) {
        flex: 0 1 auto;
    }
}

.quote__button {
    padding-left: 0;
    padding-right: 0;
    color: $color-white;
    text-transform: uppercase;
    letter-spacing: 5px;
    font-weight: $font-weight-medium;

    @include bp(sm-min) {
        letter-spacing: 3px;
    }

    .button__inner:after {
        transform: translateY(0);
        opacity: 1;
        background-color: $color-white;
        bottom: -8px;
    }

    .button__icon {
        font-size: 17px;
        margin-left: 18px;
    }
}
