/* stylelint-disable */

.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-touch-callout: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;

    &.card-grid {
        margin-bottom: -1px; // fixed render error of 1 white pixel
    }
}

.slick-list {
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;
    width: 100%;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }

    .slider--dots &,
    .slider-section & {
        height: 100%;
    }
}

.slick-slider .slick-track,
.slick-slider .slick-list {
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: flex;

    &:before,
    &:after {
        content: '';
        display: table;
    }

    &:after {
        clear: both;
    }

    .product-grid & {
        padding-bottom: 1px;
    }

    .slick-loading & {
        visibility: hidden;
    }

    .slider--vertical & {
        align-items: center;
    }

    .slider--dots &,
    .slider-section & {
        height: 100%;
    }
}

.slick-slide {
    min-height: 1px;
    display: none;
    height: 100%;

    &:focus {
        outline: none;
    }

    &.dragging img {
        pointer-events: none;
    }

    .slick-initialized & {
        display: block;
    }

    .slick-initialized &.card--testimonial {
        @include bp(lg-min) {
            display: flex;
            height: auto;
        }
    }

    .slick-loading & {
        visibility: hidden;
    }
}

.slick-arrow.slick-hidden {
    display: none;
}

/* stylelint-enable */

/* Slider */

.slider {
    position: relative;
    padding: 0 58px;

    &--vertical {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        padding: 0;
    }

    &--related-blog,
    &--in-content {
        padding: 0;
    }

    &--in-content {
        margin-left: -16px;
        margin-right: -16px;

        @include bp(sm-min) {
            margin-left: 0;
            margin-right: 0;
        }
    }

    &--template,
    &--dots {
        height: 100%;
        padding: 0;
    }
}

h1 {
    .slide-copy & {
        line-height: 48px;
        margin-bottom: 16px;
        word-wrap: break-word;
    }
}

p {
    .slide-copy & {
        line-height: 26px;
        margin-bottom: 24px;
    }
}

a {
    .slide-copy & {
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 700;
    }
}

svg {
    .slide-copy & {
        margin-top: -3px;
        font-size: 16px;
    }
}

.slider__items {
    .slider--dots & {
        height: 100%;
    }
}

.slider__item {
    display: block;
    position: relative;
    width: 100%;
    opacity: 0;
    transition: opacity $transition-duration $transition-easing-2;

    .slide-copy {
        position: absolute;
        bottom: 72px;
        left: 50%;
        transform: translateX(-50%);
        text-align: center;
        color: $color-white;
        width: 100%;
        padding-left: 24px;
        padding-right: 24px;
    }

    &.slick-current {
        opacity: 1;
    }

    .product-poster & {
        text-align: center;
    }

    .slider-section & {
        height: 700px;
    }
}

.slider__link {
    .poroduct-poster & {
        display: inline-block;
    }
}

.slider__image {
    display: block;
    width: 100%;
    height: auto;

    .slider--vertical & {
        width: 100%;
        height: auto;
        max-width: 500px;
        max-height: 600px;
        margin: 0 auto;
    }

    .slider--dots & {
        min-height: 100%;
        width: 100%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        @include bp(sm-min) {
            width: auto;
            height: auto;
            min-width: 100%;
            min-height: 100%;
            left: 0;
            transform: translateY(-50%);
        }
    }

    .slider-section & {
        top: 0;
        left: 0;
        width: auto;
        height: 100%;
        transform: none;

        @include bp(sm-min) {
            min-width: 100%;
            height: auto;
            min-height: 100%;
        }
    }
}

/* Slider arrows */

.slider__arrow {
    position: absolute;
    top: 50%;
    display: inline-block;
    margin-top: -41px;
    padding: 0;
    border: 0;
    background: none;
    cursor: pointer;
    overflow: visible;
    z-index: 10;

    &:focus {
        outline: 0;
    }

    &--left {
        left: 0;
        transform: rotate(-180deg);

        @include bp(xl-min) {
            left: -82px;
        }

        .slider--dots & {
            left: 44.5px;

            @include bp(md-min) {
                left: 89px;
            }

            @include bp(xl-min) {
                left: 89px;
            }
        }
    }

    &--right {
        right: 0;

        @include bp(xl-min) {
            right: -82px;
        }

        .slider--dots & {
            right: 44.5px;

            @include bp(md-min) {
                right: 89px;
            }

            @include bp(xl-min) {
                right: 89px;
            }
        }
    }

    .slider--in-content & {
        display: none !important;

        @include bp(sm-min) {
            display: block !important;
        }
    }

    .slider--related-blog & {
        display: none;

        @include bp(md-min) {
            display: inline-block;
            top: calc(((100vw - 112px) / 3) - ((100vw - 112px) / 6));
        }

        @include bp(lg-min) {
            top: 180px;
        }
    }

    .card-grid &--right {
        right: 0;
    }

    .card-grid &--left {
        left: 0;
    }

    &.slick-disabled {
        display: none !important;
    }
}

.slider__arrow-icon {
    display: inline-block;
    // width: 21px;
    // height: 36px;
    font-size: 82px;

    color: $color-dark;
    z-index: 2;

    .card-grid & {
        color: $color-white;
    }

    .slider--related-blog & {
        color: $color-white;

        @include bp(xl-min) {
            color: $color-dark;
        }
    }

    .slider-section & {
        font-size: 62px;
        color: $color-white;
    }
}

/* Dots */

$slick-dot-color: $color-brown-light;
$slick-dot-color-active: $color-red-brand;
$slick-opacity-on-hover: 1 !default;
$slick-dot-character: '\2022' !default;
$slick-dot-size: 35px !default;
$slick-font-family: 'slick' !default;
$slick-opacity-not-active: .25 !default;
$slick-opacity-default: 1;

.slick-dots {
    position: absolute;
    bottom: 26px;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;

    li {
        position: relative;
        display: inline-block;
        height: 10px;
        width: 10px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;

        button {
            border: 0;
            background: transparent;
            display: block;
            height: 10px;
            width: 10px;
            outline: none;
            line-height: 0;
            font-size: 0;
            color: transparent;
            padding: 5px;
            cursor: pointer;

            &:hover,
            &:focus {
                outline: none;

                &:before {
                    opacity: $slick-opacity-on-hover;
                }
            }

            &:before {
                position: absolute;
                top: 0;
                left: 0;
                content: $slick-dot-character;
                width: 10px;
                height: 10px;
                font-family: $slick-font-family;
                font-size: $slick-dot-size;
                line-height: 20px;
                text-align: center;
                color: $slick-dot-color;
                opacity: $slick-opacity-not-active;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }
        }

        &.slick-active button:before {
            color: $slick-dot-color-active;
            opacity: $slick-opacity-default;
        }
    }

    .slider--template & {
        bottom: -18px;
    }
}

/* Thumbnails */

.slider__thumbnails {
    position: absolute;
    right: 0;
    bottom: 12px;
    left: 0;

    display: flex;
    justify-content: center;
    width: 100%;
    height: 52px;

    @include bp(sm-min) {
        height: 68px;
    }

    .slider--in-content & {
        position: static;
        margin-top: 18px;
        overflow-x: auto;
        display: block;
        white-space: nowrap;

        @include bp(sm-min) {
            position: absolute;
            margin: 0;
            display: flex;
        }
    }

    .slider--vertical & {
        top: 100%;
        bottom: auto;
        margin-top: 4px;
        margin-left: -2px;
        width: auto;
        overflow-y: hidden;
        overflow-x: auto;
        justify-content: flex-start;

        @include bp(md-min) {
            top: 0;
            right: auto;
            left: 0;
            margin: 0;
            padding: 0;
            justify-content: center;
            flex-direction: column;
            width: 68px;
            height: 100%;
        }
    }
}

.slider__thumbnail {
    display: inline-block;
    width: 52px;
    height: 52px;
    margin: 0 8px;
    overflow: hidden;
    cursor: pointer;

    @include bp(sm-min) {
        width: 68px;
        height: 68px;
    }

    .slider--in-content & {
        margin-left: 0;
        margin-right: 10px;

        &:first-child {
            margin-left: 16px;

            @include bp(sm-min) {
                margin-left: 0;
            }
        }

        @include bp(sm-min) {
            margin-left: 8px;
            margin-right: 8px;
        }
    }

    .slider--vertical & {
        margin: 0 2px;

        @include bp(md-min) {
            margin: 2px 0;
            width: 52px;
            height: 52px;
        }

        &:hover {
            box-shadow: 0 0 10px 0 rgba(0,0,0,.3);
        }

        &.is-active {
            box-shadow: 0 0 10px 0 rgba(0,0,0,.6);
        }
    }
}

.slider__thumbnail-image {
    display: block;
    width: 100%;
    height: auto;
}
