.button {
    display: inline-block;
    position: relative;
    padding: 28px 26px 26px;
    border: 0;
    border-radius: 0;
    letter-spacing: 2px;
    background: $color-red-brand;
    color: $color-white;
    text-align: center;
    text-transform: uppercase;
    font-weight: $font-weight-medium;
    font-size: $font-size-small;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    cursor: pointer;
    overflow: hidden;
    // transform: translateZ(1px);
    line-height: 1.3;
    @include font-smoothing(true);
    @include transition((color), $transition-duration, $transition-easing);

    @include bp(md-min) {
        min-width: 290px;
    }

    &:before {
        content: '';
        position: relative;
        display: none;
        width: 8px;
        height: 15px;
        border-right: 2px solid $color-white;
        border-bottom: 2px solid $color-white;
        z-index: 2;
        transform: rotate(45deg);
    }

    &:focus {
        outline: none;
    }

    &:hover {
        text-decoration: none;

        .button__inner:after {
            transform: translateY(0);
            opacity: 1;
        }

        &.button--naked-dark {
            color: $color-yellow;

            .button__inner:after {
                background-color: $color-yellow;
            }
        }
    }

    &.is-triggered {
        &:before {
            display: inline-block;
        }
    }

    &.button--green-success.is-triggered {
        background-color: $color-green;
    }
}

.button__inner {
    position: relative;
    z-index: 2;

    &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -6px;
        width: 100%;
        height: 1px;
        background-color: currentColor;
        transform: translateY(5px);
        opacity: 0;
        @include transition((transform, opacity, background-color), $transition-duration, $transition-easing);
    }

    .button.is-triggered & {
        display: none;
    }

    .button--text-dark & {
        color: $color-dark;
    }

    .button--product & {
        position: relative;
        opacity: 0;
        z-index: 2;

        &:after {
            display: none;
        }
    }
}

.button__icon {
    position: relative;
    display: inline-block;
    z-index: 2;

    .button--product & {
        position: absolute;
        top: 24px;
        right: 20px;

        width: 20px;
        height: 20px;

        color: $color-white;
        z-index: 4;
        transition: top $transition-duration $transition-easing-2;
    }
}

/* Variants */

.button--white {
    background-color: $color-white;
    color: $color-red-brand;

    .button__inner:after {
        background-color: $color-red-brand;
    }
}

.button--yellow {
    background-color: $color-yellow;
    color: $color-dark;

    .button__inner:after {
        background-color: $color-dark;
    }
}

.button--dark {
    background-color: $color-dark;
    color: $color-white;

    .button__inner:after {
        background-color: $color-white;
    }
}

.button--small {
    min-width: 200px;
    padding: 17px 16px 16px;
    font-size: $font-size-tiny;
}

.button--grid {
    margin-top: 26px;
    margin-left: -26px;
    margin-right: -26px;
}

.button--gray {
    color: $color-dark;
    background-color: $color-gray-light4;

    .button__inner:after {
        background-color: $color-dark;
    }
}

.button--naked {
    background: transparent;
    color: $color-red-brand;
    font-size: $font-size-small;
    padding: 0;
    min-width: 0;
    text-transform: none;
    letter-spacing: 0;
    box-shadow: none;
    overflow: visible;

    &.color-black {
        color: $color-black-1-2;
    }

    &.color-white {
        color: $color-white;
    }
}

.button--naked-gray {
    color: $color-gray-warm2;
    @include transition((color), $transition-duration, $transition-easing);

    &:hover {
        color: $color-red-brand;
    }
}

.button--naked-dark {
    color: $color-white;
    font-weight: $font-weight-bold;
    font-size: $font-size-base;

    .button__inner:after {
        background-color: $color-white;
        bottom: -7px;
    }
}

.button--icon-rect {
    padding: 0;
    font-size: $font-size-mmidlarge;
    min-width: 0;
    border-radius: 0;
    width: 52px;
    height: 52px;
    box-shadow: none;
    display: flex;
    align-items: center;
    justify-content: center;

    .button__inner {
        flex: 1 1 auto;
    }

    .button__inner:after {
        display: none;
    }

    .button__icon {
        display: block;
        margin: 0 auto;
    }
}

.button--block {
    width: 100%;
    display: block;
}

.button--block-xs {
    width: 100%;
    display: block;

    @include bp(sm-min) {
        display: inline-block;
        width: auto;
    }
}

.button--no-shadow {
    box-shadow: none;
}

.button--product {
    position: absolute;
    right: 0;
    bottom: 0;

    display: block;
    width: 80px;
    height: 53px;
    min-width: auto;

    border: 0;
    border-radius: 0;

    font-weight: $font-weight-medium;
    font-size: $font-size-tiny;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 2px;
    line-height: 53px;

    color: $color-white;
    background: transparent;

    box-shadow: none;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    cursor: pointer;
    transition: width $transition-duration $transition-easing-2;

    &:focus {
        border: 0;
        outline: 0;
    }

    &:before {
        content: '';
        position: absolute;
        top: 23px;
        right: 27px;

        display: none;
        width: 7px;
        height: 12px;
        border-right: 2px solid $color-white;
        border-bottom: 2px solid $color-white;
        z-index: 2;
        transform: rotate(45deg);
    }

    &:after {
        content: '';
        position: absolute;
        top: -30%;
        right: -10px;

        display: block;
        width: 80px;
        height: 130%;

        background: $color-red-brand;
        clip-path: polygon(0 100%, 50% 20%, 100% 100%, 0% 100%);
        transition: all $transition-duration $transition-easing-2;
    }

    &:not(.is-triggered):hover {
        width: 100%;
        padding: 0 84px;

        &:after {
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
        }

        .button__inner {
            opacity: 1;
        }
    }

    &:hover {
        .button__icon {
            opacity: 1;
            top: 18px;
        }
    }

    &.is-triggered {
        &:before {
            position: absolute;
            display: inline-block;
        }

        .button__icon {
            display: none;
        }
    }
}

.button--add {
    display: block;
    padding: 0;

    font-size: 14px;
    letter-spacing: .5px;
    text-align: left;
    color: $color-dark;

    background: transparent;
    box-shadow: none;
    overflow: visible;

    .button__icon {
        margin-right: 10px;
        color: $color-red-brand;
    }
}

.button--green {
    background-color: $color-green;
}

.button--text-dark {
    .button__inner:after {
        background-color: currentColor;
    }
}

.button__bubble {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    transform: translate(-50%, -50%) translateZ(0);
    width: 0;
    height: 0;
    border-radius: 150px;
    background: rgba($color-white, .5);
    pointer-events: none;

    .button--white & {
        background: rgba($color-red-brand, .5);
    }

    .button--naked & {
        background: transparent;
        display: none;
    }

    .is-transitioning & {
        width: 250px;
        height: 250px;
        opacity: 0;
        transition: $transition-duration-2 $transition-easing;
        transition-property: width, height, opacity;
    }
}
