.form-number {
    min-width: 126px;
    width: 100%;
    user-select: none;

    &--small {
        min-width: 72px;

        @include bp(sm-max) {
            min-width: 126px;
        }
    }
}

.form-textfield__input {
    .form-number & {
        flex: 0 0 42px;
        max-width: 42px;
        width: 100%;
        height: 72px;
        display: inline-block;
        padding: 0 8px;
        border: none;
        font-size: $font-size-medium;
        text-align: center;
        font-weight: $font-weight-medium;
        background-color: $color-white;
        -moz-appearance: textfield;

        &::-webkit-inner-spin-button,
        &::-webkit-outer-spin-button {
            appearance: none;
            -webkit-appearance: none;
            margin: 0;
        }
    }

    .form-number--small & {
        flex: 0 0 34px;
        max-width: 34px;
        height: 25px;
        padding: 0 2px;
        font-size: $font-size-small;

        @include bp(sm-max) {
            flex: 0 0 42px;
            max-width: 42px;
            height: 46px;
            padding: 0 8px;
            font-size: $font-size-medium;
        }
    }
}

.form-textfield__increment,
.form-textfield__decrement {
    .form-number & {
        flex: 0 0 42px;
        max-width: 42px;
        width: 100%;
        background-color: $color-gray-light5;
        color: $color-dark;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: $font-size-medium;
        cursor: pointer;
    }

    .form-number--small & {
        flex: 0 0 18px;
        max-width: 18px;
        height: 25px;

        @include bp(sm-max) {
            flex: 0 0 42px;
            max-width: 42px;
            height: 46px;
        }
    }
}

.form-textfield__inner {
    .form-number & {
        display: flex;
        text-align: center;
        padding-top: 0;
    }
}

.form-textfield__label {
    .form-number & {
        max-height: none;
    }
}

