.video {
    position: relative;
    width: 100%;
    max-width: 960px;
    height: auto;
    background: $color-black;

    &:before {
        content: '';
        display: block;
        width: 100%;
        padding-top: 56.25%;
    }

    .shop__secondary & {
        .shop--confirmation & {
            margin-top: 46px;

            @include bp(md-min) {
                margin-top: 0;
            }
        }
    }
}

.video__inner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
}

.video__logo {
    position: absolute;
    top: 14px;
    left: 10px;

    display: inline-block;
    z-index: 2;
    transition: opacity $transition-duration $transition-easing-2;

    @include bp(sm-min) {
        top: 22px;
        left: 19px;
    }

    @include bp(md-min) {
        top: 39px;
        left: 33px;
    }

    .video.is-playing & {
        opacity: .3;
    }
}

.video__logo-image {
    display: block;
    width: 20px;
    height: 20px;

    @include bp(sm-min) {
        width: 34px;
        height: 34px;
    }

    @include bp(md-min) {
        width: 58px;
        height: 58px;
    }
}

.video__btn {
    position: relative;
    border: 0;
    background: transparent;
    padding: 0;
    margin: 0;
    outline: none;
    color: $color-white;
    width: 100%;
    height: 100%;
    cursor: pointer;
    transition: color $transition-duration $transition-easing-2;

    &:hover {
        color: rgba($color-white, .8);
    }

    .video.is-playing & {
        display: none;
    }
}

.video__image {
    width: 100%;
}

.video__icon {
    font-size: 28px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @include bp(sm-min) {
        font-size: 48px;
    }

    @include bp(md-min) {
        font-size: 84px;
    }
}

.video__holder {
    display: none;
    width: 100%;
    height: 100%;

    .video.is-playing & {
        display: block;
    }
}

.video iframe,
.video__iframe {
    width: 100%;
    height: 100%;
}

/* Content video */

.video-container {
    position: relative;
    width: 100%;
    max-width: 960px;
    height: auto;
    background: $color-black;

    &:before {
        content: '';
        display: block;
        width: 100%;
        padding-top: 56.25%;
    }

    iframe {
        position: absolute;
        top: 0;

        display: block;
        width: 100%;
        height: 100%;
    }
}
