.get-in-contact__content {
    display: flex;
}

.get-in-contact__details {
    flex: 1 1 auto;
}

.get-in-contact__title {
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    color: $color-dark;
    text-transform: uppercase;
    margin-left: 16px;
    padding-top: 12px;
    margin-bottom: 3px;
    display: none;

    @include bp(sm-min) {
        display: block;
    }
    @include bp(md-min) {
        display: block;
    }
}

.get-in-contact__email {
    font-size: $font-size-tiny;
    font-weight: $font-weight-normal;
    color: $color-red;
    margin-left: 16px;
    display: none;

    @include bp(sm-min) {
        display: block;
    }
    @include bp(md-min) {
        display: block;
    }
}

.get-in-contact__telephone {
    font-size: $font-size-tiny;
    font-weight: $font-weight-normal;
    color: $color-dark;
    margin-left: 16px;
    margin-top: -5px;
    display: none;

    @include bp(sm-min) {
        display: block;
    }
    @include bp(md-min) {
        display: block;
    }
}

.get-in-contact__image {
    flex: 0 1 55px;

    @include bp(sm-min) {
        flex: 0 1 86px;
    }
}

.contact__img {
    border-radius: 50%;
}
