.popup {
    display: block;
    width: 100%;
}

.popup__image,
.popup__title {
    .popup.is-success & {
        display: none;
    }
}

.popup__title {
    display: block;
    padding: 0 16px 26px 16px;
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 4px;
    text-align: center;
    text-transform: uppercase;

    @include bp(md-min) {
        padding: 0 56px 66px 56px;
    }
}

/* Popup form */

.popup__form {
    padding: 0 16px 60px 16px;

    @include bp(md-min) {
        padding: 0 56px 66px 56px;
    }
}

.popup__form-content,
.popup__form-fields {
    .popup.is-success & {
        display: none;
    }
}

.popup__form-title {
    margin-bottom: 16px;
    font-size: 22px;
    letter-spacing: 1px;
    line-height: 1.3;
    text-transform: uppercase;
    text-align: center;
    color: $color-red;

    @include bp(md-min) {
        text-align: left;
    }
}

.popup__form-text {
    display: none;
    margin-bottom: 15px;

    @include bp(md-min) {
        display: block;
    }
}

.popup__form-success {
    display: none;
    text-align: center;

    .popup.is-success & {
        display: block;
    }
}

.popup__form-success-icon {
    font-size: 102px;
    color: $color-red-brand;
    margin: 40px 0 26px 0;
}

.popup__form-success-title {
    margin-bottom: 26px;
    font-size: 22px;
    letter-spacing: 1px;
    line-height: 1.3;
    text-transform: uppercase;
    text-align: center;
    color: $color-red;
}
