.featured-cats {
    padding: 0;
    position: relative;
    overflow: hidden;

    // @include bp(md-min) {
    //     margin-bottom: 110px;
    // }

    &:before {
        content: '';
        clip-path: polygon(0% 62%, 10% 72%, 43% 48%, 74% 63%, 100% 53%, 100% 100%, 0% 100%);
        height: 100px;
        display: none;
        background-color: $color-white;
        position: absolute;
        left: -1px;
        bottom: -5px;
        right: -2px;
        margin: auto;
        z-index: 2;

        @include bp(sm-min) {
            display: block;
        }
    }
}

.featured-cats__grid {
    margin-bottom: 0;
}

.featured-cats__card {
    background-color: $color-gray-bg;
    width: 100%;
    position: relative;
    min-height: 136px;
    overflow: hidden;
}

.featured-cats__bg {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 0;
}

.featured-cats__img {
    vertical-align: top;
    object-fit: cover;
    object-position: 50% 100%;
    width: 100%;
    height: 100%;

    .featured-cats__card & {
        @include transition(transform, $transition-duration, $transition-easing);
        transform-origin: bottom center;
        transform: scale(1);
    }

    .featured-cats__card--figure1 &,
    .featured-cats__card--figure6 & {
        @include bp(sm-min) {
            transform-origin: bottom left;
            object-position: 100% 0;
        }
    }

    .featured-cats__card:hover & {
        transform: scale(1.1);
    }
}

.featured-cats__card--figure1 {
    min-height: 200px;

    @include bp(sm-min) {
        min-height: 706px;
    }
}

.featured-cats__card--figure2 {
    @include bp(sm-min) {
        min-height: 256px;
    }
}

.featured-cats__card--figure4 {
    @include bp(sm-min) {
        min-height: 162px;
    }
}

.featured-cats__card--figure6 {
    @include bp(sm-min) {
        min-height: 244px;
    }
}

.featured-cats__cat-title {
    font-size: $font-size-large;
    font-weight: $font-weight-medium;
    color: $color-brown;
    letter-spacing: 1px;
    text-transform: uppercase;
    border-bottom: 1px solid rgba($color-brown-light2, .5);
    display: inline-block;
    transform: translateY(24px);
    line-height: 1;
    padding-bottom: 8px;
    transition: $transition-duration $transition-easing;
    transition-property: border-color, color;
    @include font-smoothing(true);

    @include bp(sm-min) {
        transform: translateY(84px);
    }

    .featured-cats__card--figure4 &,
    .featured-cats__card--figure5 & {
        @include bp(sm-min) {
            transform: translateY(58px);
        }
    }

    .featured-cats__card--figure6 & {
        @include bp(sm-min) {
            transform: translateY(96px) translateX(100%);
        }

        @include bp(md-min) {
            transform: translateY(96px) translateX(0);
        }
    }

    .featured-cats__card--figure1 &,
    .featured-cats__card--figure2 & {
        @include bp(md-min) {
            transform: translateY(84px) translateX(56px);
        }
    }

    .featured-cats__card--figure4 & {
        @include bp(md-min) {
            transform: translateY(64px) translateX(56px);
        }
    }

    .featured-cats__card:hover & {
        border-color: $color-red-brand;
        color: $color-red-brand;
    }
}

.featured-cats__link {
    display: block;
    width: 100%;
    height: 100%;
    z-index: 1;
    position: relative;
    text-align: center;

    .featured-cats__card--figure1 &,
    .featured-cats__card--figure2 &,
    .featured-cats__card--figure4 & {
        @include bp(md-min) {
            text-align: left;
        }
    }
}
