.training-header__content {
    @include bp(sm-min) {
        display: flex;
        flex-direction: row;
    }

    @include bp(md-min) {
        display: flex;
        flex-direction: row;
    }
}

.training-header__figure {
    flex: 1 1 100%;
}

.training-header__card {
    background-color: $color-dark;
    width: 100%;
    display: flex;
    flex-direction: column;
    @include transition(opacity, $transition-duration, $transition-easing);

    &.is-sticky {
        @include bp(lg-min) {
            position: fixed;
            top: 52px;
            margin-right: 0;
        }
    }

    &.is-hidden {
        @include bp(lg-min) {
            opacity: 0;
        }
    }

    @include bp(sm-min) {
        width: 162px;
        margin-right: -66px;
    }

    @include bp(md-min) {
        width: 198px;
        margin-right: -153px;
    }

    @include bp(lg-min) {
        margin-right: -198px;
    }
}

.training-header__title {
    font-size: $font-size-small;
    font-weight: $font-weight-normal;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: $color-white;
    margin-left: 22px;
    padding-top: 36px;
    margin-bottom: 16px;

    @include bp(sm-min) {
        padding-top: 14px;
        margin-bottom: 0;
        margin-left: 16px;
    }

    @include bp(md-min) {
        padding-top: 42px;
        margin-bottom: 37px;
        margin-left: 22px;
    }
}

.training-header__info {
    font-size: $font-size-small;
    font-weight: $font-weight-medium;
    color: $color-brown-light2;
    margin-left: 22px;

    @include bp(sm-min) {
        margin-bottom: -3px;
        margin-left: 16px;
    }

    @include bp(md-min) {
        margin-left: 22px;
    }
}

.training-header__subinfo {
    @include font-smoothing(true);
    font-size: $font-size-tiny;
    font-weight: $font-weight-normal;
    color: $color-white;
    margin-left: 22px;
    margin-bottom: 10px;

    @include bp(sm-min) {
        margin-bottom: 0;
        margin-left: 16px;
    }

    @include bp(md-min) {
        margin-bottom: 10px;
        margin-left: 22px;
    }
}

.training-header__info-box {
    margin-bottom: 46px;

    @include bp(sm-min) {
        margin-bottom: 16px;
    }

    @include bp(md-min) {
        margin-bottom: 0;
    }
}

.training-header__card__bottom {
    margin-top: auto;
}

.training-header__button {
    font-size: $font-size-small;
    padding: 17px 92px 17px;
    min-width: 160px;

    @include bp(sm-min) {
        padding: 16px 20px 16px;
    }

    @include bp(md-min) {
        padding: 18px 44px 18px;
    }
}

.training__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    vertical-align: top;
}
