$intro-container-max-width: 1440px;

.filter-section {
    display: block;
    width: 100%;
    background-color: $color-gray-bg;
}

.filter-section__inner {
    position: relative;
    display: block;
    width: 100%;
    max-width: $intro-container-max-width;
    margin: 0 auto;
    overflow: hidden;

    @include bp(md-min) {
        display: flex;
        align-items: center;
    }

    .filter-section--search & {
        align-items: flex-end;
        overflow: visible;
    }
}

.filter-section__content {
    position: relative;
    display: block;
    padding: 48px 16px 80px 16px;
    z-index: 2;

    @include bp(sm-min) {
        padding: 46px 24px 11px 24px;
    }

    @include bp(md-min) {
        min-width: 470px;
        padding: 70px 65px 70px 70px;
        max-width: 35%;
    }

    @include bp(lg-min) {
        max-width: 45%;
    }

    .filter-section--search & {
        @include bp(md-min) {
            min-width: 470px;
            padding: 70px 65px 70px 70px;
        }
    }
}

.filter-section__title {
    font-size: $font-size-mmidlarge;
    text-transform: uppercase;
    font-weight: 700;

    @include bp(sm-min) {
        font-size: $font-size-xlarge;
    }
}

.filter-section__text {
    display: block;
    margin-top: 5px;
}

.filter-section__trigger {
    position: absolute;
    right: 0;
    bottom: 0;
    display: inline-block;
    padding-left: 10px;

    @include bp(sm-min) {
        display: none;
    }

    &:focus {
        outline: 0;
    }
}

.filter-section__trigger-text {
    position: absolute;
    top: 0;
    left: 0;

    display: inline-block;
    width: 10px;
    height: 100%;
}

.filter-section__trigger-text-inner {
    display: inline-block;
    font-size: 10px;
    font-weight: $font-weight-bold;
    line-height: 10px;
    letter-spacing: 1px;
    text-transform: uppercase;

    transform: rotate(-90deg) translateX(-70%);
    transform-origin: left top;
}

.filter-section__trigger-box {
    display: inline-block;
    width: 52px;
    height: 52px;

    line-height: 52px;
    text-align: center;
    color: $color-white;
    background-color: $color-red-brand;
}

.filter-section__trigger-icon {
    font-size: 19px;
}

.filter-section__filter {
    display: none;
    width: 100%;

    @include bp(sm-min) {
        align-self: flex-end;
        display: block;
    }

    @include bp(md-min) {
        max-width: 65%;
    }

    @include bp(lg-min) {
        max-width: 55%;
    }

    .filter-open & {
        display: block;
        position: fixed;
        top: 0;
        bottom: 0;
        z-index: 20;
        background: rgba(#1d1c1b,.76);
        padding-top: 52px;
        padding-bottom: 56px;
        overflow: auto;
    }
}
