.review {
    position: relative;
    display: block;
    background-color: $color-white;
    overflow: hidden;

    & + .review {
        margin-top: 8px;
    }

    &:nth-child(odd) {
        margin-top: 26px;
        clip-path: polygon(0% 0%, 0% 96%, 34% 100%, 67% 96%, 100% 96%, 100% 0%);

        @include bp(sm-min) {
            clip-path: polygon(0% 0%, 0% 90%, 25% 100%, 50% 92%, 75% 100%, 100% 92%, 100% 0%);
        }
    }

    &:nth-child(even) {
        clip-path: polygon(0% 2%, 0% 100%, 100% 100%, 100% 2%, 67% 0%, 34% 4%);

        @include bp(sm-min) {
            clip-path: polygon(0% 0%, 0% 100%, 100% 100%, 100% 0%, 75% 8%, 50% 0%, 25% 8%);
        }
    }

    &.review--new {
        &:nth-child(odd) {
            margin-top: 26px;
            clip-path: polygon(0% 0%, 0% 94%, 34% 100%, 73% 94%, 100% 96%, 100% 0%);

            @include bp(sm-min) {
                clip-path: polygon(0% 0%, 0% 90%, 24% 100%, 53% 91%,81% 100% ,100% 89%, 100% 0%);
            }
        }

        &:nth-child(even) {
            clip-path: polygon(0% 4%, 0% 100%, 100% 100%, 100% 4%, 73% 0%, 34% 9%);
            @include bp(sm-min) {
                clip-path: polygon(0% 4%, 0% 100%, 100% 100%, 100% 4%, 78% 10%,53% 0%, 19% 10%);
            }
        }

    }

    &--product {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 0 16px;

        @include bp(sm-min) {
            padding: 0 97px;

            .editor & {
                padding: 0 16px;
            }
        }
    }
}

.rating__result {
    .review--new & {
        display: none;
    }
}

.review__number {
    position: absolute;
    right: -59px;
    bottom: -70px;

    display: none;
    font-size: 426px;
    font-weight: $font-weight-medium;
    line-height: 408px;
    color: $color-gray-light4;

    @include bp(sm-min) {
        display: inline-block;
    }

    @include bp(md-min) {
        right: 56px;
        bottom: -100px;
    }

    .review--new & {
        bottom: unset;
        top: -10px;
    }
}

.review__header {
    display: flex;
    align-items: center;

    @include bp(sm-min) {
        display: block;
    }
}

.review__image {
    max-width: 54px;
    flex-grow: 1;

    @include bp(sm-min) {
        max-width: 181px;
        flex-grow: 0;
    }

    img {
        max-width: 100%;
    }
}

.review__title {
    margin-bottom: 12px;
    margin-left: 16px;
    line-height: 1.6;

    @include bp(sm-min) {
        margin-left: 0;
    }
}

.review__price {
    font-size: $font-size-large;
    font-weight: $font-weight-bold;
    font-style: italic;
    line-height: 1;
}

.review__content {
    display: block;
    width: 100%;

    @include bp(sm-min) {
        max-width: 70%;

        .editor & {
            max-width: 100%;
        }
    }

    @include bp(md-min) {
        max-width: 60%;

        .editor & {
            max-width: 100%;
        }
    }

    .review:nth-child(odd) & {
        padding: 24px 16px 52px 16px;

        @include bp(sm-min) {
            padding: 36px 46px 66px 46px;
        }
    }

    .review:nth-child(even) & {
        padding: 52px 16px 36px 16px;

        @include bp(sm-min) {
            padding: 66px 46px 36px 46px;
        }
    }
}

.review__author {
    margin-top: 5px;
    font-weight: $font-weight-medium;
    color: $color-gray-warm2;

    .review--product & {
        display: block;
        align-items: center;
        font-size: $font-size-base;

        @include bp(md-min) {
            display: flex;
        }
    }
}

.review__author-name {
    color: $color-dark;

    .review--product & {
        @include bp(md-min) {
            margin-left: 14px;
        }
    }
}

.review__text {
    margin-top: 18px;
}

.review__question {
    margin-top: 21px;
    font-size: $font-size-midsmall;
    font-weight: $font-weight-bold;
    line-height: 16px;
    text-align: center;

    @include bp(sm-min) {
        text-align: left;
    }
}

.review__question-text {
    display: block;
    width: 100%;
    margin-bottom: 25px;

    @include bp(sm-min) {
        display: inline-block;
        width: auto;
        margin-bottom: 0;
    }
}

.review__question-rate {
    margin: 0 12px;
    color: $color-gray-warm2;
    transition: color $transition-duration $transition-easing-2;

    @include bp(sm-min) {
        margin: 0 0 0 25px;
    }

    &:hover {
        text-decoration: none;
        color: $color-red-brand;
    }
}

.review__question-rate-icon {
    font-size: $font-size-mxlarge;

    @include bp(sm-min) {
        font-size: $font-size-semi;
    }
}

.review__question-rate-count {
    font-size: $font-size-base;
}
