.social-media {
    position: relative;
    text-align: center;
    display: flex;
    flex-direction: column;
    margin-top: 56px;

    @include bp(sm-min) {
        margin-top: 0;
    }

    .section--blog-single & {
        @include bp(sm-min) {
            margin-right: 16px;
        }

        @include bp(md-min) {
            margin-right: 0;
        }
    }
}

.social-media__title {
    font-size: $font-size-small;
    letter-spacing: 1px;
    @include font-smoothing(true);
}

.social-media__logos {
    display: flex;
    flex-direction: row;
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    color: $color-dark;

    @include bp(sm-min) {
        flex-direction: column;
    }

    @include bp(md-min) {
        flex-direction: column;
    }
}

.social-media__content {
    font-size: $font-size-small;
    font-weight: $font-weight-bold;
    text-transform: uppercase;
    color: $color-brown;
    margin-bottom: 15px;
    display: none;

    @include bp(sm-min) {
        display: block;
    }

    @include bp(md-min) {
        display: block;
    }
}

.social-media__logo {
    @include bp(sm-min) {
        margin-bottom: 26px;
    }
}

.social-media__count {
    margin-right: 20px;
    padding: 4px 0;

    @include bp(sm-min) {
        margin-bottom: 15px;
        margin-right: 0;
    }

    @include bp(md-min) {
        margin-bottom: 15px;
        margin-right: 0;
    }
}

.social-media__icon {
    margin-left: 5px;
    margin-right: 5px;
    font-size: $font-size-xxmidlarge;

    @include bp(sm-min) {
        margin: auto;
    }

    @include bp(md-min) {
        margin: auto;
    }
}

.social-media__link {
    .social-media__logo--facebook & {
        .social-media__icon {
            color: #3b5998;
        }
    }

    .social-media__logo--twitter & {
        .social-media__icon {
            color: #4099ff;
        }
    }
}
