.bottom-navigation {
    position: fixed;
    bottom: 0;
    left: 0;

    display: block;
    width: 100%;
    height: 56px;
    text-align: center;
    background-color: $color-red-brand;
    z-index: 20;
    transform: translate3d(0,0,2px);
    transition: height $transition-duration, $transition-easing;

    @include bp(md-min) {
        display: none;
    }
}

.bottom-navigation__items {
    display: inline-block;
    margin: 0 auto;
}

.bottom-navigation__item {
    display: inline-block;
    padding: 6px 13px 10px;

    text-align: center;
    color: $color-white;

    @include bp(sm-min) {
        width: 120px;
        padding: 6px 0 10px;
    }

    &.is-active {
        background-color: #252525;
    }
}

.bottom-navigation__icon {
    display: inline-block;
    font-size: 30px;
    margin-bottom: 2px;
}

.bottom-navigation__text {
    display: block;
    width: 100%;
    font-size: 11px;
    line-height: 1;
}
