.cart {
    @include bp(md-min) {
        margin-bottom: 0;
    }

    &--popout {
        margin-bottom: 0;
        width: 100%;
        max-width: 615px;
        background-color: $color-gray-light5;
        position: relative;

        @include bp(sm-min) {
            width: 615px;
        }

        &:before {
            @include bp(md-min) {
                content: '';
                position: absolute;
                bottom: 100%;
                right: 18px;
                z-index: 22;
                width: 0;
                height: 0;
                border-style: solid;
                border-color: transparent;
                border-top: 0;
                border-bottom-color: $color-gray-light5;
                border-width: 9px;
            }
        }

    }
}

.cart__panel {
    .cart--popout & {
        margin-bottom: 0;
        position: relative;
        overflow: hidden;
        padding: 26px 36px 36px;
    }
}

.cart__header {
    padding-bottom: 16px;
    color: $color-black;
    text-align: left;
}

.cart__items {
    .cart--popout & {
        max-height: 240px;
        height: 100%;
        overflow-y: scroll;
        margin-right: -20px;
    }
}

.cart__inner {
    .cart--popout & {
        overflow: hidden;
    }
}

.cart__message {
    background: $color-white;
    padding: 16px 36px;
}

.cart__quantity-field {
    max-width: 80px;

    @include bp(sm-min) {
        max-width: 50px;
    }
}

.cart__item {
    font-size: $font-size-small;

    &:not(:last-child) {
        padding: 0 0 26px;
    }

    .cart--popout & {
        padding: 0 26px 0 0;
        background-color: $color-white;

        &:not(:last-child) {
            margin-bottom: 16px;
        }
    }

    &--header {
        font-weight: 700;
        @include font-smoothing(true);
        display: none;
        padding: 0 0 28px;
        line-height: 1;

        @include bp(sm-min) {
            display: block;
        }

        .cart--popout & {
            display: none;
        }
    }
}

.cart__grid {
    display: flex;
    margin: 0 -16px;
    flex-wrap: wrap;

    @include bp(sm-min) {
        flex-wrap: nowrap;
        align-items: center;
    }
}

.cart__item-col {
    padding: 0 16px;
    display: inline-block;
}

.cart__item-product {
    flex: 1 1 100%;

    @include bp(sm-min) {
        flex: 1 1 42%;
    }

    .cart--popout & {
        flex: 1 1 56%;
    }
}

.cart__item-quantity {
    padding-top: 10px;
    flex: 1 1 50%;

    @include bp(sm-min) {
        padding-top: 0;
        flex: 1 1 24%;
    }

    .cart--popout & {
        flex: 1 1 16%;
    }
}

.cart__item-price {
    text-align: left;
    flex: 1 1 100%;

    @include bp(sm-min) {
        flex: 1 1 17%;
    }
}

.cart__item-total {
    text-align: left;
    flex: 1 1 100%;

    @include bp(sm-min) {
        text-align: right;
        flex: 1 1 17%;
    }

    .cart--popout & {
        flex: 1 1 28%;
    }

    .cart__product-name & {
        font-weight: $font-weight-normal;

        @include bp(sm-min) {
            display: none;
        }
    }
}

.cart__item-close {
    flex: 1 1 24px;
    text-align: center;
    align-self: center;
}

.cart__item-remove {
    align-self: flex-end;
    text-align: right;

    @include bp(sm-min) {
        display: none;
    }
}

.cart__title {
    display: inline-block;
    font-size: $font-size-small;
    font-weight: $font-weight-medium;
    @include font-smoothing(true);
}

.cart__item-label {
    font-size: $font-size-tiny;
    color: $color-gray-warm2;
    font-weight: $font-weight-medium;

    @include bp(sm-min) {
        display: none;
    }

    .cart__item-price & {
        .cart:not(.cart--popout) & {
            display: block;

            @include bp(sm-min) {
                display: none;
            }
        }
    }
}

.cart__product {
    display: flex;

    @include bp(sm-min) {
        align-items: center;
    }
}

.cart__product-image {
    flex: 0 1 85px;
    min-width: 85px;
}

.cart__product-img {
    width: 100%;
    height: auto;
    vertical-align: top;
}

.cart__product-name {
    font-weight: $font-weight-medium;
    padding-left: 16px;
    line-height: 1.6;
}

.cart__product-subtitle {
    display: block;
    margin: 10px 0;
    font-weight: $font-weight-normal;
    font-size: $font-size-tiny;
    color: $color-brown-dark2;
    cursor: pointer;
    @include font-smoothing(true);

    @include bp(sm-min) {
        margin: 0;
    }

    a {
        color: inherit;
    }
}

.cart__footer {
    background-color: $color-red-brand;
    padding: 24px 20px;
    color: $color-white;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    @include bp(sm-min) {
        flex-wrap: nowrap;
        padding: 10px 20px;
    }
}

.cart__secure {
    font-size: $font-size-small;
    @include font-smoothing(true);
    align-self: center;
    flex: 0 1 100%;
    text-align: left;

    @include bp(sm-min) {
        padding-right: 20px;
        flex: 0 0 45%;
    }
}

.cart__secure-icon {
    font-size: 26px;
    color: $color-yellow;
    margin-top: -2px;
    margin-bottom: -2px;
    margin-right: -4px;
}

.cart__checkout {
    align-self: center;
    text-align: right;
    margin-top: 24px;
    flex: 0 1 100%;

    @include bp(sm-min) {
        flex: 1 1 55%;
        margin-top: 0;
    }
}

.cart__checkout-button {
    padding: 28px 45px 26px;
}

.cart__secure-item {
    line-height: 1.4;
}

.cart__secure-label {
    font-weight: $font-weight-medium;
}

.icon {
    .cart__item-close & {
        font-size: 24px;
        vertical-align: top;
    }
}

.is-item-hidden {
    display: none;
}
