.image-section {
    display: flex;
    flex-flow: column;
    flex-direction: column;

    @include bp(sm-min) {
        flex-flow: row;
    }

    &--reverse {
        flex-direction: column-reverse;

        @include bp(sm-min) {
            flex-flow: column-reverse;
        }
    }

    &--full {
        display: block;
        position: relative;
    }

}

.image-section__image {
    position: relative;
    overflow: hidden;
    width: 100%;
    background-size: cover;
    background-position: center;

    @include bp(sm-min) {
        width: 50%;
    }

    .image-section--full & {
        position: absolute;
        height: 100%;
        width: 100%;
        z-index: -1;

        &:after {
            content: '';
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;
            left: 0;

            @include bp(sm-min) {
                background: linear-gradient(268.19deg, rgba($color-black-1-2, 0) 40%, $color-black 80%);
            }
        }
    }

    .image-section--reverse & {
        height: 382px;
        width: 100%;

        @include bp(sm-min) {
            height: calc(700px - 334px);
        }

        @include bp(xl-min) {
            height: 580px;
        }
    }
}

img {
    .image-section__image & {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        min-height: 100%;
        min-width: 100%;
    }
}

h1 {
    .image-section__text & {
        font-size: 24px;
        line-height: 32px;
        margin: 0 0 26px 0;

        @include bp(sm-min) {
            margin: 0 0 16px 0;
            font-size: 34px;
            line-height: 48px;
        }

        @include bp(xl-min) {
            margin: 0 0 26px 0;
        }
    }
}

.image-section__text {
    display: flex;
    flex-flow: column;
    min-height: 446px;
    justify-content: center;
    text-align: left;
    padding: 44px 24px;
    background-color: $color-black-1-2;
    color: $color-gray-light;

    @include bp(sm-min) {
        min-height: 700px;
        width: 50%;
    }

    @include bp(xl-min) {
        padding: 44px 60px;
    }

    .image-section--full & {
        padding: 44px 24px;
        background-color: rgba($color-black-1-2, .64);

        @include bp(sm-min) {
            background-color: transparent;
        }

        @include bp(xl-min) {
            padding: 46px 143px;
        }
    }

    .image-section--reverse & {
        min-height: auto;
        text-align: center;
        padding: 48px 24px 64px 24px;

        @include bp(sm-min) {
            width: 100%;
            max-width: 760px;
            margin: 0 auto;
            padding: 48px 0 64px 0;
        }

        @include bp(xl-min) {
            padding: 100px 0 46px 0;
        }
    }

    .image-section--white & {
        background-color: $color-white;
        color: $color-black-1-2;
    }
}

.bounce {
    animation-name: bounce;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}

@keyframes bounce {
    0%,
    20%,
    50%,
    80%,
    100% {
        transform: translateY(0);
    }

    40% {
        transform: translateY(-20px);
    }

    60% {
        transform: translateY(-10px);
    }
}
