body {
    min-height: 100%;
    margin-bottom: 56px;

    @include bp(md-min) {
        margin: 0;
    }

    &.is-scroll-disabled {
        overflow-y: scroll;
        position: fixed;
        width: 100%;
    }

    &.page-shop {
        background-color: $color-gray-bg;
    }

    &.page-checkout {
        background-color: $color-brown-light;
    }

    &.page-light {
        background-color: $color-gray-light2;
    }

    img.gform_ajax_spinner {
        display: none !important; // TODO: maybe make this nice?
    }

    .gform_button {
        margin-top: 24px;
    }
}

.h-container {
    width: 100%;
    max-width: $container-max-width;
    margin: 0 auto;
    padding: 0 16px;

    @include bp(sm-min) {
        padding: 0 26px;
    }

    @include bp(lg-min) {
        padding: 0 $grid-gutter/2;
    }

    &--gutter-30 {
        max-width: $container-max-width;
        padding: 0 30px;
    }

    .accordion .section--maintenance & {
        @include bp(xs-max) {
            padding: 0;
        }
    }

    .section--related-blog & {
        padding: 0;

        @include bp(sm-min) {
            padding: 0 $grid-gutter/2;
        }
    }

    .section--recipe & {
        @include bp(sm-min) {
            padding: 0 $grid-gutter/2;
        }
    }
}

.h-offset-container {
    margin-left: -16px;
    margin-right: -16px;

    @include bp(sm-min) {
        margin-left: -26px;
        margin-right: -26px;
    }

    @include bp(lg-min) {
        margin-left: -$grid-gutter/2;
        margin-right: -$grid-gutter/2;
    }
}

.h-container__error-page-bottom {

    @include bp(lg-min) {
        padding: 0 26px;
    }
}

.h-clearfix {
    &:after {
        content: '';
        display: table;
        clear: both;
    }
}

.h-pull-left {
    float: left;
}

.h-pull-right {
    float: right;
}

.h-img-fluid {
    max-width: 100%;
    height: auto;
}

.h-hidden {
    display: none !important;
}

.h-visible {
    display: block !important;
}

.h-hidden-xs {
    @media only screen and (max-width: $bp-sm-min - 1) {
        display: none !important;
    }
}

.h-hidden-sm {
    @media only screen and (min-width: $bp-sm-min) and (max-width: $bp-md-min - 1) {
        display: none !important;
    }
}

.h-hidden-md {
    @media only screen and (min-width: $bp-md-min) and (max-width: $bp-lg-min - 1) {
        display: none !important;
    }
}

.h-hidden-lg {
    @media only screen and (min-width: $bp-lg-min) {
        display: none !important;
    }
}

.h-hidden-xl {
    @media only screen and (min-width: $bp-xl-min) {
        display: none !important;
    }
}

.h-visible-xs {
    display: none !important;

    @media only screen and (max-width: $bp-sm-min - 1) {
        display: block !important;
    }
}

.h-visible-sm {
    display: none !important;

    @media only screen and (min-width: $bp-sm-min) and (max-width: $bp-md-min - 1) {
        display: block !important;
    }
}

.h-visible-xl {
    display: none !important;

    @media only screen and (min-width: $bp-xl-min) {
        display: block !important;
    }
}

img.lazyload,
img.lazyloading {
    opacity: 0;
    transition: opacity $transition-duration $transition-easing;
}

img.lazyloaded {
    opacity: 1;
    transition: opacity $transition-duration $transition-easing;
}

.h-canvas {
    padding-top: 52px;
    transition: transform 400ms cubic-bezier(.42, 0, .58, 1);

    &--brag {
        padding-top: 88px;
    }

    .h-menu-open & {
        transform: translate3d(85%, 0, 0);
        padding-top: 0;
        width: 100%;

        @include bp(sm-min) {
            transform: translate3d(320px, 0, 0);
        }

        @include bp(md-min) {
            padding-top: 52px;
            transform: none;
        }

        &--brag {
            padding-top: 36px;

            @include bp(md-min) {
                padding-top: 88px;
            }
        }
    }

    body.is-ie & {
        padding-top: 52px;
    }
}

.h-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    background: linear-gradient(to top, $color-dark, rgba($color-dark, .76));
    z-index: $z-index-overlay;
    opacity: 0;
    visibility: hidden;
    @include transition(opacity visibility, $transition-duration, $transition-easing);

    .h-menu-open &,
    .h-dd-open &,
    .search-open &,
    .has-overlay & {
        opacity: 1;
        visibility: visible;
    }
}

.h-search-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba($color-black, .5);
    z-index: $z-index-search-overlay;
    opacity: 0;
    visibility: hidden;
    @include transition(opacity visibility, $transition-duration, $transition-easing);

    .search-open & {
        opacity: 1;
        visibility: visible;
    }
}

.h-overflow-container {
    margin: 0 -95px;
}

#drift-widget-container {
    @include bp(sm-max) {
        display: none !important;
    }
}

.grecaptcha-badge {
    display: none;
}
