$check-default: $color-black;
$check-border: $color-gray;
$check-background: $color-white;
$check-valid: $color-green;

.form-check {
    position: relative;

    &--top-spacing {
        margin-top: 36px;
    }

    &:not(:last-child) {
        padding-right: 26px;
    }
}

.form-check__input {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
}

.form-check__label {
    display: block;
    padding-top: 3px;
    padding-bottom: 3px;
    padding-left: 40px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    position: relative;
    color: inherit;

    .is-invalid & {
        color: $color-red;
    }

    &:before {
        content: '';
        display: block;
        border: 2px solid $check-border;
        background: $check-background;
        position: absolute;
        top: 0;
        left: 0;
        width: 24px;
        height: 24px;

        .is-invalid & {
            border-color: $color-red;
        }
    }

    &:after {
        position: absolute;
        top: 7px;
        left: 5px;
        content: '';
        display: block;
        border-left: 2px solid $color-white;
        border-bottom: 2px solid $color-white;
        width: 14px;
        height: 7px;
        transform: rotate(-45deg);
    }

    .form-group--checklist .form-check__input:checked ~ & {
        font-weight: 700;
        -webkit-font-smoothing: antialiased;
    }

    .form-check__input:checked ~ &--sublabel {
        font-weight: 600;
    }

    .form-check__input:checked ~ &:before {
        background-color: $color-red-brand;
        border-color: $color-red-brand;
    }

    .form-check__input:checked ~ &:after {
        border-color: $color-white;
    }
}

.form-check__label-sub {
    color: $color-gray;
    -webkit-font-smoothing: initial;
    font-weight: 300;

    .form-check__input:checked ~ .form-check__label & {
        font-weight: 600;
    }
}

a.form-check__label-sub {
    color: $color-dark;
    outline: 0;

    .is-invalid & {
        color: $color-red;
    }
}

.form-check__image {
    margin: 8px 0 0 40px;
}

.form-check__hidden {
    display: none;
    margin-top: 26px;

    .form-check__input:checked ~ & {
        display: block;
        width: 100%;
    }
}

.form-check__bubble {
    pointer-events: none;
    position: absolute;
    top: 12px;
    left: 12px;
    z-index: 3;
    transform: translate(-50%, -50%) translateZ(0);
    width: 0;
    height: 0;
    border-radius: 150px;
    background: rgba($color-red-brand, .4);

    .form-check__input:checked ~ & {
        width: 66px;
        height: 66px;
        opacity: 0;
        transition: $transition-duration-2 ease-out;
        transition-property: width, height, opacity;
    }
}
