.confirmation-page {
    position: relative;
    overflow: hidden;
}

.shop__title-baseline {
    align-items: baseline;
    display: flex;
}

.confirmation-page__content {
    padding: 106px 0 68px;
    width: 100%;

    @include bp(sm-min) {
        padding: 46px 0 76px;
    }

    @include bp(md-min) {
        padding: 46px 0 156px;
    }
}

.shop__tip__confirmation {
    display: block;
}

.confirmation-page__title {
    font-size: $font-size-mmidlarge;
    display: block;
    @include bp(sm-min) {
        font-size: $font-size-xlarge;
    }
    @include bp(md-min) {
        font-size: $font-size-xlarge;
    }
}

.confirmation-page__title2 {
    white-space: nowrap;
    font-size: $font-size-mmidlarge;
    text-transform: uppercase;
    font-weight: $font-weight-medium;
    width: 627px;
    @include bp(sm-min) {
        margin-right: 303px;
        font-size: $font-size-mxlarge;
    }
    @include bp(md-min) {
        margin-right: 303px;
        font-size: $font-size-mxlarge;
    }
}

.confirmation-page__subtitle {
    font-size: $font-size-base;
    margin-bottom: 26px;
    @include bp(sm-min) {
        margin-bottom: 63px;
    }
    @include bp(md-min) {
        margin-bottom: 36px;
    }
}

.confirmation-page__label {
    font-size: $font-size-large;
    font-weight: $font-weight-medium;
    margin-bottom: 26px;
    line-height: 28px;
}

.confirmation-class__video {
    margin-bottom: 26px;
    width: auto;
    @include bp(sm-min) {
        width: 440px;
    }
    @include bp(md-min) {
        width: auto;
    }
}

.confirmation-page__email {
    font-size: $font-size-base;
    color: $color-red-brand;
    margin-bottom: 26px;
    display: none;
    @include bp(sm-min) {
        display: block;
    }
    @include bp(md-min) {
        display: block;
    }
}

.confirmation-page__phone {
    font-size: $font-size-base;
    display: none;
    @include bp(sm-min) {
        display: block;
    }
    @include bp(md-min) {
        display: block;
    }
}

.confirmation-page__xs-email {
    display: block;
    font-size: $font-size-tiny;
    color: $color-red-brand;
    margin-bottom: 27px;
    @include bp(sm-min) {
        display: none;
    }
    @include bp(md-min) {
        display: none;
    }
}

.confirmation-page__xs-phone {
    display: block;
    font-size: $font-size-tiny;
    color: $color-red-brand;
    @include bp(sm-min) {
        display: none;
    }
    @include bp(md-min) {
        display: none;
    }
}

.confirmation-page__email-icon {
    font-size: 23px;
    margin-right: 16px;
}

.confirmation-page__phone-icon {
    font-size: 23px;
    margin-right: 16px;
}

.confirmation-page__button {
    min-width: 100px;
    padding: 18px 46px 19px;
    white-space: nowrap;
    display: none;
    @include bp(sm-min) {
        display: block;
    }
    @include bp(md-min) {
        display: block;
        margin-left: 18%;
    }
}

.confirmation-page__more-button {
    min-width: 100px;
    padding: 18px 63px 19px;
    white-space: nowrap;
    display: none;
    @include bp(sm-min) {
        display: none;
    }
    @include bp(md-min) {
        display: block;
    }
}

.confirmation-page__more-button-2 {
    display: block;
    padding: 18px 95px 18px;
    margin: 0 auto;
    @include bp(sm-min) {
        display: block;
        margin-left: 272px;
        padding: 18px 63px 18px;
    }
    @include bp(md-min) {
        display: none;
    }
}

.confirmation-page__product-list {
    background-color: $color-white;
    width: 100%;
    padding: 143px 0 136px;
    @include bp(sm-min) {
        padding: 169px 0 156px;
    }
    @include bp(md-min) {
        padding: 169px 0 156px;
    }
}

.confirmation-page__product-list-top {
    display: flex;
    flex-direction: row;
    margin-bottom: 54px;
    align-items: center;
    justify-content: space-between;
}

