.confirmation-page__tip-button {
    @include bp(md-min) {
        margin-bottom: -27px;
    }
}

.confirmation-page__xs-elements {
    display: block;
    @include bp(sm-min) {
        display: none;
    }
    @include bp(md-min) {
        display: none;
    }
}

.confirmation-page__print {
    color: $color-red-brand;
    font-size: 22px;
    display: block;
    @include bp(sm-min) {
        display: none;
    }
    @include bp(md-min) {
        display: none;
    }
}

.confirmation-page__print-text {
    font-size: $font-size-tiny;
    margin-right: 26px;
}

.confirmation-page__download {
    color: $color-red-brand;
    font-size: 22px;
    display: block;
    @include bp(sm-min) {
        display: none;
    }
    @include bp(md-min) {
        display: none;
    }
}

.confirmation-page__pdf {
    width: 100%;
}

.confrimation-page__download-icon {
    font-size: 34px;
}

.confirmation-page__download-text {
    font-size: $font-size-tiny;
}

.confirmation-page__back-button {
    margin: 36px auto 0;
}

.confirmation-page__xs-elements-top {
    display: flex;
}

.shop__title-icon__confirmation {
    font-size: 27px;
    flex: 0 0 auto;
    margin-right: 10px;
}

.confirmation-page__image {
    width: 100%;
}

.confirmation-page__icon {
    font-size: 22px;
}

.confirmation-page__top-elements {
    @include bp(sm-min) {
        margin-bottom: 24px;
    }
    @include bp(md-min) {
        margin-bottom: 24px;
    }
}

.confirmation-page__cover {
    position: relative;
}

.confirmation-page__cover-text {
    position: absolute;
    top: 20px;
    left: 20px;

    @include bp(sm-min) {
        top: 50px;
        left: 50px;
    }
}
