.events-grid {
    display: block;
    width: 100%;
    margin: 0;
    position: relative;

    &:after {
        content: '';
        background: $gradient-grid;
        width: 45px;
        height: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;

        @include bp(sm-min) {
            width: 115px;
            right: -26px;
        }

        @include bp(md-min) {
            display: none;
        }
    }
}

.events-grid__title {
    font-size: $font-size-mmidlarge;
    text-transform: uppercase;
    font-weight: $font-weight-medium;
    padding: 86px 0 86px;

    @include bp(sm-min) {
        font-size: $font-size-mxlarge;
        margin-left: 0;
        padding: 156px 0 86px;
    }
}

.events-grid__cards {
    display: flex;
    flex-wrap: wrap;

    @include bp(sm-min) {
        margin: 0 -25px -80px;
    }
}

.events-grid__item {
    width: 80vw;
    padding: 0 16px;

    @include bp(sm-min) {
        width: 50%;
        padding: 0 25px 80px;
    }

    @include bp(md-min) {
        width: calc(100% / 3);
    }
}
