.order-summary {
    display: block;
}

.order-summary__title {
    font-size: $font-size-semi;
    font-weight: $font-weight-bold;
    line-height: 1;
}

/* Order summary products */

.order-summary__products {
    display: block;
    max-height: 420px;
    margin-top: 46px;
    overflow-y: auto;
}

.order-summary__product {
    display: flex;
    align-items: center;
    width: 100%;

    & + .order-summary__product {
        margin-top: 32px;
    }
}

.order-summary__product-figure {
    flex: 0 1 80px;
}

.order-summary__product-image {
    display: block;
    max-width: 100%;
}

.order-summary__product-content {
    margin-left: 16px;
    flex: 1 1 auto;

    @include bp(sm-min) {
        display: flex;
        justify-content: space-between;
    }

    @include bp(md-min) {
        display: block;
    }
}

.order-summary__product-title {
    display: block;
    width: 100%;
    font-size: $font-size-small;
    font-weight: $font-weight-medium;

    @include bp(sm-min) {
        display: inline-block;
        width: auto;
    }

    @include bp(md-min) {
        display: block;
        width: 100%;
    }
}

.order-summary__product-price {
    display: block;
    width: 100%;
    font-size: $font-size-tiny;
    color: $color-brown-dark2;

    @include bp(sm-min) {
        display: inline-block;
        width: auto;
    }

    @include bp(md-min) {
        display: block;
        width: 100%;
    }
}

/* Order summary table */

.order-summary__table {
    margin-top: 42px;
}

.order-summary__row {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    font-size: $font-size-small;

    & + .order-summary__row {
        margin-top: 6px;
    }
}

.order-summary__row-label {
    flex-grow: 1;
}

.order-summary__row-value {
    text-align: right;
}

.order-summary__row-select {
    flex-basis: 100%;
    margin: 0 0 24px;
}

.order-summary__row-delete {
    position: absolute;
    top: 0;
    right: -18px;

    @include bp(sm-min) {
        top: 1px;
        right: -25px;
    }
}

.order-summary__row-icon {
    display: inline-block;
    width: 20px;
    height: 20px;
    color: $color-gray-warm2;
}

/* Order summary terms */

.order-summary__terms {
    margin-top: 36px;
}

/* Order summary button */

.order-summary__button {
    font-size: $font-size-semi;
    margin-top: 26px;
    min-width: 200px;
    letter-spacing: 4px;
}
