.product {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    text-align: center;
    color: $color-dark;
    border: 1px solid $color-white;
    background-color: $color-white;
    overflow: hidden;

    &:hover {
        text-decoration: none;
        border: 1px solid $color-gray-warm2;
    }

    &:focus {
        outline: 0;
    }
}

.product__badge {
    position: absolute;
    top: 16px;
    left: 16px;
    z-index: 2;
}

.product__image {
    @include aspect-ratio(1,1, 'product__image-img');
    overflow: hidden;
}

.product__image-img {
    display: block;
    width: 100%;
    height: auto;

    .product-grid & {
        margin: 0 auto;
    }
}

.product__content {
    display: block;
    padding: 26px;

    .product-grid & {
        padding: 26px;
    }

    .confirmation-page & {
        padding: 22px 26px 26px;
    }
}

.product__title {
    font-size: $font-size-large;
    font-weight: $font-weight-medium;

    .product-grid & {
        font-size: $font-size-mlarge;
        line-height: 1.4;
        letter-spacing: 1px;

        @include bp(sm-min) {
            padding: 0 26px;
        }
    }
}

.product__description {
    margin-top: 13px;
    font-style: italic;
    font-size: 14px;
    color: $color-brown-light2;

    .product-grid & {
        font-size: $font-size-small;
        letter-spacing: 1px;
    }
}

.product__price {
    margin-top: 16px;
    font-weight: 500;
    font-size: 18px;
}

.product__price-sale {
    color: $color-red-brand;
    margin-left: 16px;
}

.product__button {
    transform: none;

    &.is-triggered {
        &:before {
            content: '';
            display: inline-block;
        }
    }

    &:after {
        content: '';
        display: none;

        .product:hover & {
            display: block;
        }
    }
}
