.review-group {
    display: block;
    width: 100%;
    padding: 86px 0 116px 0;
    background-color: $color-gray-bg;

    &.review-group--new {
        background-color: $color-white2;
        padding-top: 51px;
    }
}

.review-group__header {
    display: block;

    @include bp(sm-min) {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

.review-group__title {
    display: inline-block;
    margin-right: auto;
    flex-shrink: 0;

    .review-group--new {
        text-transform: uppercase;
        line-height: 36px;
        font-size: 24px;
    }
}

.review-group__add {
    .review-group--new & {
        margin-top: 16px;
        width: 100%;
        min-height: 51px;

        @include bp(sm-min) {
            width: auto;
        }
    }
}

.review-group__sorting {
    display: inline-block;
    width: auto;
    margin-right: 56px;
}

.review-group__footer {
    text-align: center;
}

.review-group__load-more {
    margin-top: 76px;

    .review-group--new & {
        margin-top: 56px;
        width: 100%;
        min-height: 51px;

        @include bp(sm-min) {
            width: auto;
        }
    }
}
