.product-poster {
    position: relative;
    display: block;
    width: 100%;

    @include bp(md-min) {
        display: flex;
    }

    .button {
        margin: 0;
        padding-left: 0;
        padding-right: 0;
    }
}

.product-poster__media {
    position: relative;
    display: block;
    width: 100%;
    padding: 0 0 72px 0;
    animation: slideFromLeft 300ms linear;

    @include bp(sm-min) {
        padding: 50px 0;
    }

    @include bp(md-min) {
        width: 50%;
    }
}

@keyframes slideFromLeft {
    from {
        opacity: 0;
        transform: translate3d(-20%, 0, 0);
    }

    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}

.product-poster__image {
    display: block;
    margin: 0 auto;
}

.product-poster__badge {
    position: absolute;
    top: 16px;
    right: 16px;
    z-index: 3;
}

.product-poster__info {
    display: block;
    width: 100%;
    padding: 48px 16px;
    background-color: $color-gray-bg;

    animation: slideFromRight 300ms linear;

    @include bp(md-min) {
        width: 50%;
        padding: 86px 0 105px 96px;
    }
}

@keyframes slideFromRight {
    from {
        opacity: 0;
        transform: translate3d(20%, 0, 0);
    }

    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}

.product-poster__info-inner {
    display: block;
    max-width: 440px;
}

.product-poster__title {
    text-transform: uppercase;
    font-size: $font-size-xlarge;
    font-weight: $font-weight-medium;
}

.product-poster__description {
    margin-top: 16px;
}

.product-poster__form {
    margin-top: 26px;
}

.product-poster__grid {
    margin-bottom: 0;
}

.product__poster-price {
    font-size: $font-size-mlarge;
    font-weight: $font-weight-bold;
    font-style: italic;
    color: inherit;

    &--sale {
        color: $color-red-brand;
    }
}

.product__poster-sale-price {
    display: block;
    font-size: $font-size-small;
    font-weight: $font-weight-medium;
    font-style: italic;
    line-height: 1;
    color: $color-brown-dark2;
}

.product__poster-price-period {
    display: inline-block;
    margin-right: 5px;
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    text-transform: uppercase;
}

.product-poster__button {
    margin-top: 23px;
}

.product-poster__transport {
    margin-top: 41px;
    margin-bottom: 64px;
}
