.form-textarea {
    .form-textfield__label {
        max-height: none;
    }
}

textarea.form-textfield__input {
    display: block;
    resize: none;
    height: 70px;
    font-size: 14px;
    font-weight: 400;
    padding-left: 0;
}

input::-webkit-input-placeholder {
    visibility: hidden;
    opacity: 0;
}

input:focus::-webkit-input-placeholder {
    visibility: visible;
    opacity: 1;

    &:after {
        visibility: hidden;
        opacity: 0;
    }
}

// Firefox < 19

input:-moz-placeholder {
    visibility: hidden;
    opacity: 0;
}

input:focus:-moz-placeholder {
    visibility: visible;
    opacity: 1;

    &:after {
        visibility: hidden;
        opacity: 0;
    }
}

// Firefox > 19

input::-moz-placeholder {
    visibility: hidden;
    opacity: 0;
}

input:focus::-moz-placeholder {
    visibility: visible;
    opacity: 1;

    &:after {
        visibility: hidden;
        opacity: 0;
    }
}

// Internet Explorer 10

input:-ms-input-placeholder {
    visibility: hidden;
    opacity: 0;
}

input:focus:-ms-input-placeholder {
    visibility: visible;
    opacity: 1;

    &:after {
        visibility: hidden;
        opacity: 0;
    }
}
