.shop {
    padding: 105px 0 35px;

    @include bp(sm-min) {
        padding: 34px 0 46px 0;
    }

    &--bottom-padding {
        padding-bottom: 80px;

        @include bp(sm-min) {
            padding-bottom: 150px;
        }
    }

    &--cart .woocommerce-error {
        display: none;
    }

    &--cart .woocommerce-message {
        display: none;
    }
}

.shop__head {
    margin-bottom: 26px;
    position: relative;
    flex-direction: column;
    align-items: center;

    @include bp(sm-min) {
        display: flex;
        align-items: flex-end;
    }

    @include bp(md-min) {
        flex-direction: row;
    }

    .shop:not(.shop--confirmation) & {
        @include bp(sm-min) {
            flex-direction: row;
        }
    }

    .shop--confirmation & {
        align-items: stretch;
    }
}

.shop__title,
.shop__tip {
    @include font-smoothing(true);
}

.shop__header-col {
    @include bp(sm-min) {
        flex: 1 1;
    }

    &--last {
        display: none;
        flex-direction: column;
        justify-content: flex-end;

        .shop--confirmation & {
            position: absolute;
            right: 0;

            @include bp(md-min) {
                position: relative;
                right: auto;
            }
        }

        @include bp(sm-min) {
            flex: 0 1 50%;
            display: flex;
        }

        @include bp(md-min) {
            flex: 0 1 33%;
        }
    }

    &--even {
        justify-content: space-between;
    }
}

.shop__title {
    display: flex;
    align-items: center;
    font-size: 24px;
    line-height: 36px;
    text-transform: uppercase;
    font-weight: $font-weight-bold;

    .shop--confirmation & {
        margin-bottom: 26px;
    }

    @include bp(sm-min) {
        font-size: 28px;
        line-height: 46px;
    }
}

.shop__title-text {
    flex: 0 1 auto;
}

.shop__title-icon {
    font-size: 38px;
    flex: 0 0 auto;
    margin-top: -2px;
    margin-right: 10px;
}

.shop__tip {
    display: flex;

    .shop__head & {
        @include bp(sm-min) {
            padding-right: 26px;
        }

        .shop--confirmation & {
            display: none;

            @include bp(md-min) {
                display: flex;
            }
        }
    }

    .shop__primary & {
        padding-top: 36px;
        padding-bottom: 36px;

        @include bp(sm-min) {
            display: none;
        }

        .shop--checkout & {
            @include bp(xs-max) {
                display: none;
            }
        }

        .shop--confirmation & {
            @include bp(xs-max) {
                display: none;
            }
        }
    }

    .shop__secondary & {
        .shop--confirmation & {
            display: flex;
            margin-top: 46px;

            @include bp(sm-min) {
                align-items: center;
            }

            @include bp(md-min) {
                margin-top: 0;
                display: none;
            }
        }
    }

    .checkout__column & {
        .shop__primary & {
            .shop--checkout & {
                @include bp(xs-max) {
                    display: flex;
                }
            }
        }
    }
}

.shop__tip-icon {
    display: inline-block;
    vertical-align: top;
    font-size: 40px;
    margin-top: -5px;
    margin-right: 12px;
    color: $color-brown-dark2;
}

.shop__tip-text {
    font-size: $font-size-tiny;
    flex: 1 1;
    line-height: 1.6;
}

.shop__panel {
    width: 100%;

    .shop__primary & {
        .shop--checkout & {
            width: 100%;
        }

        .shop--cart & {
            height: auto;
            background-color: $color-white;
            padding: 40px 16px 36px;
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;
            width: 100%;

            @include bp(sm-min) {
                padding: 38px 24px 36px;
            }

            @include bp(md-min) {
                padding: 38px 36px 36px;
            }
        }
    }

    .shop__primary--full & {
        .shop--cart & {
            @include bp(sm-min) {
                height: auto;
            }
        }
    }
}

.shop__grid {
    display: flex;
    margin-right: $grid-gutter * .5 * -1;
    margin-left: $grid-gutter * .5 * -1;
    flex-wrap: wrap;

    @include bp(sm-min) {
        flex-direction: column;
    }

    @include bp(md-min) {
        flex-direction: row;
    }
}

.shop__column {
    padding-right: $grid-gutter * .5;
    padding-left: $grid-gutter * .5;
}

.shop__primary {
    width: 100%;
    display: flex;

    @include bp(xs-max) {
        flex-direction: column;
    }

    @include bp(md-min) {
        width: calc(100% / 12 * 8);
    }

    .shop--checkout & {
        width: 100%;
    }

    &.woocommerce-error {
        display: none;
    }

    &--full {
        width: 100%;
    }
}

.shop__secondary {
    width: 100%;
    display: flex;

    @include bp(sm-min) {
        .shop:not(.shop--confirmation) & {
            margin-top: 16px;
        }
    }

    @include bp(md-min) {
        margin-top: 0 !important;
        width: calc(100% / 12 * 4);
    }

    .shop--confirmation & {
        flex-direction: column;
    }
}

.shop__footer {
    margin-top: 62px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .shop__secondary & {
        margin-top: 0;
        flex-wrap: wrap;
        justify-content: center;
    }

    .shop--confirmation & {
        display: none;
    }
}

.shop__back {
    @include bp(sm-min) {
        flex: 1 1 50%;
    }

    @include bp(md-min) {
        flex: 1 1 auto;
    }
}

.shop__summary {
    width: 100%;
}

.shop__back-icon {
    font-size: 10px;
    transform: rotate(90deg);
    margin-right: 10px;
    margin-top: -3px;
}

.shop__back-button {
    align-self: flex-end;
    display: none;

    @include bp(sm-min) {
        display: inline-block;
    }
}

.shop__back-link {
    color: $color-gray-warm2;
    font-size: $font-size-small;
    font-weight: $font-weight-medium;
    display: flex;
    align-items: center;
    @include font-smoothing(true);
}

.actions {
    margin-top: auto;
}

.shop__giftcard {
    text-align: right;
    margin-top: auto;

    .shop:not(.shop--cart) & {
        display: none;
    }

    .shop__primary & {
        margin-top: 56px;
        text-align: left;

        @include bp(sm-min) {
            text-align: right;
        }
    }

    .shop__secondary & {
        padding: 45px 30px 0;

        @include bp(sm-min) {
            padding: 0;
            flex: 1 1 50%;
        }

        @include bp(md-min) {
            padding: 45px 30px 0;
            flex: 1 1 auto;
        }

        @include bp(lg-min) {
            padding: 45px 60px 0;
        }
    }

    .shop--checkout & {
        display: none !important;
    }
}

.shop__giftcard-link {
    color: $color-brown-light2;
    font-size: $font-size-tiny;
    font-weight: $font-weight-medium;
    letter-spacing: 2px;
}

.shop__secure {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 36px 30px 0;

    @include bp(sm-min) {
        padding: 45px 30px 40px;
        justify-content: center;
        flex: 1 1 100%;
    }

    @include bp(md-min) {
        padding: 30px 30px 0;
        flex: 1 1 auto;
    }

    @include bp(lg-min) {
        padding: 30px 60px 0;
    }
}

.shop__secure-icon {
    flex: 1 0 42px;
    max-width: 42px;
    font-size: 62px;
    vertical-align: middle;
    // color: $color-brand-purple-2;
    margin-right: 15px;

    @include bp(sm-min) {
        font-size: 42px;
    }
}

.shop__secure-label {
    font-size: $font-size-small;
    // color: $color-brand-purple-2;
    flex: 0 1 auto;
}

.shop__giftcard-form {
    justify-content: flex-end;
    display: none; // will be made visible using javascript by adding is-visible class

    &.is-visible {
        display: flex;
    }
}

.form-textfield--giftcode {
    max-width: 300px;
}
