.team-list {
    padding: 36px 0 146px;

    @include bp(sm-min) {
        padding: 86px 0;
    }
}

.team-list__title {
    font-size: $font-size-mmidlarge;
    text-transform: uppercase;
    font-weight: $font-weight-medium;
    margin-bottom: 16px;

    @include bp(sm-min) {
        font-size: $font-size-mxlarge;
        margin-bottom: 26px;
    }
}

.team-list__subtitle {
    font-size: $font-size-base;

    @include bp(sm-min) {
        text-align: center;
        margin-bottom: 24px;
    }
}

.team-list__card {
    padding: 0 6px 0 6px;
    margin-bottom: 10px;

    @include bp(sm-min) {
        padding: 0;
        margin-bottom: 70px;
    }
}

.team-list__name {
    font-size: $font-size-medium;
    font-weight: $font-weight-medium;
    margin-bottom: 16px;
}

.team-list__job,
.team-list__phone,
.team-list__email {
    font-size: $font-size-base;
}

.team-list__email {
    color: $color-red-brand;
}

.team-list__image {
    width: 100%;
    margin-bottom: 16px;
}

